define('catalogentries-digitalcopies/sections/info/views/layout_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'catalogentries-digitalcopies/sections/info/views/templates/layout'
], function (require) {
    var Marionette = require('marionette');
    var InfoLayoutView = Marionette.LayoutView.extend({
        template: require('./templates/layout'),
        regions: {
            copies: '> .copies',
            search: '> .search'
        }
    });
    return InfoLayoutView;
});