define('catalogentries-digitalcopies/sections/info/views/copies_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'notifications/index',
    'catalogentries-digitalcopies/sections/info/views/hathitrust_book_reader_view',
    'catalogentries-digitalcopies/sections/info/views/templates/copy'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), Notifications = require('notifications/index').getInstance(), HathitrustBookReaderView = require('./hathitrust_book_reader_view');
    var CopyView = Marionette.ItemView.extend({
        template: require('./templates/copy'),
        tagName: 'li',
        triggers: { 'click .book-reader': 'book-reader' },
        modelEvents: { 'change': 'render' }
    });
    var NoCopiesView = Marionette.ItemView.extend({
        template: _.constant('[no copies]'),
        className: 'text-muted'
    });
    var CopiesView = Marionette.CollectionView.extend({
        tagName: 'ul',
        childView: CopyView,
        emptyView: NoCopiesView,
        initialize: function () {
            this.on('childview:book-reader', function (view) {
                var id = view.model.get('copyId');
                var idParts = id.match(/^htid:(\d{9})#(.+)$/);
                if (!idParts) {
                    throw new Error('expected HathiTrust resource identifier, received {' + id + '}');
                }
                var htid = idParts[2];
                var bookReaderView = new HathitrustBookReaderView({ htid: htid });
                Notifications.alert({
                    title: view.model.get('title'),
                    view: bookReaderView
                });
            }, this);
        }
    });
    return CopiesView;
});