define('catalogentries-reln/sections/info/views/type_selector_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'mixins/editable_field_view_mixin',
    'controls/select/select',
    'catalogentries-reln/sections/info/views/templates/type_selector'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), EditableFieldViewMixin = require('mixins/editable_field_view_mixin'), SelectControl = require('controls/select/select');
    var TypeSelectorView = Marionette.LayoutView.extend({
        template: require('./templates/type_selector'),
        className: 'editable-field',
        regions: { typeSelect: '> .control .type-select' },
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing'
        },
        modelEvents: {
            'change:type': function (model, type) {
                if (type) {
                    this.$('> .display .type').text(type.get('title'));
                    this._typeSelectControl.setValue(type);
                } else {
                    this.$('> .display .type').html('<span class="text-muted">[type]</span>');
                }
            }
        },
        templateHelpers: function () {
            var type = this.model.get('type');
            return { type: type ? type.get('title') : null };
        },
        initialize: function (opts) {
            if (!opts.channel) {
                throw new TypeError('no channel provided');
            }
            if (!opts.types) {
                throw new TypeError('no relationship type collection provided');
            }
            _.extend(this, _.pick(opts, 'types'));
            this.listenTo(opts.channel, 'commit', this.stopEditing);
            this.initializeEditableField(opts);
        },
        onBlur: function () {
            this.updateViewModel();
        },
        onShow: function () {
            this._typeSelectControl = new SelectControl({
                collection: this.types,
                value: this.model.get('type'),
                provider: function (o) {
                    return {
                        value: o.cid,
                        label: o.get('title')
                    };
                }
            });
            this.getRegion('typeSelect').show(this._typeSelectControl.getView());
        },
        updateViewModel: function () {
            this.model.set({ type: this._typeSelectControl.getValue() });
            if (this.model.changedAttributes()) {
                this.model.flush();
            }
        }
    });
    _.defaults(TypeSelectorView.prototype, EditableFieldViewMixin);
    return TypeSelectorView;
});