define('catalogentries-reln/entities/provenance', [
    'require',
    'exports',
    'module',
    'catalogentries-reln/entities/base',
    'underscore',
    'moment'
], function (require) {
    var BaseModel = require('./base'), _ = require('underscore'), moment = require('moment');
    var ProvenanceDomainModel = BaseModel.extend({
        defaults: function () {
            return {
                creatorUris: [],
                dateCreated: moment(),
                dateModified: moment()
            };
        },
        parse: function (attrs) {
            if (attrs.dateCreated) {
                attrs.dateCreated = moment(attrs.dateCreated);
            }
            if (attrs.dateModified) {
                attrs.dateModified = moment(attrs.dateModified);
            }
            return attrs;
        },
        toJSON: function () {
            var json = BaseModel.prototype.toJSON.apply(this, arguments);
            if (_.has(json, 'dateCreated') && !_.isNull(json.dateCreated) && !_.isUndefined(json.dateCreated)) {
                json.dateCreated = json.dateCreated.toISOString();
            }
            if (_.has(json, 'dateModified') && !_.isNull(json.dateModified) && !_.isUndefined(json.dateModified)) {
                json.dateModified = json.dateModified.toISOString();
            }
            return json;
        }
    });
    return ProvenanceDomainModel;
});