define('modules/work_editor/sections/volume_info/entities/volume', [
    'require',
    'exports',
    'module',
    'backbone',
    'modules/work_editor/controls/author_refs_editor/author_refs_editor',
    'modules/work_editor/controls/titles_editor/titles_editor',
    'modules/work_editor/controls/publication_info/publication_info',
    'controls/summary/summary'
], function (require) {
    var Backbone = require('backbone'), AuthorRefsEditorControl = require('modules/work_editor/controls/author_refs_editor/author_refs_editor'), TitlesEditorControl = require('modules/work_editor/controls/titles_editor/titles_editor'), PublicationInfoControl = require('modules/work_editor/controls/publication_info/publication_info'), SummaryControl = require('controls/summary/summary');
    var VolumeViewModel = Backbone.Model.extend({
        defaults: function () {
            return {
                volumeNumber: '',
                publicationInfo: new PublicationInfoControl.Model(),
                authors: new AuthorRefsEditorControl.Collection(),
                titles: new TitlesEditorControl.Collection(),
                otherAuthors: new AuthorRefsEditorControl.Collection(),
                summary: new SummaryControl.Model()
            };
        },
        flush: function () {
            this.trigger('flush', this);
        }
    });
    return VolumeViewModel;
});