define('modules/person_editor/sections/info_editor/views/person_info_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'modules/person_editor/sections/info_editor/views/templates/person'
], function (require) {
    var Marionette = require('marionette');
    var PersonInfoView = Marionette.LayoutView.extend({
        template: require('./templates/person'),
        regions: {
            displayName: '.display-name',
            names: '.names',
            birth: '.birth',
            death: '.death',
            summary: '.summary'
        },
        onBeforeDestroy: function () {
            this.model.flush();
        }
    });
    return PersonInfoView;
});