define('modules/person_editor/sections/info_editor/entities/name', [
    'require',
    'exports',
    'module',
    'backbone'
], function (require) {
    var Backbone = require('backbone');
    var NameViewModel = Backbone.Model.extend({
        defaults: {
            prefix: '',
            first: '',
            middle: '',
            last: '',
            suffix: '',
            display: ''
        },
        flush: function () {
            this.trigger('flush', this);
        }
    });
    return NameViewModel;
});