define('catalogentries-reln/sections/master/relationships_master_controller', [
    'require',
    'exports',
    'module',
    'promise',
    'underscore',
    'catalogentries-reln/sections/master/entities/type_groups',
    'catalogentries-reln/sections/master/entities/type_group',
    'catalogentries-reln/sections/master/entities/relationships',
    'catalogentries-reln/sections/master/entities/relationship',
    'catalogentries-reln/sections/master/entities/author_refs',
    'catalogentries-reln/sections/master/entities/author_ref'
], function (require) {
    var Promise = require('promise'), _ = require('underscore'), RelationshipTypeGroupViewModelCollection = require('./entities/type_groups'), RelationshipTypeGroupViewModel = require('./entities/type_group'), RelationshipViewModelCollection = require('./entities/relationships'), RelationshipViewModel = require('./entities/relationship'), AuthorRefViewModelCollection = require('./entities/author_refs'), AuthorRefViewModel = require('./entities/author_ref');
    function bindAuthorRefViewModel(domainModel, viewModel) {
        domainModel.on('change:firstName', function (model, value) {
            viewModel.set('firstName', value);
        });
        domainModel.on('change:lastName', function (model, value) {
            viewModel.set('lastName', value);
        });
        return Promise.resolve(viewModel);
    }
    function createAuthorRefViewModel(domainModel, services) {
        var viewModel = new AuthorRefViewModel({
            id: domainModel.id,
            firstName: domainModel.get('firstName'),
            lastName: domainModel.get('lastName')
        });
        return bindAuthorRefViewModel(domainModel, viewModel, services);
    }
    function bindAuthorRefViewModelCollection(domainCollection, viewCollection, services) {
        domainCollection.on('add', function (domainModel) {
            createAuthorRefViewModel(domainModel, services).then(function (viewModel) {
                viewCollection.add(viewModel);
            });
        });
        return Promise.resolve(viewCollection);
    }
    function createAuthorRefViewModelCollection(domainCollection, services) {
        var domainModels = domainCollection ? domainCollection.models : [];
        var viewCollectionPromise = Promise.map(domainModels, function (domainModel) {
            return createAuthorRefViewModel(domainModel, services);
        }).then(function (viewModels) {
            var viewCollection = new AuthorRefViewModelCollection(viewModels);
            return domainCollection ? bindAuthorRefViewModelCollection(domainCollection, viewCollection, services) : viewCollection;
        });
        return viewCollectionPromise;
    }
    function bindRelationshipViewModel(domainModel, viewModel, services) {
        var channel = services.channel;
        viewModel.on('edit', function () {
            channel.trigger('edit:relationship', domainModel);
        });
        viewModel.on('destroy', function (model, collection, options) {
            if (!options.keepDomainModel) {
                domainModel.destroy();
            }
        });
        return Promise.resolve(viewModel);
    }
    function createRelationshipViewModel(domainModel, services) {
        var work = services.work, types = services.types, worksRepo = services.worksRepo;
        var currentWorkUris = work.get('editions').map(function (e) {
            return e.getUri();
        });
        currentWorkUris.push(work.getUri());
        var sourceUris = _.chain(domainModel.get('relatedEntities').pluck('entryUris')).flatten().uniq().value();
        var targetUris = _.chain(domainModel.get('targetEntities').pluck('entryUris')).flatten().uniq().value();
        var reverse;
        if (_.intersection(currentWorkUris, sourceUris).length > 0) {
            reverse = false;
        } else if (_.intersection(currentWorkUris, targetUris).length > 0) {
            reverse = true;
        } else {
            throw new Error('currently selected work does not appear to be targeted by relationship');
        }
        var typeId = domainModel.get('typeId');
        var type = types.get(typeId);
        if (!type) {
            throw new Error('unable to find relationship type {' + typeId + '}');
        }
        var targetUri = reverse ? _.first(sourceUris) : _.first(targetUris);
        var workOrEditionPromise = targetUri ? worksRepo.find(targetUri) : Promise.resolve(null);
        var authorsPromise = workOrEditionPromise.then(function (resolution) {
            var authorRefDomainModelCollection = resolution ? resolution.model.get('authors') : null;
            return createAuthorRefViewModelCollection(authorRefDomainModelCollection, services);
        });
        var viewModelPromise = Promise.join(workOrEditionPromise, authorsPromise, function (resolution, authors) {
            var title = resolution ? resolution.model.getCanonicalTitle() : null;
            var attrs = {
                _domainCid: domainModel.cid,
                type: type.getTitle(reverse),
                title: title ? title.getFullTitle() : '',
                authors: authors
            };
            if (resolution && resolution.type === 'edition') {
                var pubDateDescription = resolution.model.get('publicationInfo').get('date');
                if (pubDateDescription) {
                    attrs.pubDate = pubDateDescription.get('calendar');
                }
                attrs.edition = resolution.model.get('editionName');
            }
            var viewModel = new RelationshipViewModel(attrs);
            return bindRelationshipViewModel(domainModel, viewModel, services);
        });
        return viewModelPromise;
    }
    function bindRelationshipTypeGroupViewModelCollection(domainCollection, viewCollection, services) {
        function createAndAddViewModel(domainModel) {
            createRelationshipViewModel(domainModel, services).then(function (viewModel) {
                var type = viewModel.get('type');
                var group = viewCollection.findWhere({ title: type });
                if (group) {
                    group.get('relationships').add(viewModel);
                } else {
                    viewCollection.add({
                        title: type,
                        relationships: new RelationshipViewModelCollection([viewModel])
                    });
                }
            });
        }
        domainCollection.on('add', createAndAddViewModel);
        domainCollection.on('change', function (domainModel) {
            viewCollection.find(function (group) {
                var viewModel = group.get('relationships').findWhere({ _domainCid: domainModel.cid });
                if (!viewModel) {
                    return false;
                }
                domainModel.stopListening(viewModel);
                viewModel.destroy({ keepDomainModel: true });
                createAndAddViewModel(domainModel);
                if (group.get('relationships').isEmpty()) {
                    group.destroy();
                }
                return true;
            });
        });
        return Promise.resolve(viewCollection);
    }
    function createRelationshipTypeGroupViewModelCollection(domainCollection, services) {
        var viewModelPromises = domainCollection.map(function (domainModel) {
            return createRelationshipViewModel(domainModel, services);
        });
        var viewCollectionPromise = Promise.all(viewModelPromises).then(function (viewModels) {
            var typeGroups = _.groupBy(viewModels, function (relationshipViewModel) {
                return relationshipViewModel.get('type');
            });
            var groupCollection = _.map(typeGroups, function (relationships, type) {
                return new RelationshipTypeGroupViewModel({
                    title: type,
                    relationships: new RelationshipViewModelCollection(relationships)
                });
            });
            var viewCollection = new RelationshipTypeGroupViewModelCollection(groupCollection);
            return bindRelationshipTypeGroupViewModelCollection(domainCollection, viewCollection, services);
        });
        return viewCollectionPromise;
    }
    function RelationshipsMasterController(opts) {
        if (!opts.domainCollection) {
            throw new TypeError('no relationships domain collection given');
        }
        if (!opts.channel) {
            throw new TypeError('no channel provided');
        }
        if (!opts.types) {
            throw new TypeError('no relationship type domain collection provided');
        }
        if (!opts.work) {
            throw new TypeError('no work provided as currently focused work');
        }
        if (!opts.worksRepo) {
            throw new TypeError('no works repository provided');
        }
        _.extend(this, _.pick(opts, 'domainCollection', 'channel', 'types', 'work', 'worksRepo'));
    }
    _.extend(RelationshipsMasterController.prototype, {
        getViewCollection: function () {
            return createRelationshipTypeGroupViewModelCollection(this.domainCollection, _.pick(this, 'channel', 'types', 'work', 'worksRepo'));
        }
    });
    return RelationshipsMasterController;
});