define('catalogentries-digitalcopies/sections/info/views/reference_editor_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'catalogentries-digitalcopies/sections/info/views/templates/editor'
], function (require) {
    var Marionette = require('marionette');
    var ReferenceEditorView = Marionette.LayoutView.extend({
        template: require('./templates/editor'),
        regions: { summary: '#copy-ref-summary' },
        events: {
            'keyup #copy-ref-title': function () {
                var title = this.$('#copy-ref-title').val();
                this.model.set('title', title);
            }
        }
    });
    return ReferenceEditorView;
});