define('services/repos/entities/historical_event', [
    'require',
    'exports',
    'module',
    'services/repos/entities/base',
    'services/repos/entities/date_description'
], function (require) {
    var BaseModel = require('./base'), DateDescription = require('./date_description');
    var HistoricalEvent = BaseModel.extend({
        relations: { date: { class: DateDescription } },
        defaults: function () {
            return {
                title: '',
                description: '',
                location: '',
                date: new DateDescription()
            };
        },
        getMoment: function () {
            var date = this.get('date');
            return date ? date.get('calendar') : null;
        }
    });
    return HistoricalEvent;
});