define('modules/work_editor/sections/work_master/sections/edition_master/sections/volume_master/volume_section', [
    'require',
    'exports',
    'module',
    'underscore',
    'modules/work_editor/controls/publication_info/publication_info',
    'controls/summary/summary',
    'modules/work_editor/sections/work_master/sections/edition_master/sections/volume_master/volume_controller',
    'modules/work_editor/sections/work_master/sections/edition_master/sections/volume_master/views/volume_view',
    'modules/work_editor/sections/work_master/sections/edition_master/sections/volume_master/views/author_refs_view'
], function (require) {
    var _ = require('underscore'), PublicationInfoControl = require('modules/work_editor/controls/publication_info/publication_info'), SummaryControl = require('controls/summary/summary'), VolumeMasterController = require('./volume_controller'), VolumeView = require('./views/volume_view'), AuthorRefsView = require('./views/author_refs_view');
    function constructSection(options) {
        var opts = _.defaults(options, { viewParentId: null });
        if (!opts.volume) {
            throw new TypeError('no volume model provided');
        }
        var controller = new VolumeMasterController({ volume: opts.volume });
        var viewModel = controller.getViewModel();
        var volumeView = new VolumeView({
            model: viewModel,
            parentId: opts.viewParentId
        });
        var authorsView = new AuthorRefsView({ collection: viewModel.get('authors') });
        var otherAuthorsView = new AuthorRefsView({
            collection: viewModel.get('otherAuthors'),
            hideEmptyView: true
        });
        var pubInfoView = new PublicationInfoControl.DisplayView({ model: viewModel.get('publicationInfo') });
        var summaryView = new SummaryControl.DisplayView({ model: viewModel.get('summary') });
        function attachControls() {
            volumeView.getRegion('authors').show(authorsView);
            volumeView.getRegion('otherAuthors').show(otherAuthorsView);
            volumeView.getRegion('publicationInfo').show(pubInfoView);
            volumeView.getRegion('summary').show(summaryView);
        }
        volumeView.once('show', attachControls);
        return {
            getController: function () {
                return controller;
            },
            getView: function () {
                return volumeView;
            }
        };
    }
    return { create: constructSection };
});