define('modules/work_editor/controls/titles_editor/views/titles_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'backbone.wreqr',
    'mixins/editable_field_view_mixin',
    'modules/work_editor/controls/titles_editor/views/templates/title',
    'modules/work_editor/controls/titles_editor/views/templates/titles'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), Wreqr = require('backbone.wreqr'), EditableFieldMixin = require('mixins/editable_field_view_mixin');
    var KEY_ENTER = 13;
    var TitleView = Marionette.ItemView.extend({
        template: require('./templates/title'),
        tagName: 'section',
        className: 'title editable-field',
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing',
            'click button.delete': function () {
                this.model.destroy();
            },
            'keyup input': function (e) {
                if (e.keyCode === KEY_ENTER) {
                    this.stopEditing();
                }
            }
        },
        modelEvents: {
            'change:type': function (model, value) {
                this.$('input[name=type]').val(value);
                this.$('.display .type').text(value || '<span class="text-muted">[no type]</span>');
            },
            'change:title': function (model, value) {
                this.$('input[name=title]').val(value);
                this.$('.display .title').text(value || '<span class="text-muted">[no title]</span>');
            },
            'change:subtitle': function (model, value) {
                this.$('input[name=subtitle]').val(value);
                this.$('.display .subtitle').text(value);
                if (value) {
                    this.$('.title-subtitle-sep').show();
                } else {
                    this.$('.title-subtitle-sep').hide();
                }
            }
        },
        templateHelpers: function () {
            return { typeListId: this.typeListId };
        },
        initialize: function (options) {
            var opts = _.defaults(options, {});
            _.extend(this, _.pick(opts, 'typeListId'));
            if (opts.channel) {
                this.listenTo(opts.channel, 'commit', this.stopEditing);
            }
            this.initializeEditableField(options);
        },
        onFocus: function () {
            this.$('input[name=type]').focus();
        },
        onBlur: function () {
            this.updateViewModel();
        },
        updateViewModel: function () {
            this.model.set({
                type: this.$('input[name=type]').val(),
                title: this.$('input[name=title]').val(),
                subtitle: this.$('input[name=subtitle]').val()
            });
            if (this.model.changedAttributes()) {
                this.model.flush();
            }
        }
    });
    _.defaults(TitleView.prototype, EditableFieldMixin);
    var NoTitlesView = Marionette.ItemView.extend({
        template: function () {
            return '[no titles added yet]';
        },
        tagName: 'span',
        className: 'text-muted',
        triggers: { dblclick: 'add:title' }
    });
    var TitlesView = Marionette.CompositeView.extend({
        template: require('./templates/titles'),
        childView: TitleView,
        emptyView: NoTitlesView,
        childViewContainer: '.titles',
        childViewOptions: function () {
            return {
                typeListId: this.typeListId,
                channel: this.channel
            };
        },
        events: {
            'click button.add-title': function () {
                this.collection.add({});
            }
        },
        templateHelpers: function () {
            return {
                typeListId: this.typeListId,
                types: this.types
            };
        },
        initialize: function (options) {
            var opts = _.defaults(options, {
                channel: new Wreqr.EventAggregator(),
                title: 'Authors',
                typeListId: 'titleTypesList-' + this.cid,
                types: [
                    'Canonical',
                    'Short'
                ]
            });
            _.extend(this, _.pick(opts, 'channel', 'title', 'typeListId', 'types'));
            this.on('childview:add:title', function () {
                this.collection.add({});
            }, this);
            this.rendered = false;
        },
        onAddChild: function (childView) {
            if (this.rendered) {
                childView.startEditing();
            }
        },
        onShow: function () {
            this.rendered = true;
        }
    });
    return TitlesView;
});