define('notifications/index', [
    'require',
    'exports',
    'module',
    'promise',
    'jquery',
    'underscore',
    'controls/modal/modal',
    'notifications/entities/message',
    'notifications/entities/messages',
    'notifications/views/messages_view',
    'notifications/display_chain/default_message_display_handler',
    'notifications/display_chain/alert_message_display_handler',
    'notifications/display_chain/confirm_message_display_handler'
], function (require) {
    var Promise = require('promise'), $ = require('jquery'), _ = require('underscore'), Modal = require('controls/modal/modal'), Message = require('./entities/message'), MessageCollection = require('./entities/messages'), MessagesView = require('./views/messages_view'), DefaultMessageDisplayHandler = require('./display_chain/default_message_display_handler'), AlertMessageDisplayHandler = require('./display_chain/alert_message_display_handler'), ConfirmMessageDisplayHandler = require('./display_chain/confirm_message_display_handler');
    function assembleMessageDisplayChain(opts) {
        var defaultHandler = new DefaultMessageDisplayHandler({ collection: opts.messageCollection });
        var alertHandler = new AlertMessageDisplayHandler({ region: opts.modalRegion });
        alertHandler.next = defaultHandler;
        var confirmHandler = new ConfirmMessageDisplayHandler({ region: opts.modalRegion });
        confirmHandler.next = alertHandler;
        return confirmHandler;
    }
    function NotificationService(opts) {
        opts = opts || {};
        if (!opts.el) {
            this._defaultElement = document.createElement('div');
            document.body.appendChild(this._defaultElement);
            opts.el = this._defaultElement;
        }
        this.messageCollection = new MessageCollection();
        this.messagesView = new MessagesView({ collection: this.messageCollection });
        this.messagesView.render();
        this.setEl(opts.el);
        var modalElement = $('<div>', {
            class: 'modal fade',
            tabindex: '-1',
            role: 'dialog',
            'aria-hidden': 'true'
        }).appendTo(document.body);
        this.modalRegion = new Modal.Region({ el: modalElement });
        this.messageDisplayChain = assembleMessageDisplayChain({
            messageCollection: this.messageCollection,
            modalRegion: this.modalRegion
        });
    }
    _.extend(NotificationService.prototype, {
        display: function (attrs, options) {
            var chain = this.messageDisplayChain;
            return new Promise(function (resolve) {
                var message = new Message(attrs);
                message.on('dismiss', resolve);
                chain.handle(message, options);
            });
        },
        setEl: function (el) {
            var $el = $(el);
            if (!$el.length) {
                throw new TypeError('selector {' + el + '} does not reference any element');
            }
            $el.append(this.messagesView.el);
            this.$el = $el;
            if (this._defaultElement) {
                this._defaultElement.remove();
                this._defaultElement = null;
            }
        }
    });
    _.each(Message.MESSAGE_TYPES, function (type) {
        NotificationService.prototype[type] = function (attrs, options) {
            if (_.isString(attrs)) {
                attrs = { title: attrs };
            }
            return this.display(_.extend(attrs, { type: type }), options);
        };
    });
    var instance = null;
    _.extend(NotificationService, {
        getInstance: function () {
            if (_.isNull(instance)) {
                instance = new NotificationService();
            }
            return instance;
        }
    });
    return NotificationService;
});