define('modules/person_editor/sections/master_editor/views/date_location_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'modules/person_editor/sections/master_editor/views/templates/date_location'
], function (require) {
    var Marionette = require('marionette');
    var DateLocationView = Marionette.ItemView.extend({
        template: require('./templates/date_location'),
        tagName: 'span',
        className: 'date-location',
        modelEvents: { 'change': 'render' }
    });
    return DateLocationView;
});