define('controls/search_form/views/results_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'controls/search_form/views/templates/default_result'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var DefaultResultView = Marionette.ItemView.extend({
        template: require('./templates/default_result'),
        tagName: 'a',
        className: 'list-group-item result',
        triggers: { click: 'click' },
        serializeData: function () {
            var data = Marionette.ItemView.prototype.serializeData.apply(this, arguments);
            return _.defaults(data, { description: '' });
        }
    });
    var NoResultsView = Marionette.ItemView.extend({ template: _.constant('<span class="text-muted">[no results]</span>') });
    var ResultsView = Marionette.CollectionView.extend({
        emptyView: NoResultsView,
        childViewEventPrefix: 'result',
        className: 'list-group',
        getChildView: function () {
            return this.resultView;
        },
        initialize: function (options) {
            var opts = _.defaults(options, { resultView: DefaultResultView });
            _.extend(this, _.pick(opts, 'resultView'));
        }
    });
    return ResultsView;
});