define('modules/work_editor/sections/work_master/views/author_refs_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var AuthorRefView = Marionette.ItemView.extend({
        template: function (ctx) {
            var display = ctx.lastName;
            if (ctx.firstName) {
                display += ', ' + ctx.firstName;
            }
            if (_.last(display) !== '.') {
                display += '.';
            }
            return display;
        },
        tagName: 'a',
        className: 'author',
        attributes: function () {
            return { href: '#' };
        },
        modelEvents: { change: 'render' }
    });
    var NoAuthorRefsView = Marionette.ItemView.extend({
        template: function () {
            return '[no authors]';
        },
        tagName: 'span',
        className: 'text-muted'
    });
    var AuthorRefsView = Marionette.CollectionView.extend({
        childView: AuthorRefView,
        getEmptyView: function () {
            if (!this.hideEmptyView) {
                return NoAuthorRefsView;
            }
        },
        tagName: 'span',
        className: 'authors',
        initialize: function (options) {
            var opts = _.defaults(options, { hideEmptyView: false });
            _.extend(this, _.pick(opts, 'hideEmptyView'));
        }
    });
    return AuthorRefsView;
});