define('services/repos/entities/volume', [
    'require',
    'exports',
    'module',
    'services/repos/entities/base',
    'underscore',
    'mixins/dirtyable_model_mixin',
    'services/repos/entities/author_refs',
    'services/repos/entities/titles',
    'services/repos/entities/publication_info'
], function (require) {
    var BaseModel = require('./base'), _ = require('underscore'), DirtyableModelMixin = require('mixins/dirtyable_model_mixin'), AuthorRefs = require('./author_refs'), Titles = require('./titles'), PublicationInfo = require('./publication_info');
    var Volume = BaseModel.extend({
        relations: {
            publicationInfo: { class: PublicationInfo },
            authors: { class: AuthorRefs },
            titles: { class: Titles },
            otherAuthors: { class: AuthorRefs }
        },
        defaults: function () {
            return {
                volumeNumber: '',
                publicationInfo: new PublicationInfo(),
                authors: new AuthorRefs(),
                titles: new Titles(),
                otherAuthors: new AuthorRefs(),
                summary: '',
                series: ''
            };
        },
        initialize: function (attributes, options) {
            this.initializeDirtyable(options);
        },
        getCanonicalTitle: function () {
            var result = this.get('titles').find(function (title) {
                return title.isCanonical();
            });
            return _.isUndefined(result) ? null : result;
        },
        setRepo: function (repo) {
            this._repo = repo;
        },
        setEdition: function (edition) {
            this._edition = edition;
        },
        getEdition: function () {
            return this._edition;
        },
        getUri: function () {
            if (!this._repo) {
                throw new Error('no repo');
            }
            return this._repo.getVolumeUri(this);
        }
    });
    _.defaults(Volume.prototype, DirtyableModelMixin);
    return Volume;
});