define('catalogentries-reln/sections/info/views/target_selector_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'controls/select/select',
    'controls/autocomplete/autocomplete',
    'mixins/editable_field_view_mixin',
    'catalogentries-reln/sections/info/views/templates/target_selector'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), SelectControl = require('controls/select/select'), AutocompleteControl = require('controls/autocomplete/autocomplete'), EditableFieldViewMixin = require('mixins/editable_field_view_mixin');
    var KEY_ENTER = 13;
    var TargetSelectorView = Marionette.LayoutView.extend({
        template: require('./templates/target_selector'),
        className: 'editable-field',
        regions: { editionSelect: '> .control .edition-select' },
        templateHelpers: function () {
            var work = this.model.get('work'), edition = this.model.get('edition');
            return {
                workTitle: work ? work.get('title') : '',
                editionName: edition ? edition.get('name') : ''
            };
        },
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing',
            'keyup input': function (e) {
                if (e.keyCode === KEY_ENTER && !this._autocompleteJustClosed) {
                    this.stopEditing();
                }
            }
        },
        modelEvents: {
            'change:work': function (model, work) {
                if (work) {
                    this.$('> .display .title').text(work.get('title'));
                    this.$('input[name=workTitle]').val(work.get('title'));
                } else {
                    this.$('> .display .title').html('<span class="text-muted">[work]</span>');
                }
            },
            'change:edition': function (model, edition) {
                if (edition) {
                    this.$('> .display .edition-name').text(edition.get('name'));
                } else {
                    this.$('> .display .edition-name').html('<span class="text-muted">[edition]</span>');
                }
            }
        },
        initialize: function (opts) {
            if (!opts.channel) {
                throw new TypeError('no channel provided');
            }
            if (!opts.workSearchProvider) {
                throw new TypeError('no work search provider given');
            }
            _.extend(this, _.pick(opts, 'workSearchProvider'));
            this.listenTo(opts.channel, 'commit', this.stopEditing);
            this.initializeEditableField(opts);
            this._autocompleteJustClosed = false;
            this._changedByControl = false;
        },
        onShow: function () {
            this._editionSelectControl = new SelectControl({
                empty: '[all]',
                value: this.model.get('edition'),
                provider: function (edition) {
                    return {
                        value: edition.id,
                        label: edition.get('name')
                    };
                }
            });
            if (this.model.get('work')) {
                this._editionSelectControl.setOptions(this.model.get('work').get('editions'));
            }
            this.autocomplete = new AutocompleteControl({
                input: this.$('input[name=workTitle]'),
                target: this.$('.work-autocomplete'),
                provider: this.workSearchProvider
            });
            this.autocomplete.on('close', function () {
                this._autocompleteJustClosed = true;
                var _this = this;
                setTimeout(function () {
                    _this._autocompleteJustClosed = false;
                }, 100);
            }, this);
            this.autocomplete.on('select', function (workProxy) {
                var _this = this;
                workProxy.getViewModel().then(function (work) {
                    _this.model.set('work', work);
                    if (_this.model.changedAttributes()) {
                        _this._changedByControl = true;
                    }
                    _this._editionSelectControl.setOptions(work.get('editions').models);
                });
            }, this);
            this.getRegion('editionSelect').show(this._editionSelectControl.getView());
        },
        onFocus: function () {
            this.$('input[name=workTitle]').focus();
        },
        onBlur: function () {
            this.updateViewModel();
        },
        updateViewModel: function () {
            this.model.set('edition', this._editionSelectControl.getValue());
            if (this._changedByControl || this.model.changedAttributes()) {
                this.model.flush();
            }
            this._changedByControl = false;
        }
    });
    _.defaults(TargetSelectorView.prototype, EditableFieldViewMixin);
    return TargetSelectorView;
});