define('modules/work_editor/controls/author_refs_editor/entities/author_ref', [
    'require',
    'exports',
    'module',
    'backbone'
], function (require) {
    var Backbone = require('backbone');
    var AuthorRefViewModel = Backbone.Model.extend({
        defaults: {
            role: 'Author',
            firstName: '',
            lastName: '',
            authorId: null,
            authorLabel: null
        },
        flush: function () {
            this.trigger('flush', this);
        }
    });
    return AuthorRefViewModel;
});