define('modules/person_editor/sections/master_editor/entities/date_location', [
    'require',
    'exports',
    'module',
    'backbone',
    'moment'
], function (require) {
    var Backbone = require('backbone'), moment = require('moment');
    var DateLocationViewModel = Backbone.Model.extend({
        defaults: function () {
            return {
                location: '',
                dateDisplay: '',
                dateValue: null
            };
        },
        parse: function (attrs) {
            if (attrs.dateValue) {
                attrs.dateValue = moment(attrs.dateValue);
            }
            return attrs;
        },
        flush: function () {
            throw new Error('view model is read-only');
        }
    });
    return DateLocationViewModel;
});