define('catalogentries-digitalcopies/sections/info/dc_info_controller', [
    'require',
    'exports',
    'module',
    'underscore',
    'controls/summary/summary',
    'catalogentries-digitalcopies/sections/info/entities/copy_ref',
    'catalogentries-digitalcopies/sections/info/entities/copy_refs'
], function (require) {
    var _ = require('underscore'), SummaryControl = require('controls/summary/summary'), CopyReferenceViewModel = require('./entities/copy_ref'), CopyReferenceDomainModelCollection = require('./entities/copy_refs');
    function bindCopyReferenceViewModel(domainModel, viewModel) {
        viewModel.on('change:title', function (model, value) {
            domainModel.set('title', value);
        });
        viewModel.get('summary').on('change:summary', function (summaryViewModel) {
            domainModel.set('summary', summaryViewModel.get('summary'));
        });
        domainModel.on('change:title', function (model, value) {
            viewModel.set('title', value);
        });
        domainModel.on('change:copyId', function (model, value) {
            viewModel.set('copyId', value);
        });
        domainModel.on('change:summary', function (model, value) {
            viewModel.get('summary').set('summary', value);
        });
        return Promise.resolve(viewModel);
    }
    function createCopyReferenceViewModel(domainModel, services) {
        var summaryViewModel = new SummaryControl.Model({ summary: domainModel.get('summary') });
        var viewModel = new CopyReferenceViewModel({
            copyId: domainModel.get('copyId'),
            title: domainModel.get('title'),
            summary: summaryViewModel
        });
        return bindCopyReferenceViewModel(domainModel, viewModel, services);
    }
    function bindCopyReferenceViewModelCollection(domainCollection, viewCollection) {
        domainCollection.on('add', function (domainModel) {
            createCopyReferenceViewModel(domainModel).then(function (viewModel) {
                viewCollection.add(viewModel);
            });
        });
        return Promise.resolve(viewCollection);
    }
    function createCopyReferenceViewModelCollection(domainCollection, services) {
        var modelPromises = domainCollection.map(createCopyReferenceViewModel);
        return Promise.all(modelPromises).then(function (models) {
            var viewCollection = new CopyReferenceDomainModelCollection(models);
            return bindCopyReferenceViewModelCollection(domainCollection, viewCollection, services);
        });
    }
    function DigitalCopyInfoController() {
        _.bindAll(this, 'createViewModel', 'createViewCollection');
    }
    _.extend(DigitalCopyInfoController.prototype, {
        createViewModel: function (domainModel) {
            return createCopyReferenceViewModel(domainModel);
        },
        createViewCollection: function (domainCollection) {
            return createCopyReferenceViewModelCollection(domainCollection);
        }
    });
    return DigitalCopyInfoController;
});