define('mixins/editable_field_view_mixin', [
    'require',
    'exports',
    'module',
    'underscore'
], function (require) {
    var _ = require('underscore');
    var EditableFieldViewMixin = {
        onFocus: _.noop,
        onBlur: _.noop,
        initializeEditableField: function (options) {
            this.channel = options.channel;
            if (this.channel) {
                this.channel.on('focus:editable:field', this.stopEditing, this);
            }
            this._focus = false;
        },
        startEditing: function () {
            if (this._focus) {
                return;
            }
            if (this.channel) {
                this.channel.trigger('focus:editable:field', {
                    view: this,
                    model: this.model
                });
            }
            this.$el.addClass('editing');
            this._focus = true;
            this.onFocus();
        },
        stopEditing: function () {
            if (!this._focus) {
                return;
            }
            this.$el.removeClass('editing');
            this._focus = false;
            this.onBlur();
        }
    };
    return EditableFieldViewMixin;
});