define('controls/modal/marionette.region.modal', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'bootstrap'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    require('bootstrap');
    var ModalRegion = Marionette.Region.extend({
        getEl: function () {
            var $el = Marionette.Region.prototype.getEl.apply(this, arguments);
            $el.on('hidden.bs.modal', _.bind(this.destroyModal, this));
            return $el;
        },
        onShow: function (view) {
            this.showModal(view);
        },
        showModal: function (view) {
            var region = this;
            var closeHandler = function (done) {
                region.hideModal(view).then(done);
            };
            this.listenTo(view, 'modal:close', closeHandler);
            this.listenTo(view, 'modal:dismiss', closeHandler);
            this.$el.modal({
                backdrop: 'static',
                keyboard: false
            });
            this.$el.modal('show');
        },
        hideModal: function () {
            var _this = this;
            return new Promise(function (resolve) {
                _this.$el.one('hidden.bs.modal', resolve);
                _this.$el.modal('hide');
            });
        },
        destroyModal: function () {
            this.stopListening(this.currentView);
            this.empty();
        }
    });
    return ModalRegion;
});