define('modules/work_editor/sections/work_info/work_info_section', [
    'require',
    'exports',
    'module',
    'backbone.wreqr',
    'underscore',
    'controls/summary/summary',
    'modules/work_editor/controls/author_refs_editor/author_refs_editor',
    'modules/work_editor/controls/titles_editor/titles_editor',
    'modules/work_editor/sections/work_info/work_info_controller',
    'modules/work_editor/sections/work_info/views/work_info_view'
], function (require) {
    var Wreqr = require('backbone.wreqr'), _ = require('underscore'), SummaryControl = require('controls/summary/summary'), AuthorRefsEditorControl = require('modules/work_editor/controls/author_refs_editor/author_refs_editor'), TitlesEditorControl = require('modules/work_editor/controls/titles_editor/titles_editor'), WorkInfoController = require('./work_info_controller'), WorkInfoView = require('./views/work_info_view');
    function constructSection(options) {
        var opts = _.defaults(options, { channel: new Wreqr.EventAggregator() });
        if (!opts.work) {
            throw new TypeError('no work provided to work info section');
        }
        if (!opts.peopleRepo) {
            throw new TypeError('no people repository provided to work info section');
        }
        var controller = new WorkInfoController({ peopleRepo: opts.peopleRepo });
        controller.setDomainModel(opts.work);
        var viewModel = controller.getViewModel();
        var workView = new WorkInfoView({
            model: viewModel,
            channel: opts.channel
        });
        var provider = _.throttle(function (q, limit) {
            return opts.peopleRepo.findAllByName(q, { limit: limit }).then(function (results) {
                return _.map(results, function (person) {
                    return {
                        label: person.formattedName,
                        obj: controller.createAuthorResultViewModel(person)
                    };
                });
            });
        }, 500);
        var authorsView = new AuthorRefsEditorControl.View({
            collection: viewModel.get('authors'),
            channel: opts.channel,
            authorSearchProvider: provider
        });
        var titlesView = new TitlesEditorControl.View({
            collection: viewModel.get('titles'),
            channel: opts.channel
        });
        var otherAuthorsView = new AuthorRefsEditorControl.View({
            collection: viewModel.get('otherAuthors'),
            channel: opts.channel,
            title: 'Other Authors',
            authorSearchProvider: provider
        });
        var summaryView = new SummaryControl.EditorView({
            model: viewModel.get('summary'),
            channel: opts.channel
        });
        function attachControls() {
            workView.getRegion('authors').show(authorsView);
            workView.getRegion('titles').show(titlesView);
            workView.getRegion('otherAuthors').show(otherAuthorsView);
            workView.getRegion('summary').show(summaryView);
        }
        workView.once('attach', attachControls);
        return {
            getController: function () {
                return controller;
            },
            getView: function () {
                return workView;
            }
        };
    }
    return { create: constructSection };
});