define('notifications/views/alert_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'notifications/views/templates/alert'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var ModalView = Marionette.LayoutView.extend({
        template: require('./templates/alert'),
        className: 'modal-dialog modal-lg',
        regions: { content: '.modal-body' },
        templateHelpers: function () {
            return _.pick(this, 'dismissText');
        },
        initialize: function (options) {
            var opts = _.defaults(options || {}, { dismissText: 'Dismiss' });
            _.extend(this, _.pick(opts, 'dismissText'));
        },
        show: function (view) {
            this.getRegion('content').show(view);
        }
    });
    return ModalView;
});