define('modules/catalog_navigation/sections/works_nav_section/works_nav_section', [
    'require',
    'exports',
    'module',
    'underscore',
    'controls/search_form/search_form',
    'modules/catalog_navigation/sections/works_nav_section/views/work_result_view',
    'modules/catalog_navigation/sections/works_nav_section/views/works_nav_view'
], function (require) {
    var _ = require('underscore'), SearchForm = require('controls/search_form/search_form'), WorkResultView = require('./views/work_result_view'), WorksNavLayoutView = require('./views/works_nav_view');
    function constructSection(options) {
        var opts = _.defaults(options, { limit: 20 });
        if (!opts.channel) {
            throw new TypeError('no channel provided');
        }
        if (!opts.worksRepo) {
            throw new TypeError('no works repository provided');
        }
        var view = new WorksNavLayoutView();
        view.on('new:work', function () {
            opts.channel.trigger('new:work');
        });
        function workSearch(title, limit) {
            var findOpts = { limit: limit || opts.limit };
            return opts.worksRepo.search(title, findOpts).then(function (works) {
                return _.sortBy(works, function (work) {
                    var authorName = '';
                    if (work.authors.length > 0) {
                        authorName += work.authors[0].lastName + work.authors[0].firstName;
                    }
                    return authorName + work.title;
                });
            });
        }
        function selectWork(workResult) {
            opts.worksRepo.findWork(workResult.get('id')).then(function (work) {
                opts.channel.trigger('edit:work', work);
            });
        }
        var searchForm = new SearchForm({
            searchProvider: workSearch,
            resultView: WorkResultView,
            placeholder: 'search by title'
        });
        searchForm.on('result:click', selectWork);
        function attachControls() {
            view.getRegion('search').show(searchForm.getView());
        }
        view.once('show', attachControls);
        return {
            getView: function () {
                return view;
            }
        };
    }
    return { create: constructSection };
});