define('modules/person_editor/sections/master_editor/master_editor_controller', [
    'require',
    'exports',
    'module',
    'underscore',
    'controls/summary/summary',
    'modules/person_editor/sections/master_editor/entities/person',
    'modules/person_editor/sections/master_editor/entities/name',
    'modules/person_editor/sections/master_editor/entities/names',
    'modules/person_editor/sections/master_editor/entities/date_location'
], function (require) {
    var _ = require('underscore'), SummaryControl = require('controls/summary/summary'), PersonViewModel = require('./entities/person'), NameViewModel = require('./entities/name'), NameViewModelCollection = require('./entities/names'), DateLocationViewModel = require('./entities/date_location');
    function bindNameViewModel(domainModel, viewModel) {
        domainModel.on('change:title', function (model, value) {
            viewModel.set('prefix', value);
        });
        domainModel.on('change:givenName', function (model, value) {
            viewModel.set('first', value);
        });
        domainModel.on('change:middleName', function (model, value) {
            viewModel.set('middle', value);
        });
        domainModel.on('change:familyName', function (model, value) {
            viewModel.set('last', value);
        });
        domainModel.on('change:suffix', function (model, value) {
            viewModel.set('suffix', value);
        });
        domainModel.on('change:displayName', function (model, value) {
            viewModel.set('display', value);
        });
        domainModel.on('destroy', function (options) {
            if (domainModel === options.domainModel) {
                return;
            }
            _.extend(options, { domainModel: domainModel });
            viewModel.destroy(options);
        });
    }
    function createNameViewModel(domainModel) {
        var viewModel = new NameViewModel({
            prefix: domainModel.get('title'),
            first: domainModel.get('givenName'),
            middle: domainModel.get('middleName'),
            last: domainModel.get('familyName'),
            suffix: domainModel.get('suffix'),
            display: domainModel.get('displayName')
        });
        bindNameViewModel(domainModel, viewModel);
        return viewModel;
    }
    function bindNameViewModelCollection(domainCollection, viewCollection) {
        domainCollection.on('add', function (domainModel, collection, options) {
            if (viewCollection.contains(options.viewModel)) {
                return;
            }
            _.extend(options, { domainModel: domainModel });
            var viewModel = createNameViewModel(domainModel);
            viewCollection.add(viewModel, options);
        });
    }
    function createNameViewModelCollection(domainCollection) {
        var viewCollection = new NameViewModelCollection(domainCollection.map(createNameViewModel));
        bindNameViewModelCollection(domainCollection, viewCollection);
        return viewCollection;
    }
    function bindDateLocationViewModel(domainModel, viewModel) {
        domainModel.get('date').on('change:calendar', function (model, value) {
            viewModel.set('dateValue', value);
        });
        domainModel.get('date').on('change:description', function (model, value) {
            viewModel.set('dateDisplay', value);
        });
        domainModel.on('change:location', function (model, value) {
            viewModel.set('location', value);
        });
    }
    function createDateLocationViewModel(domainModel) {
        var viewModel = new DateLocationViewModel({
            dateValue: domainModel.get('date').get('calendar'),
            dateDisplay: domainModel.get('date').get('description'),
            location: domainModel.get('location')
        });
        bindDateLocationViewModel(domainModel, viewModel);
        return viewModel;
    }
    function bindPersonViewModel(domainModel, viewModel) {
        domainModel.on('change:summary', function (model, value) {
            viewModel.get('summary').set('summary', value);
        });
    }
    function createPersonViewModel(domainModel) {
        var summaryViewModel = new SummaryControl.Model({ summary: domainModel.get('summary') });
        var viewModel = new PersonViewModel({
            displayName: createNameViewModel(domainModel.get('displayName')),
            names: createNameViewModelCollection(domainModel.get('names')),
            birth: createDateLocationViewModel(domainModel.get('birth')),
            death: createDateLocationViewModel(domainModel.get('death')),
            summary: summaryViewModel
        });
        bindPersonViewModel(domainModel, viewModel);
        return viewModel;
    }
    function PersonInfoEditorController() {
    }
    _.extend(PersonInfoEditorController.prototype, {
        setDomainModel: function (domainModel) {
            this.domainModel = domainModel;
            this.viewModel = null;
        },
        getViewModel: function () {
            if (!this.viewModel) {
                this.viewModel = createPersonViewModel(this.domainModel);
            }
            return this.viewModel;
        }
    });
    return PersonInfoEditorController;
});