define('catalogentries-reln/sections/info/relationship_info_controller', [
    'require',
    'exports',
    'module',
    'backbone',
    'promise',
    'underscore',
    'controls/summary/summary',
    'catalogentries-reln/sections/info/entities/relationship',
    'catalogentries-reln/sections/info/entities/type',
    'catalogentries-reln/sections/info/entities/types',
    'catalogentries-reln/sections/info/entities/work',
    'catalogentries-reln/sections/info/entities/edition',
    'catalogentries-reln/sections/info/entities/editions',
    'catalogentries-reln/sections/info/entities/work_edition_anchor'
], function (require) {
    var Backbone = require('backbone'), Promise = require('promise'), _ = require('underscore'), SummaryControl = require('controls/summary/summary'), RelationshipViewModel = require('./entities/relationship'), TypeViewModel = require('./entities/type'), TypeViewModelCollection = require('./entities/types'), WorkViewModel = require('./entities/work'), EditionViewModel = require('./entities/edition'), EditionViewModelCollection = require('./entities/editions'), WorkEditionAnchorViewModel = require('./entities/work_edition_anchor');
    function bindEditionViewModel(domainModel, viewModel) {
        domainModel.on('change:editionName', function (model, value) {
            viewModel.set('name', value);
        });
        domainModel.on('destroy', function () {
            viewModel.destroy();
        });
        return Promise.resolve(viewModel);
    }
    function createEditionViewModel(domainModel) {
        var viewModel = new EditionViewModel({
            id: domainModel.getUri(),
            name: domainModel.get('editionName')
        });
        return bindEditionViewModel(domainModel, viewModel);
    }
    function bindEditionViewModelCollection(domainCollection, viewCollection) {
        domainCollection.on('add', function (domainModel) {
            createEditionViewModel(domainModel).then(function (viewModel) {
                viewCollection.add(viewModel);
            });
        });
        return Promise.resolve(viewCollection);
    }
    function createEditionViewModelCollection(domainCollection) {
        var editionViewModelPromises = domainCollection.map(createEditionViewModel);
        var viewCollectionPromise = Promise.all(editionViewModelPromises).then(function (editionViewModels) {
            var viewCollection = new EditionViewModelCollection(editionViewModels);
            return bindEditionViewModelCollection(domainCollection, viewCollection);
        });
        return viewCollectionPromise;
    }
    function createWorkViewModel(domainModel) {
        var viewModelPromise = Promise.props({ editionViewCollection: createEditionViewModelCollection(domainModel.get('editions')) }).then(function (attrs) {
            var workTitle = domainModel.getCanonicalTitle();
            var workViewModel = new WorkViewModel({
                id: domainModel.getUri(),
                title: workTitle.getFullTitle(),
                editions: attrs.editionViewCollection
            });
            return workViewModel;
        });
        return viewModelPromise;
    }
    function resolveAnchorAttrs(uri, services) {
        var worksRepo = services.worksRepo;
        var workOrEditionPromise = uri ? worksRepo.find(uri) : Promise.resolve(null);
        return workOrEditionPromise.then(function (resolution) {
            var attrs = {
                work: null,
                edition: null
            };
            if (resolution) {
                if (resolution.type === 'work') {
                    attrs.work = createWorkViewModel(resolution.model);
                } else if (resolution.type === 'edition') {
                    attrs.edition = createEditionViewModel(resolution.model);
                    attrs.work = createWorkViewModel(resolution.model.getWork());
                } else {
                    throw new TypeError('unrecognized type resolved by works repository: ' + resolution.type);
                }
            }
            return Promise.props(attrs);
        });
    }
    function bindWorkEditionAnchorViewModel(domainModel, viewModel, services) {
        domainModel.on('change:entryUris', function (model, value) {
            var uri = _.first(value);
            if (uri === viewModel.getUri()) {
                return;
            }
            resolveAnchorAttrs(uri, services).then(function (attrs) {
                viewModel.set(attrs);
            });
        });
        viewModel.on('flush', function () {
            domainModel.set('entryUris', [viewModel.getUri()]);
        });
        return Promise.resolve(viewModel);
    }
    function createWorkEditionAnchorViewModel(domainModel, services) {
        var uri = domainModel ? _.first(domainModel.get('entryUris')) : null;
        var viewModelPromise = resolveAnchorAttrs(uri, services).then(function (attrs) {
            var viewModel = new WorkEditionAnchorViewModel(attrs);
            return bindWorkEditionAnchorViewModel(domainModel, viewModel, services);
        });
        return viewModelPromise;
    }
    function bindRelationshipViewModel(domainModel, viewModel) {
        domainModel.on('change:description', function (model, value) {
            viewModel.get('summary').set('summary', value);
        });
        viewModel.get('summary').on('flush', function (summaryViewModel) {
            domainModel.set('description', summaryViewModel.get('summary'));
        });
        var viewType = viewModel.get('type');
        var modelIsReversed = viewType ? viewType.get('reverse') : false;
        viewModel.on('change:type', function (model, vType) {
            var attrs = { typeId: vType.get('typeId') };
            var typeIsReversed = vType.get('reverse');
            if (typeIsReversed && !modelIsReversed || !typeIsReversed && modelIsReversed) {
                modelIsReversed = typeIsReversed;
                attrs.relatedEntities = domainModel.get('targetEntities');
                attrs.targetEntities = domainModel.get('relatedEntities');
            }
            domainModel.set(attrs);
        });
        return Promise.resolve(viewModel);
    }
    function createRelationshipViewModel(domainModel, services) {
        var viewTypes = services.viewTypes, work = services.work;
        var summaryViewModel = new SummaryControl.Model({ summary: domainModel.get('description') });
        var workUris = work.get('editions').map(function (e) {
            return e.getUri();
        });
        workUris.push(work.getUri());
        var sourceUris = _.chain(domainModel.get('relatedEntities').pluck('entryUris')).flatten().uniq().value();
        var targetUris = _.chain(domainModel.get('targetEntities').pluck('entryUris')).flatten().uniq().value();
        var reverse;
        var viewType = null;
        if (_.intersection(sourceUris, workUris).length > 0) {
            reverse = false;
            viewType = viewTypes.find(function (type) {
                var typesEqual = type.get('typeId') === domainModel.get('typeId'), isDirected = type.get('directed'), isReversed = type.get('reverse');
                return typesEqual && (!isDirected || !isReversed);
            });
            if (!viewType) {
                console.error('unable to find forward/undirected view type corresponding with type ID {' + domainModel.get('typeId') + '}');
                viewType = viewTypes.findWhere({ reverse: false });
            }
        } else if (_.intersection(targetUris, workUris).length > 0) {
            reverse = true;
            viewType = viewTypes.find(function (type) {
                var typesEqual = type.get('typeId') === domainModel.get('typeId'), isDirected = type.get('directed'), isReversed = type.get('reverse');
                return typesEqual && (!isDirected || isReversed);
            });
            if (!viewType) {
                console.error('unable to find reverse/undirected view type corresponding with type ID {' + domainModel.get('typeId') + '}');
                viewType = viewTypes.findWhere({ reverse: true });
            }
        } else {
            throw new Error('current work does not appear to be a source or a target of this relationship');
        }
        if (!viewType) {
            throw new Error('unable to find view type for relationship');
        }
        var viewModelPromise = Promise.join(createWorkEditionAnchorViewModel(domainModel.get('relatedEntities').first(), services), createWorkEditionAnchorViewModel(domainModel.get('targetEntities').first(), services), function (source, target) {
            var attrs = {
                type: viewType,
                summary: summaryViewModel
            };
            if (reverse) {
                attrs.source = target;
                attrs.target = source;
            } else {
                attrs.source = source;
                attrs.target = target;
            }
            var viewModel = new RelationshipViewModel(attrs);
            return bindRelationshipViewModel(domainModel, viewModel);
        });
        return viewModelPromise;
    }
    function createViewTypeCollection(domainTypeCollection) {
        function createTypeViewModels(domainModel) {
            var fwdModel = new TypeViewModel({
                typeId: domainModel.id,
                title: domainModel.get('title'),
                directed: domainModel.get('isDirected')
            });
            var models = [fwdModel];
            if (domainModel.get('isDirected')) {
                models.push(new TypeViewModel({
                    typeId: domainModel.id,
                    title: domainModel.get('reverseTitle'),
                    directed: true,
                    reverse: true
                }));
            }
            return models;
        }
        var viewModels = _.flatten(domainTypeCollection.map(createTypeViewModels), true);
        return new TypeViewModelCollection(viewModels);
    }
    function RelationshipInfoController(options) {
        var opts = _.defaults(options || {}, {});
        if (!opts.work) {
            throw new TypeError('no focused work provided to act as relationship source');
        }
        if (!opts.types) {
            throw new TypeError('no relationship types provided');
        }
        if (!opts.worksRepo) {
            throw new TypeError('no works repository provided');
        }
        _.extend(this, _.pick(opts, 'worksRepo', 'types', 'work'));
        this.viewTypes = createViewTypeCollection(this.types);
        this.viewModelPromise = null;
    }
    _.extend(RelationshipInfoController.prototype, Backbone.Events, {
        setDomainModel: function (domainModel) {
            this.domainModel = domainModel;
            this.viewModelPromise = null;
        },
        getViewModel: function () {
            if (!this.viewModelPromise) {
                this.viewModelPromise = createRelationshipViewModel(this.domainModel, _.pick(this, 'worksRepo', 'viewTypes', 'work'));
            }
            return this.viewModelPromise;
        },
        getViewTypes: function () {
            return this.viewTypes;
        },
        convertWorkDomainModel: createWorkViewModel
    });
    return RelationshipInfoController;
});