define('services/repos/entities/date_description', [
    'require',
    'exports',
    'module',
    'services/repos/entities/base',
    'moment',
    'underscore'
], function (require) {
    var BaseModel = require('./base'), moment = require('moment'), _ = require('underscore');
    var DateDescription = BaseModel.extend({
        defaults: {
            description: '',
            calendar: null
        },
        parse: function (attrs) {
            if (attrs.calendar) {
                attrs.calendar = moment(attrs.calendar);
            }
            return attrs;
        },
        toJSON: function () {
            var json = BaseModel.prototype.toJSON.apply(this, arguments);
            if (_.has(json, 'calendar') && !_.isNull(json.calendar) && !_.isUndefined(json.calendar)) {
                json.calendar = json.calendar.format('YYYY-MM-DD');
            }
            return json;
        }
    });
    return DateDescription;
});