define('controls/autocomplete/autocomplete', [
    'require',
    'exports',
    'module',
    'backbone',
    'underscore',
    'jquery',
    'controls/autocomplete/entities/items',
    'controls/autocomplete/views/results_view'
], function (require) {
    var Backbone = require('backbone'), _ = require('underscore'), $ = require('jquery'), ResultItemCollection = require('./entities/items'), ResultsView = require('./views/results_view');
    var KEY_TAB = 9, KEY_ENTER = 13, KEY_ESC = 27, KEY_SPACE = 32, KEY_UP = 38, KEY_DOWN = 40;
    function AutocompleteControl(options) {
        var opts = _.defaults(options, { limit: 10 });
        if (!opts.provider) {
            throw new TypeError('no provider callback given');
        }
        if (!opts.input) {
            throw new TypeError('no input element on which to attach');
        }
        if (!opts.target) {
            throw new TypeError('no target in which to render autocomplete list');
        }
        opts.input = $(opts.input);
        opts.target = $(opts.target);
        _.extend(this, _.pick(opts, 'provider', 'input', 'limit', 'target'));
        this._initializeEvents();
        this._open = false;
        this._collection = null;
        this._view = null;
    }
    _.extend(AutocompleteControl.prototype, Backbone.Events, {
        isOpen: function () {
            return this._open;
        },
        open: function () {
            if (this._open) {
                return;
            }
            this._collection = new ResultItemCollection();
            this._view = new ResultsView({ collection: this._collection });
            this._view.on('childview:select', function (itemView, args) {
                this.select(args.model.get('obj'));
            }, this);
            this.target.append(this._view.render().el);
            this._open = true;
            this.trigger('open');
        },
        close: function () {
            if (!this._open) {
                return;
            }
            this._collection = null;
            this._view.destroy();
            this._view = null;
            this._open = false;
            this.trigger('close');
        },
        lookup: function (query) {
            if (!this.isOpen()) {
                return;
            }
            var promise = this.provider(query, this.limit);
            this.trigger('lookup', query);
            var isCollectionReset = false;
            this._collection.once('reset', function () {
                isCollectionReset = true;
            });
            var _this = this;
            promise.then(function (results) {
                if (isCollectionReset) {
                    return;
                }
                _this._collection.reset(results);
                _this.trigger('results', results);
            });
        },
        select: function (obj) {
            this.close();
            this.trigger('select', obj);
        },
        _initializeEvents: function () {
            var _this = this;
            function onKeyDown(e) {
                if (_this.isOpen() && e.keyCode === KEY_ENTER) {
                    var model = _this._collection.getSelected();
                    if (!_.isNull(model)) {
                        _this.select(model.get('obj'));
                    }
                } else if (_this.isOpen() && (e.keyCode === KEY_ESC || e.keyCode === KEY_TAB)) {
                    _this.close();
                } else if (!_this.isOpen() && e.keyCode === KEY_SPACE && e.ctrlKey) {
                    _this.open();
                } else if (_this.isOpen() && e.keyCode === KEY_DOWN) {
                    _this._collection.selectNext();
                } else if (_this.isOpen() && e.keyCode === KEY_UP) {
                    _this._collection.selectPrevious();
                }
            }
            var specialKeys = [
                KEY_ENTER,
                KEY_ESC,
                KEY_SPACE,
                KEY_DOWN,
                KEY_UP
            ];
            function onKeyUp(e) {
                if (_.contains(specialKeys, e.keyCode)) {
                    return;
                }
                var value = _this.input.val();
                _this.lookup(value);
            }
            function onFocus() {
                _this.open();
            }
            function onBlur() {
                if (_this._view && _this._view.isFocused()) {
                    return;
                }
                _this.close();
            }
            this.input.on('keydown', onKeyDown).on('keyup', onKeyUp).on('focus', onFocus).on('blur', onBlur);
        }
    });
    return AutocompleteControl;
});