define('app', [
    'require',
    'exports',
    'module',
    'backbone.wreqr',
    'underscore',
    'catalogentries-digitalcopies/index',
    'catalogentries-reln/index',
    'services/registry',
    'services/rest_client',
    'services/repos/people',
    'services/repos/works',
    'views/app_layout',
    'modules/person_editor/person_editor',
    'modules/work_editor/work_editor',
    'modules/catalog_navigation/catalog_navigation'
], function (require) {
    var Wreqr = require('backbone.wreqr'), _ = require('underscore'), DigitalCopies = require('catalogentries-digitalcopies/index'), Relationships = require('catalogentries-reln/index'), ServiceRegistry = require('./services/registry'), RestClient = require('./services/rest_client'), PeopleRepository = require('./services/repos/people'), WorksRepository = require('./services/repos/works'), ApplicationLayoutView = require('./views/app_layout'), PersonEditor = require('./modules/person_editor/person_editor'), WorkEditor = require('./modules/work_editor/work_editor'), NavigationPanel = require('./modules/catalog_navigation/catalog_navigation');
    var serviceRegistry = new ServiceRegistry();
    var serviceHandles = [];
    var modules = {};
    var layout = null;
    function loadServices(config) {
        serviceHandles.push(serviceRegistry.register({
            id: 'edu.tamu.tcat.rest.client',
            name: 'Promise-based REST client',
            service: RestClient
        }));
        serviceHandles.push(serviceRegistry.register({
            id: 'edu.tamu.tcat.catalogentries.repos.people',
            name: 'People Repository',
            service: new PeopleRepository({
                apiEndpoint: config.api_endpoint_url + '/people',
                restClient: RestClient
            })
        }));
        serviceHandles.push(serviceRegistry.register({
            id: 'edu.tamu.tcat.catalogentries.repos.works',
            name: 'Works Repository',
            service: new WorksRepository({
                apiEndpoint: config.api_endpoint_url + '/works',
                restClient: RestClient
            })
        }));
        serviceHandles.push(serviceRegistry.register({
            id: 'edu.tamu.tcat.catalogentries.repos.relationships',
            name: 'Work Relationships Repository',
            service: new Relationships.Repository({
                apiEndpoint: config.api_endpoint_url + '/relationships',
                restClient: RestClient
            })
        }));
        serviceHandles.push(serviceRegistry.register({
            id: 'edu.tamu.tcat.catalogentries.repos.books',
            name: 'Digital Copies Book Search Repository',
            service: new DigitalCopies.BooksRepository({
                apiEndpoint: config.api_endpoint_url + '/resources/books',
                restClient: RestClient
            })
        }));
        serviceHandles.push(serviceRegistry.register({
            id: 'edu.tamu.tcat.catalogentries.repos.copies',
            name: 'Digital Copy References Repository',
            service: new DigitalCopies.ReferencesRepository({
                apiEndpoint: config.api_endpoint_url + '/copies',
                restClient: RestClient
            })
        }));
        serviceHandles.push(serviceRegistry.register({
            id: 'edu.tamu.tcat.catalogentries.channel',
            name: 'Common Event Bus (Backbone.Wreqr)',
            service: new Wreqr.EventAggregator()
        }));
    }
    function loadApplicationModules() {
        modules.personEditor = new PersonEditor({
            region: layout.getRegion('main'),
            peopleRepo: serviceRegistry.get('edu.tamu.tcat.catalogentries.repos.people').getInstance(),
            channel: serviceRegistry.get('edu.tamu.tcat.catalogentries.channel').getInstance()
        });
        modules.workEditor = new WorkEditor({
            region: layout.getRegion('main'),
            worksRepo: serviceRegistry.get('edu.tamu.tcat.catalogentries.repos.works').getInstance(),
            peopleRepo: serviceRegistry.get('edu.tamu.tcat.catalogentries.repos.people').getInstance(),
            relationshipsRepo: serviceRegistry.get('edu.tamu.tcat.catalogentries.repos.relationships').getInstance(),
            booksRepo: serviceRegistry.get('edu.tamu.tcat.catalogentries.repos.books').getInstance(),
            copiesRepo: serviceRegistry.get('edu.tamu.tcat.catalogentries.repos.copies').getInstance(),
            channel: serviceRegistry.get('edu.tamu.tcat.catalogentries.channel').getInstance()
        });
        modules.navigation = new NavigationPanel({
            region: layout.getRegion('nav'),
            worksRepo: serviceRegistry.get('edu.tamu.tcat.catalogentries.repos.works').getInstance(),
            peopleRepo: serviceRegistry.get('edu.tamu.tcat.catalogentries.repos.people').getInstance(),
            channel: serviceRegistry.get('edu.tamu.tcat.catalogentries.channel').getInstance()
        });
    }
    function start(config) {
        layout = new ApplicationLayoutView({ el: config.el });
        layout.render();
        loadServices(config);
        loadApplicationModules(config);
    }
    function stop() {
        _.invoke(serviceHandles, 'unregister');
    }
    var Application = {
        start: start,
        stop: stop
    };
    return Application;
});