define('controls/modal/views/layout_helper_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'controls/modal/views/templates/layout_helper'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var ModalView = Marionette.LayoutView.extend({
        template: require('./templates/layout_helper'),
        className: 'modal-dialog',
        regions: { content: '.modal-content > .modal-region' },
        templateHelpers: function () {
            return { title: this.title };
        },
        initialize: function (opts) {
            if (!opts.view) {
                throw new TypeError('no view provided');
            }
            _.extend(this, _.pick(opts, 'view'));
            this.title = _.result(this.view, 'title') || 'Attention';
        },
        onShow: function () {
            this.getRegion('content').show(this.view);
        }
    });
    return ModalView;
});