define('controls/summary/entities/summary', [
    'require',
    'exports',
    'module',
    'backbone'
], function (require) {
    var Backbone = require('backbone');
    var SummaryViewModel = Backbone.Model.extend({
        defaults: { summary: '' },
        getDisplayValue: function () {
            var value = this.get('summary');
            if (!value) {
                return '';
            }
            var strippedValue = value.replace(/<[^>]+>/g, '').trim();
            return strippedValue ? value : '';
        },
        flush: function () {
            this.trigger('flush', this);
        }
    });
    return SummaryViewModel;
});