define('notifications/display_chain/confirm_message_display_handler', [
    'require',
    'exports',
    'module',
    'underscore',
    'notifications/display_chain/message_display_chain_mixin',
    'notifications/views/confirm_view'
], function (require) {
    var _ = require('underscore'), MessageDisplayChainMixin = require('./message_display_chain_mixin'), ConfirmView = require('../views/confirm_view');
    function ConfirmMessageDisplayHandler(opts) {
        opts = opts || {};
        if (!opts.region) {
            throw new TypeError('no modal region provided');
        }
        _.extend(this, _.pick(opts, 'region'));
    }
    _.extend(ConfirmMessageDisplayHandler.prototype, MessageDisplayChainMixin, {
        handlesType: function (type) {
            return type === 'confirm';
        },
        display: function (model, options) {
            var view = new ConfirmView(_.extend(options || {}, { model: model }));
            this.region.show(view);
            if (model.has('view')) {
                view.show(model.get('view'));
            }
        }
    });
    return ConfirmMessageDisplayHandler;
});