define('modules/work_editor/sections/work_master/sections/edition_master/views/edition_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'notifications/index',
    'modules/work_editor/sections/work_master/sections/edition_master/views/templates/edition'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), Notifications = require('notifications/index').getInstance();
    var EditionView = Marionette.LayoutView.extend({
        template: require('./templates/edition'),
        className: 'panel panel-default edition',
        templateHelpers: function () {
            return {
                parentId: this.parentId,
                viewId: this.cid
            };
        },
        regions: {
            authors: '> .panel-collapse > .panel-body > .citation > .authors',
            otherAuthors: '> .panel-collapse > .panel-body > .citation > .other-authors',
            publicationInfo: '> .panel-collapse > .panel-body > .citation > .publication-info',
            summary: '> .panel-collapse > .panel-body > .summary',
            volumes: '> .panel-collapse > .panel-body > .volumes'
        },
        events: {
            'click button.link-copies': function (evt) {
                evt.stopPropagation();
                this.expand();
                this.trigger('link-copies');
            },
            'click button.edit-edition': function (evt) {
                evt.stopPropagation();
                this.expand();
                this.trigger('edit');
            },
            'click button.delete-edition': function (evt) {
                evt.stopPropagation();
                var _this = this;
                Notifications.confirm({
                    title: 'Delete Edition',
                    description: 'Are you sure you want to delete this edition?'
                }).then(function (result) {
                    if (result) {
                        _this.model.destroy();
                    }
                });
            }
        },
        modelEvents: {
            'change:editionName': function (model, value) {
                if (value) {
                    this.$('> .panel-heading > h4.panel-title').text(value);
                } else {
                    this.$('> .panel-heading > h4.panel-title').html('<span class="text-muted">[no edition name]</span>');
                }
            },
            'change:title': function (model, value) {
                if (_.isNull(value)) {
                    this.$('> .panel-collapse > .panel-body > .citation > .title').html('<span class="text-muted">[no canonical title]</span>');
                } else {
                    this.$('> .panel-collapse > .panel-body > .citation > .title').text(value);
                }
            }
        },
        initialize: function (options) {
            var opts = _.defaults(options, { parentId: null });
            _.extend(this, _.pick(opts, 'parentId'));
        },
        expand: function () {
            this.$('>.collapse').collapse('show');
        },
        collapse: function () {
            this.$('>.collapse').collapse('hide');
        },
        toggle: function () {
            this.$('>.collapse').collapse('toggle');
        }
    });
    return EditionView;
});