define('views/tab_panel_view', [
    'require',
    'exports',
    'module',
    'backbone',
    'marionette',
    'underscore'
], function (require) {
    var Backbone = require('backbone'), Marionette = require('marionette'), _ = require('underscore');
    var TabModel = Backbone.Model.extend({
        defaults: function () {
            return {
                title: '',
                view: new Marionette.ItemView()
            };
        }
    });
    var TabModelCollection = Backbone.Collection.extend({ model: TabModel });
    var TabView = Marionette.ItemView.extend({
        tagName: 'li',
        attributes: { role: 'presentation' },
        template: function (ctx) {
            return '<a href="#' + ctx.target + '" aria-controls="' + ctx.target + '" role="tab" data-toggle="tab">' + ctx.title + '</a>';
        },
        templateHelpers: function () {
            return { target: this.model.get('view').cid };
        },
        modelEvents: { 'change:title': 'render' }
    });
    var PaneView = Marionette.LayoutView.extend({
        template: _.constant('<div class="main-tab-content"></div>'),
        className: 'tab-pane',
        id: function () {
            return this.model.get('view').cid;
        },
        attributes: { role: 'tabpanel' },
        regions: { main: '> .main-tab-content' },
        onShow: function () {
            this.getRegion('main').show(this.model.get('view'));
        }
    });
    var TabListView = Marionette.CollectionView.extend({
        tagName: 'ul',
        className: 'nav nav-tabs',
        attributes: { role: 'tablist' },
        childView: TabView
    });
    var PaneListView = Marionette.CollectionView.extend({
        className: 'tab-content',
        childView: PaneView
    });
    var TabPanelView = Marionette.LayoutView.extend({
        template: _.constant('<div class="tabs"></div><div class="panes"></div>'),
        attributes: { role: 'tabpanel' },
        regions: {
            tabs: '> .tabs',
            panes: '> .panes'
        },
        initialize: function () {
            this.collection = new TabModelCollection();
        },
        add: function (title, view) {
            return this.collection.add({
                title: title,
                view: view
            });
        },
        onShow: function () {
            var tabsView = new TabListView({ collection: this.collection });
            var panesView = new PaneListView({ collection: this.collection });
            this.getRegion('tabs').show(tabsView);
            this.getRegion('panes').show(panesView);
        },
        onDestroy: function () {
            this.collection.reset();
            this.collection = null;
        }
    });
    return TabPanelView;
});