define('services/repos/entities/title', [
    'require',
    'exports',
    'module',
    'services/repos/entities/base'
], function (require) {
    var BaseModel = require('./base');
    var Title = BaseModel.extend({
        defaults: {
            type: '',
            lg: '',
            title: '',
            subtitle: ''
        },
        initialize: function () {
            this.on('change:title change:subtitle', function () {
                this.trigger('change:fullTitle', this, this.getFullTitle());
            }, this);
        },
        getFullTitle: function () {
            var fullTitle = this.get('title');
            if (this.get('subtitle')) {
                fullTitle += ': ' + this.get('subtitle');
            }
            return fullTitle;
        },
        isCanonical: function () {
            return this.get('type').match(/canonical/i);
        }
    });
    return Title;
});