define('notifications/views/message_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'notifications/views/templates/message'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var BS_TYPE_MAP = { error: 'danger' };
    var MessageView = Marionette.ItemView.extend({
        template: require('./templates/message'),
        attributes: { role: 'alert' },
        className: function () {
            return 'alert alert-dismissable alert-' + this.getType();
        },
        events: {
            'closed.bs.alert': function () {
                this.model.dismiss();
            }
        },
        getType: function () {
            var type = this.model.get('type');
            return _.has(BS_TYPE_MAP, type) ? BS_TYPE_MAP[type] : type;
        }
    });
    return MessageView;
});