define('catalogentries-digitalcopies/books_repository', [
    'require',
    'exports',
    'module',
    'underscore',
    'catalogentries-digitalcopies/entities/search_results'
], function (require) {
    var _ = require('underscore'), SearchResultDomainModelCollection = require('./entities/search_results');
    function SearchRepository(options) {
        var opts = _.defaults(options || {}, {});
        if (!opts.apiEndpoint) {
            throw new TypeError('no API endpoint');
        }
        if (!opts.restClient) {
            throw new TypeError('no REST client given');
        }
        _.extend(this, _.pick(opts, 'apiEndpoint', 'restClient'));
    }
    _.extend(SearchRepository.prototype, {
        search: function (query, options) {
            var opts = _.defaults(options || {}, {
                offset: 0,
                limit: 25
            });
            var repo = this;
            return this.restClient.get(this.apiEndpoint + '/search', {
                q: query,
                offset: opts.offset,
                limit: opts.limit
            }).catch(function (err) {
                console.error('unable to fetch results: ' + err.error);
                throw new Error('Cannot connect to server');
            }).then(function (data) {
                var collection = new SearchResultDomainModelCollection(data.copies, { parse: true });
                collection.invoke('setRepo', repo);
                return collection;
            });
        },
        find: function (id) {
        }
    });
    return SearchRepository;
});