define('modules/work_editor/sections/volume_info/views/volume_info_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'modules/work_editor/sections/volume_info/views/templates/volume_info'
], function (require) {
    var Marionette = require('marionette');
    var VolumeInfoView = Marionette.LayoutView.extend({
        template: require('./templates/volume_info'),
        regions: {
            volumeNumber: '.volume-number',
            publicationInfo: '.publication-info',
            authors: '.authors',
            titles: '.titles',
            otherAuthors: '.other-authors',
            summary: '.summary'
        }
    });
    return VolumeInfoView;
});