define('modules/person_editor/sections/master_editor/master_editor_section', [
    'require',
    'exports',
    'module',
    'underscore',
    'controls/summary/summary',
    'modules/person_editor/sections/master_editor/master_editor_controller',
    'modules/person_editor/sections/master_editor/views/person_master_view',
    'modules/person_editor/sections/master_editor/views/display_name_view',
    'modules/person_editor/sections/master_editor/views/names_list_view',
    'modules/person_editor/sections/master_editor/views/date_location_view'
], function (require) {
    var _ = require('underscore'), SummaryControl = require('controls/summary/summary'), PersonInfoController = require('./master_editor_controller'), PersonMasterEditorView = require('./views/person_master_view'), DisplayNameView = require('./views/display_name_view'), NamesListView = require('./views/names_list_view'), DateLocationView = require('./views/date_location_view');
    function constructSection(options) {
        var opts = _.defaults(options, {});
        if (!opts.person) {
            throw new TypeError('no person supplied to person master section');
        }
        var controller = new PersonInfoController();
        controller.setDomainModel(opts.person);
        var model = controller.getViewModel();
        var personView = new PersonMasterEditorView({ model: model });
        var displayNameView = new DisplayNameView({ model: model.get('displayName') });
        var namesView = new NamesListView({ collection: model.get('names') });
        var birthView = new DateLocationView({ model: model.get('birth') });
        var deathView = new DateLocationView({ model: model.get('death') });
        var summaryView = new SummaryControl.DisplayView({ model: model.get('summary') });
        function attachControls() {
            personView.getRegion('displayName').show(displayNameView);
            personView.getRegion('names').show(namesView);
            personView.getRegion('birth').show(birthView);
            personView.getRegion('death').show(deathView);
            personView.getRegion('summary').show(summaryView);
        }
        personView.on('attach', attachControls);
        return {
            getController: function () {
                return controller;
            },
            getView: function () {
                return personView;
            }
        };
    }
    return { create: constructSection };
});