define('modules/work_editor/sections/work_master/sections/edition_master/entities/edition', [
    'require',
    'exports',
    'module',
    'backbone',
    'controls/summary/summary',
    'modules/work_editor/controls/publication_info/publication_info',
    'modules/work_editor/sections/work_master/sections/edition_master/entities/author_refs'
], function (require) {
    var Backbone = require('backbone'), SummaryControl = require('controls/summary/summary'), PublicationInfoControl = require('modules/work_editor/controls/publication_info/publication_info'), AuthorRefViewModelCollection = require('./author_refs');
    var EditionViewModel = Backbone.Model.extend({
        defaults: function () {
            return {
                editionName: '',
                publicationInfo: new PublicationInfoControl.Model(),
                authors: new AuthorRefViewModelCollection(),
                otherAuthors: new AuthorRefViewModelCollection(),
                title: null,
                summary: new SummaryControl.Model()
            };
        },
        flush: function () {
            this.trigger('flush', this);
        }
    });
    return EditionViewModel;
});