define('modules/catalog_navigation/sections/people_nav_section/people_nav_section', [
    'require',
    'exports',
    'module',
    'underscore',
    'controls/search_form/search_form',
    'modules/catalog_navigation/sections/people_nav_section/views/people_nav_view'
], function (require) {
    var _ = require('underscore'), SearchForm = require('controls/search_form/search_form'), PeopleNavLayoutView = require('./views/people_nav_view');
    function constructSection(options) {
        var opts = _.defaults(options, { limit: 20 });
        if (!opts.channel) {
            throw new TypeError('no channel provided');
        }
        if (!opts.peopleRepo) {
            throw new TypeError('no people repository provided');
        }
        var view = new PeopleNavLayoutView();
        view.on('new:person', function () {
            opts.channel.trigger('new:person');
        });
        function findPeopleByName(name, limit) {
            var findOpts = { limit: limit || opts.limit };
            return opts.peopleRepo.findAllByName(name, findOpts).then(function (people) {
                return _.map(people, function (personResult) {
                    return {
                        title: personResult.formattedName,
                        id: personResult.id
                    };
                });
            });
        }
        function selectPerson(personResult) {
            opts.peopleRepo.find(personResult.get('id')).then(function (person) {
                opts.channel.trigger('edit:person', person);
            });
        }
        var searchForm = new SearchForm({
            searchProvider: findPeopleByName,
            placeholder: 'search by name'
        });
        searchForm.on('result:click', selectPerson);
        function attachControls() {
            view.getRegion('search').show(searchForm.getView());
        }
        view.once('show', attachControls);
        return {
            getView: function () {
                return view;
            }
        };
    }
    return { create: constructSection };
});