define('controls/select/views/select_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var SelectItemView = Marionette.ItemView.extend({
        tagName: 'option',
        template: _.property('label'),
        attributes: function () {
            return { value: this.model.get('value') };
        },
        modelEvents: { change: 'render' }
    });
    var SelectView = Marionette.CollectionView.extend({
        tagName: 'select',
        attributes: function () {
            return _.pick(this, 'name');
        },
        childView: SelectItemView,
        events: {
            change: function () {
                this.trigger('select', this.getValue());
            }
        },
        initialize: function (options) {
            var opts = _.defaults(options, { name: this.cid });
            _.extend(this, _.pick(opts, 'name'));
        },
        getValue: function () {
            var item = this.collection.findWhere({ value: this.$el.val() });
            return item;
        },
        setValue: function (item) {
            this.selectedValue = item ? item.get('value') : null;
            this.$el.val(this.selectedValue);
        },
        onRender: function () {
            if (this.selectedValue) {
                this.$el.val(this.selectedValue);
            }
        }
    });
    return SelectView;
});