define('controls/search_form/views/search_form_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'controls/search_form/views/templates/search_form'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var SearchFormView = Marionette.LayoutView.extend({
        template: require('./templates/search_form'),
        tagName: 'form',
        className: 'search-form',
        templateHelpers: function () {
            return _.pick(this, 'placeholder');
        },
        regions: { 'results': '> .results' },
        events: {
            submit: function (e) {
                e.preventDefault();
                var query = this.$('input[type=search]').val();
                this.trigger('search', query);
            },
            'click button[type=reset]': 'clear'
        },
        clear: function () {
            this.$('input[type=search]').val('');
            this.getRegion('results').empty();
        },
        initialize: function (options) {
            var opts = _.defaults(options, { placeholder: 'search' });
            _.extend(this, _.pick(opts, 'placeholder'));
        }
    });
    return SearchFormView;
});