define('modules/person_editor/sections/info_editor/info_editor_section', [
    'require',
    'exports',
    'module',
    'backbone.wreqr',
    'underscore',
    'controls/summary/summary',
    'modules/person_editor/sections/info_editor/info_editor_controller',
    'modules/person_editor/sections/info_editor/views/person_info_view',
    'modules/person_editor/sections/info_editor/views/name_view',
    'modules/person_editor/sections/info_editor/views/names_view',
    'modules/person_editor/sections/info_editor/views/date_location_view'
], function (require) {
    var Wreqr = require('backbone.wreqr'), _ = require('underscore'), SummaryControl = require('controls/summary/summary'), PersonInfoController = require('./info_editor_controller'), PersonInfoEditorView = require('./views/person_info_view'), NameView = require('./views/name_view'), NamesView = require('./views/names_view'), DateLocationView = require('./views/date_location_view');
    function constructSection(options) {
        var opts = _.defaults(options, { channel: new Wreqr.EventAggregator() });
        if (!opts.person) {
            throw new TypeError('no person supplied to person info editor section');
        }
        var controller = new PersonInfoController();
        controller.setDomainModel(opts.person);
        var model = controller.getViewModel();
        var personView = new PersonInfoEditorView({
            model: model,
            channel: opts.channel
        });
        var displayNameView = new NameView({
            model: model.get('displayName'),
            allowDelete: false,
            channel: opts.channel
        });
        var namesView = new NamesView({
            collection: model.get('names'),
            channel: opts.channel
        });
        var birthView = new DateLocationView({
            model: model.get('birth'),
            channel: opts.channel
        });
        var deathView = new DateLocationView({
            model: model.get('death'),
            channel: opts.channel
        });
        var summaryView = new SummaryControl.EditorView({
            model: model.get('summary'),
            channel: opts.channel
        });
        function attachControls() {
            personView.getRegion('displayName').show(displayNameView);
            personView.getRegion('names').show(namesView);
            personView.getRegion('birth').show(birthView);
            personView.getRegion('death').show(deathView);
            personView.getRegion('summary').show(summaryView);
        }
        personView.on('attach', attachControls);
        return {
            getController: function () {
                return controller;
            },
            getView: function () {
                return personView;
            }
        };
    }
    return { create: constructSection };
});