define('catalogentries-reln/sections/info/views/edition_selector_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'controls/select/select',
    'mixins/editable_field_view_mixin',
    'catalogentries-reln/sections/info/views/templates/edition_selector'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), SelectControl = require('controls/select/select'), EditableFieldViewMixin = require('mixins/editable_field_view_mixin');
    var EditionSelectorView = Marionette.LayoutView.extend({
        template: require('./templates/edition_selector'),
        className: 'editable-field',
        regions: { 'editionSelect': '> .control .edition-select' },
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing'
        },
        modelEvents: {
            'change:edition': function (model, edition) {
                if (edition) {
                    this.$('> .display > .edition').text(edition.get('name'));
                } else {
                    this.$('> .display > .edition').html('<span class="text-muted">[all editions]</span>');
                }
            }
        },
        templateHelpers: function () {
            return {
                title: this.model.get('work').get('title'),
                edition: this.model.get('edition') ? this.model.get('edition').get('name') : null
            };
        },
        initialize: function (opts) {
            if (!opts.channel) {
                throw new TypeError('no channel provided');
            }
            this.listenTo(opts.channel, 'commit', this.stopEditing);
            this.initializeEditableField(opts);
        },
        onShow: function () {
            this._editionSelectControl = new SelectControl({
                empty: '[all]',
                collection: this.model.get('work').get('editions'),
                value: this.model.get('edition'),
                provider: function (edition) {
                    return {
                        value: edition.id,
                        label: edition.get('name')
                    };
                }
            });
            this.getRegion('editionSelect').show(this._editionSelectControl.getView());
        },
        onBlur: function () {
            this.updateViewModel();
        },
        updateViewModel: function () {
            this.model.set('edition', this._editionSelectControl.getValue());
            if (this.model.changedAttributes()) {
                this.model.flush();
            }
        }
    });
    _.defaults(EditionSelectorView.prototype, EditableFieldViewMixin);
    return EditionSelectorView;
});