define('controls/autocomplete/entities/item', [
    'require',
    'exports',
    'module',
    'backbone'
], function (require) {
    var Backbone = require('backbone');
    var AutocompleteItem = Backbone.Model.extend({
        defaults: {
            label: '',
            obj: null
        },
        initialize: function () {
            this._selected = false;
        },
        isSelected: function () {
            return this._selected;
        },
        select: function () {
            if (this._selected) {
                return;
            }
            this._selected = true;
            this.trigger('select');
        },
        deselect: function () {
            if (!this._selected) {
                return;
            }
            this._selected = false;
            this.trigger('deselect');
        }
    });
    return AutocompleteItem;
});