define('controls/search_form/views/error_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var ErrorView = Marionette.ItemView.extend({
        template: function (ctx) {
            var admonition = ctx.title ? '<strong>' + ctx.title + '</strong>: ' : '';
            return admonition + ctx.message;
        },
        className: 'alert alert-danger',
        attributes: { role: 'alert' },
        templateHelpers: function () {
            return _.pick(this, 'title', 'message');
        },
        initialize: function (options) {
            var opts = _.defaults(options, { title: 'Error' });
            if (!opts.message) {
                throw new TypeError('no message provided');
            }
            _.extend(this, _.pick(opts, 'title', 'message'));
        }
    });
    return ErrorView;
});