define('catalogentries-reln/sections/info/entities/relationship', [
    'require',
    'exports',
    'module',
    'backbone',
    'controls/summary/summary',
    'catalogentries-reln/sections/info/entities/work_edition_anchor'
], function (require) {
    var Backbone = require('backbone'), SummaryControl = require('controls/summary/summary'), WorkEditionAnchorViewModel = require('./work_edition_anchor');
    var RelationshipViewModel = Backbone.Model.extend({
        defaults: function () {
            return {
                type: null,
                source: new WorkEditionAnchorViewModel(),
                target: new WorkEditionAnchorViewModel(),
                summary: new SummaryControl.Model()
            };
        },
        flush: function () {
            this.trigger('flush', this);
        }
    });
    return RelationshipViewModel;
});