define('modules/person_editor/person_editor', [
    'require',
    'exports',
    'module',
    'backbone',
    'promise',
    'backbone.wreqr',
    'underscore',
    'notifications/index',
    'modules/person_editor/sections/master_editor/master_editor_section',
    'modules/person_editor/sections/info_editor/info_editor_section',
    'modules/person_editor/views/editor_layout'
], function (require) {
    var Backbone = require('backbone'), Promise = require('promise'), Wreqr = require('backbone.wreqr'), _ = require('underscore'), Notifications = require('notifications/index').getInstance(), MasterEditorSection = require('./sections/master_editor/master_editor_section'), PersonInfoEditorSection = require('./sections/info_editor/info_editor_section'), PersonEditorLayout = require('./views/editor_layout');
    function PersonEditor(options) {
        var opts = _.defaults(options, { channel: new Wreqr.EventAggregator() });
        if (!opts.region) {
            throw new TypeError('No region specified');
        }
        if (!opts.peopleRepo) {
            throw new TypeError('No people repository specified');
        }
        _.extend(this, _.pick(opts, 'channel', 'region', 'peopleRepo'));
        this.listenTo(this.channel, 'new:person', this.create);
        this.listenTo(this.channel, 'edit:person', this._renderPersonEditor);
        this.layout = null;
    }
    _.extend(PersonEditor.prototype, Backbone.Events, {
        edit: function (id) {
            var render = _.bind(this._renderPersonEditor, this);
            this.peopleRepo.find(id).then(render);
        },
        create: function () {
            var render = _.bind(this._renderPersonEditor, this);
            this.peopleRepo.create().then(render);
        },
        remove: function (id) {
            this.peopleRepo.remove(id);
        },
        quickAdd: function () {
        },
        save: function () {
            if (this.person) {
                this.channel.trigger('commit');
                this.person.save().then(function () {
                    Notifications.success({ title: 'Data saved successfully!' });
                }).catch(function () {
                    Notifications.error({ title: 'Oops! Sorry, I was not able to save your data.' });
                });
            }
        },
        getLayout: function () {
            if (!_.isNull(this.layout)) {
                return this.layout;
            }
            this.layout = new PersonEditorLayout();
            this.listenTo(this.layout, 'save', this.save);
            this.listenTo(this.layout, 'delete', function () {
                var _this = this;
                Notifications.confirm({
                    title: 'Delete Person',
                    description: 'Are you sure you want to delete this person?'
                }).then(function (result) {
                    if (result) {
                        _this.person.destroy();
                    }
                });
            });
            this.listenTo(this.layout, 'before:destroy', this._onFocusLost);
            this.listenTo(this.layout, 'destroy', function () {
                this.layout = null;
            });
            return this.layout;
        },
        _renderPersonEditor: function (person) {
            var promise;
            var _this = this;
            if (this.person && person !== this.person && this.person.isDirty()) {
                promise = Notifications.confirm({
                    title: 'Unsaved Changes',
                    description: 'Do you want to save the changes to this person?'
                }).then(function (result) {
                    if (result) {
                        _this.save();
                    }
                    _this.stopListening(_this.person);
                });
            } else {
                promise = Promise.resolve(null);
            }
            promise.then(function () {
                _this.person = person;
                var masterEditorSection = MasterEditorSection.create({ person: person });
                var infoEditorSection = PersonInfoEditorSection.create({
                    channel: _this.channel,
                    person: person
                });
                var layout = _this.getLayout();
                _this.region.show(layout);
                _this.listenTo(_this.person, 'dirty:changed', function (model, dirty) {
                    layout.setDirty(dirty);
                });
                layout.setDirty(_this.person.isDirty());
                _this.listenTo(_this.person, 'destroy', function () {
                    layout.destroy();
                });
                layout.getRegion('master').show(masterEditorSection.getView());
                layout.getRegion('details').show(infoEditorSection.getView());
            });
        },
        _onFocusLost: function () {
            var promise;
            var _this = this;
            if (this.person && this.person.isDirty()) {
                promise = Notifications.confirm({
                    title: 'Unsaved Changes',
                    description: 'Do you want to save the changes to this person?'
                }).then(function (result) {
                    if (result) {
                        _this.save();
                    }
                });
            } else {
                promise = Promise.resolve(null);
            }
            promise.then(function () {
                _this.stopListening(_this.person);
                _this.person = null;
            });
        }
    });
    return PersonEditor;
});