define('modules/work_editor/controls/author_refs_editor/views/author_refs_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'backbone.wreqr',
    'controls/autocomplete/autocomplete',
    'mixins/editable_field_view_mixin',
    'modules/work_editor/controls/author_refs_editor/views/templates/author_ref',
    'modules/work_editor/controls/author_refs_editor/views/templates/author_refs'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), Wreqr = require('backbone.wreqr'), AutocompleteControl = require('controls/autocomplete/autocomplete'), EditableFieldMixin = require('mixins/editable_field_view_mixin');
    var KEY_ENTER = 13;
    var AuthorRefView = Marionette.ItemView.extend({
        template: require('./templates/author_ref'),
        tagName: 'section',
        className: 'author-ref editable-field',
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing',
            'click button.delete': function () {
                this.model.destroy();
            },
            'keyup input': function (e) {
                if (!this._autocompleteJustClosed && e.keyCode === KEY_ENTER) {
                    this.stopEditing();
                }
            }
        },
        modelEvents: {
            'change:lastName': function (model, value) {
                this.$('input[name=lastName]').val(value);
                this.$('.display .name .last').text(value);
                if (value) {
                    this.$('.display .name .no-name').hide();
                } else if (!model.get('firstName')) {
                    this.$('.display .name .no-name').show();
                }
            },
            'change:firstName': function (model, value) {
                this.$('input[name=firstName]').val(value);
                this.$('.display .name .first').text(value);
                if (value) {
                    this.$('.display .name .no-name').hide();
                } else if (!model.get('lastName')) {
                    this.$('.display .name .no-name').show();
                }
            },
            'change:role': function (model, value) {
                this.$('input[name=role]').val(value);
                if (value) {
                    this.$('.display .role').text(value);
                } else {
                    this.$('.display .role').html('<span class="text-muted">[no role]</span>');
                }
            },
            'change:authorId': function (model, value) {
                this.$('input[name=authorId]').val(value);
            },
            'change:authorLabel': function (model, value) {
                this.$('.ref-author-label').text(value || '...');
            }
        },
        templateHelpers: function () {
            return { roleListId: this.roleListId };
        },
        initialize: function (options) {
            var opts = _.defaults(options, {});
            if (!opts.authorSearchProvider) {
                throw new TypeError('no author search provider given to author ref view');
            }
            _.extend(this, _.pick(opts, 'authorSearchProvider', 'roleListId'));
            if (opts.channel) {
                this.listenTo(opts.channel, 'commit', this.stopEditing);
            }
            this.initializeEditableField(options);
            this._changedByAutocomplete = false;
        },
        onFocus: function () {
            this.$('input[name=lastName]').focus();
        },
        onBlur: function () {
            this.updateViewModel();
        },
        onDomRefresh: function () {
            this.autocomplete = new AutocompleteControl({
                input: this.$('input[name=lastName]'),
                target: this.$('.autocomplete'),
                provider: this.authorSearchProvider
            });
            this.autocomplete.on('close', function () {
                this._autocompleteJustClosed = true;
                var _this = this;
                setTimeout(function () {
                    _this._autocompleteJustClosed = false;
                }, 100);
            }, this);
            this.autocomplete.on('select', function (author) {
                this.model.set({
                    firstName: author.get('firstName'),
                    lastName: author.get('lastName'),
                    authorId: author.id,
                    authorLabel: author.get('formattedName')
                });
                if (this.model.changedAttributes()) {
                    this._changedByAutocomplete = true;
                }
            }, this);
        },
        updateViewModel: function () {
            this.model.set({
                firstName: this.$('input[name=firstName]').val(),
                lastName: this.$('input[name=lastName]').val(),
                role: this.$('input[name=role]').val()
            });
            if (this.model.changedAttributes() || this._changedByAutocomplete) {
                this.model.flush();
            }
            this._changedByAutocomplete = false;
        }
    });
    _.defaults(AuthorRefView.prototype, EditableFieldMixin);
    var NoAuthorRefsView = Marionette.ItemView.extend({
        template: function () {
            return '[no author references added yet]';
        },
        tagName: 'span',
        className: 'text-muted',
        triggers: { dblclick: 'add:ref' }
    });
    var AuthorRefsView = Marionette.CompositeView.extend({
        template: require('./templates/author_refs'),
        childView: AuthorRefView,
        emptyView: NoAuthorRefsView,
        childViewContainer: '.author-refs',
        childViewOptions: function () {
            return _.pick(this, 'channel', 'roleListId', 'authorSearchProvider');
        },
        events: {
            'click button.add-author-ref': function () {
                this.collection.add({});
            }
        },
        templateHelpers: function () {
            return {
                title: this.title,
                roleListId: this.roleListId,
                roles: this.roles
            };
        },
        initialize: function (options) {
            var opts = _.defaults(options, {
                channel: new Wreqr.EventAggregator(),
                title: 'Authors',
                roleListId: 'authorRefsList-' + this.cid,
                roles: [
                    'Author',
                    'Editor',
                    'Translator',
                    'Other'
                ]
            });
            if (!opts.authorSearchProvider) {
                throw new TypeError('no author search provider given to author references view');
            }
            _.extend(this, _.pick(opts, 'channel', 'title', 'roleListId', 'roles', 'authorSearchProvider'));
            this.on('childview:add:ref', function () {
                this.collection.add({});
            }, this);
            this.rendered = false;
        },
        onAddChild: function (childView) {
            if (this.rendered) {
                childView.startEditing();
            }
        },
        onShow: function () {
            this.rendered = true;
        }
    });
    return AuthorRefsView;
});