define('catalogentries-digitalcopies/entities/search_result', [
    'require',
    'exports',
    'module',
    'backbone',
    'moment'
], function (require) {
    var Backbone = require('backbone'), moment = require('moment');
    var SearchResultDomainModel = Backbone.Model.extend({
        idAttribute: 'identifier',
        defaults: {
            title: '',
            description: null,
            copyProvider: '',
            sourceSummary: '',
            rights: '',
            publicationDate: moment()
        },
        parse: function (attrs) {
            if (attrs.publicationDate) {
                attrs.publicationDate = moment(attrs.publicationDate);
            }
            return attrs;
        },
        setRepo: function (repo) {
            this._repo = repo;
        }
    });
    return SearchResultDomainModel;
});