define('modules/work_editor/sections/volume_info/volume_info_controller', [
    'require',
    'exports',
    'module',
    'underscore',
    'controls/summary/summary',
    'modules/work_editor/controls/author_refs_editor/author_refs_editor',
    'modules/work_editor/controls/publication_info/publication_info',
    'modules/work_editor/controls/titles_editor/titles_editor',
    'modules/work_editor/sections/volume_info/entities/author',
    'modules/work_editor/sections/volume_info/entities/volume'
], function (require) {
    var _ = require('underscore'), SummaryControl = require('controls/summary/summary'), AuthorRefsEditorControl = require('modules/work_editor/controls/author_refs_editor/author_refs_editor'), PublicationInfoControl = require('modules/work_editor/controls/publication_info/publication_info'), TitleEditorControl = require('modules/work_editor/controls/titles_editor/titles_editor'), AuthorViewModel = require('./entities/author'), VolumeViewModel = require('./entities/volume');
    function bindVolumeViewModel(domainModel, viewModel) {
        viewModel.on('flush', function () {
            domainModel.set({ volumeNumber: viewModel.get('volumeNumber') });
        });
        viewModel.get('summary').on('flush', function (summaryViewModel) {
            domainModel.set({ summary: summaryViewModel.get('summary') });
        });
        domainModel.on('change:volumeNumber', function (model, value) {
            viewModel.set('volumeNumber', value);
        });
        domainModel.on('change:summary', function (model, value) {
            viewModel.get('summary').set('summary', value);
        });
    }
    function bindPublicationInfoViewModel(domainModel, viewModel) {
        viewModel.on('flush', function () {
            domainModel.set({
                publisher: viewModel.get('publisher'),
                place: viewModel.get('place')
            });
            domainModel.get('date').set({
                description: viewModel.get('dateDescription'),
                calendar: viewModel.get('dateValue')
            });
        });
        domainModel.on('change:publisher', function (model, value) {
            viewModel.set('publisher', value);
        });
        domainModel.on('change:place', function (model, value) {
            viewModel.set('place', value);
        });
        domainModel.get('date').on('change:calendar', function (model, value) {
            viewModel.set('dateValue', value);
        });
        domainModel.get('date').on('change:description', function (model, value) {
            viewModel.set('dateDescription', value);
        });
        domainModel.on('destroy', function () {
            viewModel.destroy();
        });
    }
    function createPublicationInfoViewModel(domainModel) {
        var viewModel = new PublicationInfoControl.Model({
            publisher: domainModel.get('publisher'),
            place: domainModel.get('place'),
            dateValue: domainModel.get('date').get('calendar'),
            dateDescription: domainModel.get('date').get('description')
        });
        bindPublicationInfoViewModel(domainModel, viewModel);
        return viewModel;
    }
    function bindAuthorRefViewModel(domainModel, viewModel) {
        viewModel.on('flush', function () {
            domainModel.set({
                role: viewModel.get('role'),
                firstName: viewModel.get('firstName'),
                lastName: viewModel.get('lastName'),
                authorId: viewModel.get('authorId')
            });
        });
        viewModel.on('destroy', function (options) {
            if (viewModel === options.viewModel) {
                return;
            }
            _.extend(options, { viewModel: viewModel });
            domainModel.destroy(options);
        });
        domainModel.on('change:role', function (model, value) {
            viewModel.set('role', value);
        });
        domainModel.on('change:firstName', function (model, value) {
            viewModel.set('firstName', value);
        });
        domainModel.on('change:lastName', function (model, value) {
            viewModel.set('lastName', value);
        });
        domainModel.on('change:authorId', function (model, value) {
            viewModel.set('authorId', value);
        });
        domainModel.on('destroy', function (options) {
            if (domainModel === options.domainModel) {
                return;
            }
            _.extend(options, { domainModel: domainModel });
            viewModel.destroy(options);
        });
    }
    function createAuthorRefViewModel(domainModel, peopleRepo) {
        var authorId = domainModel.get('authorId');
        var viewModel = new AuthorRefsEditorControl.Model({
            role: domainModel.get('role'),
            firstName: domainModel.get('firstName'),
            lastName: domainModel.get('lastName'),
            authorId: authorId
        });
        var isAuthorLabelChanged = false;
        function resolveAuthor(author) {
            if (isAuthorLabelChanged) {
                return;
            }
            viewModel.set('authorLabel', author.getFormattedName());
        }
        function rejectAuthor() {
            if (isAuthorLabelChanged) {
                return;
            }
            viewModel.set('authorLabel', null);
            console.error('invalid author reference id');
        }
        if (!_.isNull(authorId)) {
            viewModel.once('change:authorLabel', function () {
                isAuthorLabelChanged = true;
            });
            peopleRepo.find(authorId).then(resolveAuthor, rejectAuthor);
        }
        bindAuthorRefViewModel(domainModel, viewModel);
        return viewModel;
    }
    function createAuthorRefViewModelCollection(domainCollection, peopleRepo) {
        var viewCollection = new AuthorRefsEditorControl.Collection(domainCollection.map(function (authorRefDomainModel) {
            return createAuthorRefViewModel(authorRefDomainModel, peopleRepo);
        }));
        function viewToDomain(authorRefViewModel) {
            return {
                role: authorRefViewModel.get('role'),
                firstName: authorRefViewModel.get('firstName'),
                lastName: authorRefViewModel.get('lastName'),
                authorId: authorRefViewModel.get('authorId')
            };
        }
        viewCollection.on('add', function (viewModel, collection, options) {
            if (domainCollection.contains(options.domainModel)) {
                return;
            }
            _.extend(options, { viewModel: viewModel });
            var domainModel = domainCollection.add(viewToDomain(viewModel), options);
            bindAuthorRefViewModel(domainModel, viewModel);
        });
        domainCollection.on('add', function (domainModel, collection, options) {
            if (viewCollection.contains(options.viewModel)) {
                return;
            }
            _.extend(options, { domainModel: domainModel });
            var viewModel = createAuthorRefViewModel(domainModel, peopleRepo);
            viewCollection.add(viewModel, options);
        });
        return viewCollection;
    }
    function bindTitleViewModel(domainModel, viewModel) {
        viewModel.on('flush', function () {
            domainModel.set({
                type: viewModel.get('type'),
                title: viewModel.get('title'),
                subtitle: viewModel.get('subtitle')
            });
        });
        viewModel.on('destroy', function (options) {
            if (viewModel === options.viewModel) {
                return;
            }
            _.extend(options, { viewModel: viewModel });
            domainModel.destroy(options);
        });
        domainModel.on('change:type', function (model, value) {
            viewModel.set('type', value);
        });
        domainModel.on('change:title', function (model, value) {
            viewModel.set('title', value);
        });
        domainModel.on('change:subtitle', function (model, value) {
            viewModel.set('subtitle', value);
        });
        domainModel.on('destroy', function (options) {
            if (domainModel === options.domainModel) {
                return;
            }
            _.extend(options, { domainModel: domainModel });
            viewModel.destroy(options);
        });
    }
    function createTitleViewModel(domainModel) {
        var viewModel = new TitleEditorControl.Model({
            type: domainModel.get('type'),
            title: domainModel.get('title'),
            subtitle: domainModel.get('subtitle')
        });
        bindTitleViewModel(domainModel, viewModel);
        return viewModel;
    }
    function createTitleViewModelCollection(domainCollection) {
        var viewCollection = new TitleEditorControl.Collection(domainCollection.map(createTitleViewModel));
        function viewToDomain(titleViewModel) {
            return {
                type: titleViewModel.get('type'),
                title: titleViewModel.get('title'),
                subtitle: titleViewModel.get('subtitle')
            };
        }
        viewCollection.on('add', function (viewModel, collection, options) {
            if (domainCollection.contains(options.domainModel)) {
                return;
            }
            _.extend(options, { viewModel: viewModel });
            var domainModel = domainCollection.add(viewToDomain(viewModel), options);
            bindTitleViewModel(domainModel, viewModel);
        });
        domainCollection.on('add', function (domainModel, collection, options) {
            if (viewCollection.contains(options.viewModel)) {
                return;
            }
            _.extend(options, { domainModel: domainModel });
            var viewModel = createTitleViewModel(domainModel);
            viewCollection.add(viewModel, options);
        });
        return viewCollection;
    }
    function createVolumeViewModel(domainModel, peopleRepo) {
        var summaryViewModel = new SummaryControl.Model({ summary: domainModel.get('summary') });
        var volumeViewModel = new VolumeViewModel({
            volumeNumber: domainModel.get('volumeNumber'),
            publicationInfo: createPublicationInfoViewModel(domainModel.get('publicationInfo')),
            authors: createAuthorRefViewModelCollection(domainModel.get('authors'), peopleRepo),
            titles: createTitleViewModelCollection(domainModel.get('titles')),
            otherAuthors: createAuthorRefViewModelCollection(domainModel.get('otherAuthors'), peopleRepo),
            summary: summaryViewModel
        });
        bindVolumeViewModel(domainModel, volumeViewModel);
        return volumeViewModel;
    }
    function VolumeInfoController(options) {
        var opts = _.defaults(options, {});
        if (!opts.peopleRepo) {
            throw new TypeError('no people repository provided to volume info controller');
        }
        _.extend(this, _.pick(opts, 'peopleRepo'));
    }
    _.extend(VolumeInfoController.prototype, {
        setDomainModel: function (domainModel) {
            this.domainModel = domainModel;
            this.viewModel = null;
        },
        getViewModel: function () {
            if (!this.viewModel) {
                this.viewModel = createVolumeViewModel(this.domainModel, this.peopleRepo);
            }
            return this.viewModel;
        },
        createAuthorResultViewModel: function (result) {
            var n = result.name;
            var firstName = _([
                n.givenName,
                n.middleName,
                n.suffix
            ]).filter(_.identity).join(' ');
            var lastName = n.familyName;
            return new AuthorViewModel({
                id: result.id,
                firstName: firstName.trim(),
                lastName: lastName.trim(),
                formattedName: result.formattedName
            });
        }
    });
    return VolumeInfoController;
});