define('mixins/dirtyable_model_mixin', [
    'require',
    'exports',
    'module',
    'underscore'
], function (require) {
    var _ = require('underscore');
    var DirtyableModelMixin = {
        initializeDirtyable: function () {
            this.on('change', this.setDirty, this);
            this.on('sync', _.partial(this.setDirty, false), this);
            this._dirty = false;
        },
        setDirty: function (dirty) {
            this._dirty = _.isUndefined(dirty) ? true : dirty;
            this.trigger('dirty:changed', this, this._dirty);
        },
        isDirty: function () {
            return this._dirty;
        }
    };
    return DirtyableModelMixin;
});