define('catalogentries-reln/sections/master/views/relationships_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'notifications/index',
    'catalogentries-reln/sections/master/views/templates/relationship',
    'catalogentries-reln/sections/master/views/templates/type_group',
    'catalogentries-reln/sections/master/views/templates/relationships'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), Notifications = require('notifications/index').getInstance();
    var AuthorRefView = Marionette.ItemView.extend({
        template: _.property('lastName'),
        tagName: 'a',
        attributes: function () {
            return { href: '#' };
        },
        modelEvents: { change: 'render' }
    });
    var NoAuthorRefView = Marionette.ItemView.extend({
        template: _.constant('[authors]'),
        tagName: 'span',
        className: 'text-muted'
    });
    var AuthorRefsView = Marionette.CollectionView.extend({
        tagName: 'span',
        className: 'authors',
        childView: AuthorRefView,
        emptyView: NoAuthorRefView
    });
    var RelationshipView = Marionette.LayoutView.extend({
        template: require('./templates/relationship'),
        tagName: 'li',
        className: 'relationship',
        regions: { authors: '> .citation > .authors' },
        events: {
            'click button.delete-relationship': 'delete',
            'click button.edit-relationship': 'edit'
        },
        onShow: function () {
            var authorsView = new AuthorRefsView({ collection: this.model.get('authors') });
            this.getRegion('authors').show(authorsView);
        },
        delete: function () {
            var _this = this;
            Notifications.confirm({
                title: 'Delete Relationship',
                description: 'Are you sure you want to delete this relationship?'
            }).then(function (result) {
                if (result) {
                    _this.model.destroy();
                }
            });
        },
        edit: function () {
            this.model.edit();
        }
    });
    var NoRelationshipsView = Marionette.ItemView.extend({
        tagName: 'span',
        className: 'text-muted',
        template: _.constant('[no relationships]'),
        triggers: { dblclick: 'add' }
    });
    var RelationshipTypeGroupView = Marionette.CompositeView.extend({
        template: require('./templates/type_group'),
        childView: RelationshipView,
        emptyView: NoRelationshipsView,
        childViewContainer: '> .relationship-type-group'
    });
    var RelationshipsView = Marionette.CompositeView.extend({
        template: require('./templates/relationships'),
        childView: RelationshipTypeGroupView,
        emptyView: NoRelationshipsView,
        childViewContainer: '> .relationships',
        triggers: { 'click button.add-relationship': 'add' },
        initialize: function () {
            this.on('childview:add', function () {
                this.trigger('add');
            }, this);
        },
        buildChildView: function (child, ChildViewClass, childViewOptions) {
            var options = _.extend({ model: child }, childViewOptions);
            if (child.has('relationships')) {
                options.collection = child.get('relationships');
            }
            return new ChildViewClass(options);
        }
    });
    return RelationshipsView;
});