define('modules/work_editor/sections/work_master/sections/edition_master/sections/volume_master/volume_controller', [
    'require',
    'exports',
    'module',
    'underscore',
    'modules/work_editor/controls/publication_info/publication_info',
    'controls/summary/summary',
    'modules/work_editor/sections/work_master/sections/edition_master/sections/volume_master/entities/volume',
    'modules/work_editor/sections/work_master/sections/edition_master/sections/volume_master/entities/author_refs',
    'modules/work_editor/sections/work_master/sections/edition_master/sections/volume_master/entities/author_ref'
], function (require) {
    var _ = require('underscore'), PublicationInfoControl = require('modules/work_editor/controls/publication_info/publication_info'), SummaryControl = require('controls/summary/summary'), VolumeViewModel = require('./entities/volume'), AuthorRefViewModelCollection = require('./entities/author_refs'), AuthorRefViewModel = require('./entities/author_ref');
    function findCanonicalTitle(volumeDomainModel) {
        var canonicalTitleDomainModel = volumeDomainModel.getCanonicalTitle();
        var canonicalTitle = null;
        if (!_.isNull(canonicalTitleDomainModel)) {
            canonicalTitle = canonicalTitleDomainModel.getFullTitle();
        }
        return canonicalTitle;
    }
    function bindPublicationInfoViewModel(domainModel, viewModel) {
        domainModel.on('change:publisher', function (model, value) {
            viewModel.set('publisher', value);
        });
        domainModel.on('change:place', function (model, value) {
            viewModel.set('place', value);
        });
        domainModel.get('date').on('change:calendar', function (model, value) {
            viewModel.set('dateValue', value);
        });
        domainModel.get('date').on('change:description', function (model, value) {
            viewModel.set('dateDescription', value);
        });
        domainModel.on('destroy', function () {
            viewModel.destroy();
        });
    }
    function createPublicationInfoViewModel(domainModel) {
        var viewModel = new PublicationInfoControl.Model({
            publisher: domainModel.get('publisher'),
            place: domainModel.get('place'),
            dateValue: domainModel.get('date').get('calendar'),
            dateDescription: domainModel.get('date').get('description')
        });
        bindPublicationInfoViewModel(domainModel, viewModel);
        return viewModel;
    }
    function bindAuthorRefViewModel(domainModel, viewModel) {
        domainModel.on('change:role', function (model, value) {
            viewModel.set('role', value);
        });
        domainModel.on('change:firstName', function (model, value) {
            viewModel.set('firstName', value);
        });
        domainModel.on('change:lastName', function (model, value) {
            viewModel.set('lastName', value);
        });
        domainModel.on('change:authorId', function (model, value) {
            viewModel.set('authorId', value);
        });
        domainModel.on('destroy', function () {
            viewModel.destroy();
        });
    }
    function createAuthorRefViewModel(domainModel) {
        var viewModel = new AuthorRefViewModel({
            role: domainModel.get('role'),
            firstName: domainModel.get('firstName'),
            lastName: domainModel.get('lastName'),
            authorId: domainModel.get('authorId')
        });
        bindAuthorRefViewModel(domainModel, viewModel);
        return viewModel;
    }
    function bindAuthorRefViewModelCollection(domainCollection, viewCollection) {
        domainCollection.on('add', function (domainModel) {
            viewCollection.add(createAuthorRefViewModel(domainModel));
        });
    }
    function createAuthorRefViewModelCollection(domainCollection) {
        var viewCollection = new AuthorRefViewModelCollection(domainCollection.map(createAuthorRefViewModel));
        bindAuthorRefViewModelCollection(domainCollection, viewCollection);
        return viewCollection;
    }
    function bindVolumeViewModel(domainModel, viewModel) {
        domainModel.on('change:volumeNumber', function (model, value) {
            viewModel.set('volumeNumber', value);
        });
        domainModel.on('change:summary', function (model, value) {
            viewModel.get('summary').set('summary', value);
        });
        domainModel.get('titles').on('add remove change', function () {
            viewModel.set('title', findCanonicalTitle(domainModel));
        });
        domainModel.on('destroy', function (options) {
            if (domainModel === options.domainModel) {
                return;
            }
            _.extend(options, { domainModel: domainModel });
            viewModel.destroy(options);
        });
        viewModel.on('destroy', function (options) {
            if (viewModel === options.viewModel) {
                return;
            }
            _.extend(options, { viewModel: viewModel });
            domainModel.destroy(options);
        });
    }
    function createVolumeViewModel(domainModel) {
        var summaryViewModel = new SummaryControl.Model({ summary: domainModel.get('summary') });
        var volumeViewModel = new VolumeViewModel({
            volumeNumber: domainModel.get('volumeNumber'),
            publicationInfo: createPublicationInfoViewModel(domainModel.get('publicationInfo')),
            authors: createAuthorRefViewModelCollection(domainModel.get('authors')),
            title: findCanonicalTitle(domainModel),
            otherAuthors: createAuthorRefViewModelCollection(domainModel.get('otherAuthors')),
            summary: summaryViewModel
        });
        bindVolumeViewModel(domainModel, volumeViewModel);
        return volumeViewModel;
    }
    function VolumesController(opts) {
        if (!opts.volume) {
            throw new TypeError('no volume domain model provided');
        }
        this.domainModel = opts.volume;
        this.viewModel = null;
    }
    _.extend(VolumesController.prototype, {
        getViewModel: function () {
            if (!this.viewModel) {
                this.viewModel = createVolumeViewModel(this.domainModel);
            }
            return this.viewModel;
        }
    });
    return VolumesController;
});