define('modules/work_editor/sections/work_master/sections/edition_master/edition_section', [
    'require',
    'exports',
    'module',
    'underscore',
    'notifications/index',
    'modules/work_editor/controls/publication_info/publication_info',
    'controls/summary/summary',
    'modules/work_editor/sections/work_master/sections/edition_master/edition_controller',
    'modules/work_editor/sections/work_master/sections/edition_master/views/edition_view',
    'modules/work_editor/sections/work_master/sections/edition_master/views/author_refs_view',
    'modules/work_editor/sections/work_master/sections/edition_master/views/volumes_domain_view',
    'modules/work_editor/sections/work_master/sections/edition_master/sections/volume_master/volume_section'
], function (require) {
    var _ = require('underscore'), Notifications = require('notifications/index'), PublicationInfoControl = require('modules/work_editor/controls/publication_info/publication_info'), SummaryControl = require('controls/summary/summary'), EditionMasterController = require('./edition_controller'), EditionView = require('./views/edition_view'), AuthorRefsView = require('./views/author_refs_view'), VolumesDomainView = require('./views/volumes_domain_view'), VolumeSection = require('./sections/volume_master/volume_section');
    function constructSection(options) {
        var opts = _.defaults(options, { viewParentId: null });
        if (!opts.edition) {
            throw new TypeError('no edition model provided');
        }
        if (!opts.channel) {
            throw new TypeError('no channel provided');
        }
        var controller = new EditionMasterController({ edition: opts.edition });
        var viewModel = controller.getViewModel();
        var editionView = new EditionView({
            model: viewModel,
            parentId: opts.viewParentId
        });
        var authorsView = new AuthorRefsView({ collection: viewModel.get('authors') });
        var otherAuthorsView = new AuthorRefsView({
            collection: viewModel.get('otherAuthors'),
            hideEmptyView: true
        });
        var pubInfoView = new PublicationInfoControl.DisplayView({ model: viewModel.get('publicationInfo') });
        var summaryView = new SummaryControl.DisplayView({ model: viewModel.get('summary') });
        var volumesView = new VolumesDomainView({
            collection: opts.edition.get('volumes'),
            sectionFactory: VolumeSection
        });
        function editVolume(volume) {
            opts.channel.trigger('edit:volume', volume);
        }
        volumesView.on('add', function () {
            opts.edition.createVolume().then(editVolume);
        });
        volumesView.on('edit', editVolume);
        volumesView.on('link-copies', function (volume) {
            if (volume.isNew()) {
                Notifications.info('Digital copies may only be linked with a volume that has already been persisted.');
                return;
            }
            opts.channel.trigger('edit:copies', volume.getUri());
        });
        function attachControls() {
            editionView.getRegion('authors').show(authorsView);
            editionView.getRegion('otherAuthors').show(otherAuthorsView);
            editionView.getRegion('publicationInfo').show(pubInfoView);
            editionView.getRegion('summary').show(summaryView);
            editionView.getRegion('volumes').show(volumesView);
        }
        editionView.once('show', attachControls);
        return {
            getController: function () {
                return controller;
            },
            getView: function () {
                return editionView;
            }
        };
    }
    return { create: constructSection };
});