define('controls/summary/views/editor_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'quill',
    'underscore',
    'mixins/editable_field_view_mixin',
    'controls/summary/views/templates/summary'
], function (require) {
    var Marionette = require('marionette'), Quill = require('quill'), _ = require('underscore'), EditableFieldMixin = require('mixins/editable_field_view_mixin');
    var KEY_ENTER = 13;
    var SummaryView = Marionette.ItemView.extend({
        template: require('./templates/summary'),
        tagName: 'section',
        className: 'summary editable-field',
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing',
            'keydown .ql-editor': function (e) {
                if (e.keyCode === KEY_ENTER && (e.ctrlKey || e.shiftKey || e.metaKey)) {
                    this.stopEditing();
                }
            }
        },
        modelEvents: {
            'change:summary': function (model, value) {
                var effectiveDisplayValue = model.getDisplayValue();
                this.$('.display .summary').html(effectiveDisplayValue || '<span class="text-muted">[double-click to edit]</span>');
                this.quill.setHTML(value);
            }
        },
        templateHelpers: function () {
            return { display: this.model.getDisplayValue() };
        },
        initialize: function (options) {
            if (options.channel) {
                this.listenTo(options.channel, 'commit', this.stopEditing);
            }
            this.initializeEditableField(options);
        },
        onAttach: function () {
            this.quill = new Quill(this.$('.quill .editor').get(0), {
                theme: 'snow',
                formats: [
                    'bold',
                    'italic',
                    'underline',
                    'strike',
                    'list',
                    'bullet'
                ]
            });
            this.quill.addModule('toolbar', { container: this.$('.quill .toolbar').get(0) });
        },
        onFocus: function () {
            this.quill.focus();
        },
        onBlur: function () {
            this.updateViewModel();
        },
        updateViewModel: function () {
            this.model.set('summary', this.quill.getHTML());
            if (this.model.changedAttributes()) {
                this.model.flush();
            }
        }
    });
    _.defaults(SummaryView.prototype, EditableFieldMixin);
    return SummaryView;
});