define('modules/work_editor/sections/work_master/entities/work', [
    'require',
    'exports',
    'module',
    'backbone',
    'controls/summary/summary',
    'modules/work_editor/sections/work_master/entities/author_refs'
], function (require) {
    var Backbone = require('backbone'), SummaryControl = require('controls/summary/summary'), AuthorRefViewModelCollection = require('./author_refs');
    var WorkViewModel = Backbone.Model.extend({
        defaults: function () {
            return {
                authors: new AuthorRefViewModelCollection(),
                otherAuthors: new AuthorRefViewModelCollection(),
                title: null,
                summary: new SummaryControl.Model()
            };
        }
    });
    return WorkViewModel;
});