define('catalogentries-reln/sections/info/entities/work_edition_anchor', [
    'require',
    'exports',
    'module',
    'backbone',
    'catalogentries-reln/sections/info/entities/work'
], function (require) {
    var Backbone = require('backbone'), WorkViewModel = require('./work');
    var WorkEditionAnchorViewModel = Backbone.Model.extend({
        defaults: function () {
            return {
                work: new WorkViewModel(),
                edition: null
            };
        },
        getUri: function () {
            var work = this.get('work'), edition = this.get('edition');
            return edition ? edition.id : work ? work.id : null;
        },
        flush: function () {
            this.trigger('flush', this);
        }
    });
    return WorkEditionAnchorViewModel;
});