define('modules/person_editor/sections/info_editor/views/date_location_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'moment',
    'underscore',
    'controls/moment.infer',
    'mixins/editable_field_view_mixin',
    'modules/person_editor/sections/info_editor/views/templates/date_location'
], function (require) {
    var Marionette = require('marionette'), moment = require('moment'), _ = require('underscore'), inferMoment = require('controls/moment.infer'), EditableFieldMixin = require('mixins/editable_field_view_mixin');
    var KEY_ENTER = 13;
    var DateLocationView = Marionette.ItemView.extend({
        template: require('./templates/date_location'),
        tagName: 'section',
        className: 'date-location editable-field',
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing',
            'keypress input': function (e) {
                if (e.keyCode === KEY_ENTER) {
                    e.preventDefault();
                    this.stopEditing();
                }
            },
            'change input[name=dateDisplay]': 'parseDateValue'
        },
        modelEvents: {
            'change:dateDisplay': function (model, value) {
                this.$('input[name=dateDisplay]').val(value);
                this.$('.display .date').text(value);
            },
            'change:dateValue': function (model, value) {
                this.$('input[name=dateValue]').val(value && value.isValid() ? value.format('YYYY-MM-DD') : '');
                this.$('.display .date').attr('datetime', value && value.isValid() ? value.format() : '');
            },
            'change:location': function (model, value) {
                this.$('input[name=location]').val(value);
                this.$('.display .location').text(value);
            }
        },
        initialize: function (options) {
            if (options.channel) {
                this.listenTo(options.channel, 'commit', this.stopEditing);
            }
            this.initializeEditableField(options);
        },
        onBlur: function () {
            this.updateViewModel();
        },
        updateViewModel: function () {
            var dateValue = this.$('input[name=dateValue]').val();
            this.model.set({
                dateDisplay: this.$('input[name=dateDisplay]').val(),
                dateValue: moment(dateValue.trim() ? moment(dateValue.trim(), 'YYYY-MM-DD') : null),
                location: this.$('input[name=location]').val()
            });
            if (this.model.changedAttributes()) {
                this.model.flush();
            }
        },
        parseDateValue: function () {
            var $displayInput = this.$('input[name=dateDisplay]');
            var $valueInput = this.$('input[name=dateValue]');
            var dateValue = moment($valueInput.val(), 'YYYY-MM-DD');
            if (dateValue.isValid()) {
                return;
            }
            var parsedDateValue = inferMoment($displayInput.val());
            if (parsedDateValue.isValid()) {
                this.model.set('dateValue', parsedDateValue);
            }
        }
    });
    _.defaults(DateLocationView.prototype, EditableFieldMixin);
    return DateLocationView;
});