define('notifications/display_chain/default_message_display_handler', [
    'require',
    'exports',
    'module',
    'underscore',
    'notifications/display_chain/message_display_chain_mixin'
], function (require) {
    var _ = require('underscore'), MessageDisplayChainMixin = require('./message_display_chain_mixin');
    function DefaultMessageDisplayHandler(opts) {
        opts = opts || {};
        if (!opts.collection) {
            throw new TypeError('no message collection provided');
        }
        _.extend(this, _.pick(opts, 'collection'));
    }
    _.extend(DefaultMessageDisplayHandler.prototype, MessageDisplayChainMixin, {
        handlesType: _.constant(true),
        display: function (model) {
            this.collection.add(model);
        }
    });
    return DefaultMessageDisplayHandler;
});