define('catalogentries-reln/entities/type', [
    'require',
    'exports',
    'module',
    'catalogentries-reln/entities/base'
], function (require) {
    var BaseModel = require('./base');
    var TypeDomainModel = BaseModel.extend({
        idAttribute: 'identifier',
        defaults: {
            title: '',
            reverseTitle: '',
            description: '',
            isDirected: false
        },
        getTitle: function (reverse) {
            return this.get(reverse && this.get('isDirected') ? 'reverseTitle' : 'title');
        }
    });
    return TypeDomainModel;
});