define('catalogentries-reln/relationship_repository', [
    'require',
    'exports',
    'module',
    'promise',
    'underscore',
    'catalogentries-reln/entities/relationship',
    'catalogentries-reln/entities/relationships',
    'catalogentries-reln/entities/types'
], function (require) {
    var Promise = require('promise'), _ = require('underscore'), RelationshipDomainModel = require('./entities/relationship'), RelationshipDomainModelCollection = require('./entities/relationships'), TypeDomainModelCollection = require('./entities/types');
    function RelationshipRepository(options) {
        var opts = _.defaults(options, {});
        if (!opts.apiEndpoint) {
            throw new TypeError('remote API is not defined');
        }
        if (!opts.restClient) {
            throw new TypeError('REST client is not defined');
        }
        _.extend(this, _.pick(opts, 'apiEndpoint', 'restClient'));
    }
    _.extend(RelationshipRepository.prototype, {
        findAllByUri: function (uri) {
            var repo = this;
            return this.restClient.get(this.apiEndpoint, {
                entity: uri,
                direction: 'any'
            }).then(function (data) {
                var collection = new RelationshipDomainModelCollection(data, { parse: true });
                collection.each(function (relationship) {
                    relationship.setRepo(repo);
                });
                return collection;
            });
        },
        create: function (sourceUri) {
            if (!sourceUri) {
                throw new TypeError('no source URI provided');
            }
            var relationship = new RelationshipDomainModel({
                relatedEntities: [{ entryUris: [sourceUri] }],
                typeId: 'uk.ac.ox.bodleian.sda.relationships.influence'
            }, { parse: true });
            relationship.setRepo(this);
            return Promise.resolve(relationship);
        },
        saveRelationship: function (relationship) {
            return this.restClient.send({
                method: relationship.isNew() ? 'post' : 'put',
                url: this.apiEndpoint + (relationship.isNew() ? '' : '/' + relationship.id),
                data: relationship.toJSON()
            }).then(_.property('id'));
        },
        deleteRelationship: function (relationship) {
            if (relationship.isNew()) {
                return Promise.resolve(false);
            }
            return this.restClient.delete(this.apiEndpoint + '/' + relationship.id);
        },
        findTypes: function () {
            return this.restClient.get(this.apiEndpoint + '/types').then(function (data) {
                return new TypeDomainModelCollection(data);
            });
        }
    });
    return RelationshipRepository;
});