define('services/repos/entities/base', [
    'require',
    'exports',
    'module',
    'backbone',
    'underscore'
], function (require) {
    var Backbone = require('backbone'), _ = require('underscore');
    var EVENT_MAP = {
        reset: ['change'],
        add: ['change'],
        remove: ['change'],
        destroy: ['change']
    };
    function callConstructor(ctor) {
        var Factory = ctor.bind.apply(ctor, arguments);
        return new Factory();
    }
    var BaseModel = Backbone.Model.extend({
        sync: _.noop,
        relations: {},
        constructor: function () {
            this._initializeRelations();
            Backbone.Model.prototype.constructor.apply(this, arguments);
            this._bindRelationEvents();
        },
        _initializeRelations: function () {
            this._relations = {};
            _.each(_.result(this, 'relations'), function (opts, field) {
                if (_.has(opts, 'class') && !_.has(opts, 'factory')) {
                    opts.factory = callConstructor.bind(null, opts.class);
                }
                if (!_.has(opts, 'factory')) {
                    throw new Error('Invalid definition of related field {' + field + '}: missing factory or class');
                }
                this._relations[field] = opts;
            }, this);
        },
        _bindRelationEvents: function () {
            _.each(this._relations, function (opts, field) {
                this.listenTo(this.get(field), 'all', this._buildRelationEventHandler(field));
            }, this);
        },
        _buildRelationEventHandler: function (field) {
            return function (eventName) {
                if (eventName.indexOf(':') !== -1) {
                    return;
                }
                var args = _.tail(arguments);
                args.unshift(this);
                this.trigger.apply(this, [eventName + ':' + field].concat(args));
                if (_.has(EVENT_MAP, eventName)) {
                    _.each(EVENT_MAP[eventName], function (mappedEventName) {
                        this.trigger.apply(this, [mappedEventName].concat(args));
                        this.trigger.apply(this, [mappedEventName + ':' + field].concat(args));
                    }, this);
                } else {
                    this.trigger.apply(this, [eventName].concat(args));
                }
            };
        },
        parse: function (data) {
            _.each(this._relations, function (opts, field) {
                if (_.has(data, field)) {
                    data[field] = opts.factory(data[field], { parse: true });
                }
            });
            return data;
        },
        toJSON: function (options) {
            options = options || {};
            var opts = _.defaults(options, { deep: true });
            var attrs = Backbone.Model.prototype.toJSON.apply(this, arguments);
            if (opts.deep) {
                _.each(this._relations, function (relOpts, field) {
                    if (_.has(attrs, field) && !_.isNull(attrs[field]) && !_.isUndefined(attrs[field])) {
                        attrs[field] = attrs[field].toJSON(opts);
                    }
                });
            }
            return attrs;
        }
    });
    return BaseModel;
});