define('modules/work_editor/views/editor_layout', [
    'require',
    'exports',
    'module',
    'marionette',
    'modules/work_editor/views/templates/editor_layout'
], function (require) {
    var Marionette = require('marionette');
    var WorkEditorLayoutView = Marionette.LayoutView.extend({
        template: require('./templates/editor_layout'),
        regions: {
            master: '#master-pane',
            details: '#details-pane'
        },
        triggers: {
            'click .save-work': 'save',
            'click .delete-work': 'delete',
            'click .link-copies': 'link-copies',
            'click .edit-work': 'edit'
        },
        setDetailsTitle: function (title) {
            this.$('.details-title').text(title);
        },
        setDirty: function (dirty) {
            this.$('.save-work').prop('disabled', !dirty);
            if (dirty) {
                this.$('.save-work .btn-label').text('save');
                this.$('.save-work .glyphicon').addClass('glyphicon-save').removeClass('glyphicon-saved');
            } else {
                this.$('.save-work .btn-label').text('saved');
                this.$('.save-work .glyphicon').addClass('glyphicon-saved').removeClass('glyphicon-save');
            }
        }
    });
    return WorkEditorLayoutView;
});