define('modules/work_editor/controls/titles_editor/entities/title', [
    'require',
    'exports',
    'module',
    'backbone'
], function (require) {
    var Backbone = require('backbone');
    var TitleViewModel = Backbone.Model.extend({
        defaults: {
            type: '',
            title: '',
            subtitle: ''
        },
        flush: function () {
            this.trigger('flush', this);
        }
    });
    return TitleViewModel;
});