define('modules/work_editor/controls/publication_info/views/editor_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'controls/moment.infer',
    'mixins/editable_field_view_mixin',
    'modules/work_editor/controls/publication_info/views/templates/editor'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), inferMoment = require('controls/moment.infer'), EditableFieldViewMixin = require('mixins/editable_field_view_mixin');
    var KEY_ENTER = 13;
    var PublicationInfoEditorView = Marionette.ItemView.extend({
        template: require('./templates/editor'),
        tagName: 'section',
        className: 'publication-info editable-field',
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing',
            'keyup input': function (e) {
                if (e.keyCode === KEY_ENTER) {
                    this.stopEditing();
                }
            },
            'change input[name=dateDescription]': '_parseDateDescription',
            'keyup input[name=dateValue]': '_parseDateValue'
        },
        modelEvents: {
            'change:place': function (model, value) {
                this.$('input[name=place]').val(value.trim());
                if (value) {
                    this.$('.display .place').text(value);
                } else {
                    this.$('.display .place').html('<span class="text-muted">[place]</span>');
                }
            },
            'change:publisher': function (model, value) {
                this.$('input[name=publisher]').val(value.trim());
                if (value) {
                    this.$('.display .publisher').text(value);
                } else {
                    this.$('.display .publisher').html('<span class="text-muted">[publisher]</span>');
                }
            },
            'change:dateDescription': function (model, value) {
                this.$('input[name=dateDescription]').val(value.trim());
                if (value) {
                    this.$('.display .date').text(value);
                } else {
                    this.$('.display .date').html('<span class="text-muted">[date]</span>');
                }
            },
            'change:dateValue': function (model, value) {
                if (value) {
                    this.$('.display .date').attr('datetime', value.format('YYYY-MM-DD'));
                } else {
                    this.$('.display .date').removeAttr('datetime');
                }
            }
        },
        initialize: function (options) {
            if (options.channel) {
                this.listenTo(options.channel, 'commit', this.stopEditing);
            }
            this.initializeEditableField(options);
            this._dateValueChanged = false;
        },
        onFocus: function () {
            this.$('input[name=place]').focus();
        },
        onBlur: function () {
            this.updateViewModel();
        },
        updateViewModel: function () {
            this.model.set({
                publisher: this.$('input[name=publisher]').val(),
                place: this.$('input[name=place]').val(),
                dateDescription: this.$('input[name=dateDescription]').val()
            });
            if (this._dateValueChanged || this.model.changedAttributes()) {
                this.model.flush();
                this._dateValueChanged = false;
            }
        },
        _parseDateDescription: function () {
            var $descriptionInput = this.$('input[name=dateDescription]');
            var value = $descriptionInput.val();
            if (!value) {
                return;
            }
            var $valueInput = this.$('input[name=dateValue]');
            if ($valueInput.val()) {
                return;
            }
            var m = inferMoment(value);
            if (m.isValid()) {
                $valueInput.val(m.format('D MMM YYYY'));
                $valueInput.addClass('success');
                this.$('.inferred-date').text(m.format('D MMM YYYY'));
            }
        },
        _parseDateValue: function () {
            var $input = this.$('input[name=dateValue]');
            $input.removeClass('error').removeClass('success');
            var value = $input.val();
            if (!value) {
                this.$('.inferred-date').html('&hellip;');
                return;
            }
            var m = inferMoment(value);
            if (m.isValid()) {
                $input.addClass('success');
                this.$('.inferred-date').text(m.format('D MMM YYYY'));
                this.model.set('dateValue', m);
                if (this.model.changedAttributes) {
                    this._dateValueChanged = true;
                }
            } else {
                $input.addClass('error');
                this.$('.inferred-date').text('parse error');
                this.model.set('dateValue', null);
                if (this.model.changedAttributes) {
                    this._dateValueChanged = true;
                }
            }
        }
    });
    _.defaults(PublicationInfoEditorView.prototype, EditableFieldViewMixin);
    return PublicationInfoEditorView;
});