define('catalogentries-reln/sections/info/entities/work', [
    'require',
    'exports',
    'module',
    'backbone',
    'catalogentries-reln/sections/info/entities/editions'
], function (require) {
    var Backbone = require('backbone'), EditionViewModelCollection = require('./editions');
    var WorkViewModel = Backbone.Model.extend({
        defaults: function () {
            return {
                title: '',
                editions: new EditionViewModelCollection()
            };
        }
    });
    return WorkViewModel;
});