define('controls/search_form/search_form', [
    'require',
    'exports',
    'module',
    'backbone',
    'underscore',
    'controls/search_form/views/results_view',
    'controls/search_form/views/error_view',
    'controls/search_form/views/search_form_view'
], function (require) {
    var Backbone = require('backbone'), _ = require('underscore'), ResultsView = require('./views/results_view'), ErrorView = require('./views/error_view'), SearchFormView = require('./views/search_form_view');
    function SearchFormControl(options) {
        var opts = _.defaults(options, { placeholder: 'search' });
        if (!opts.searchProvider) {
            throw new TypeError('no search provider given');
        }
        _.extend(this, _.pick(opts, 'searchProvider', 'resultView'));
        this.view = new SearchFormView(_.pick(opts, 'placeholder'));
        this.listenTo(this.view, 'search', this.search);
        _.bindAll(this, '_buildResultsView');
    }
    _.extend(SearchFormControl.prototype, Backbone.Events, {
        getView: function () {
            return this.view;
        },
        search: function (query) {
            var region = this.view.getRegion('results'), showView = _.bind(region.show, region);
            this.searchProvider(query).then(this._buildResultsView, this._buildErrorView).then(showView);
        },
        _buildResultsView: function (results) {
            var models = results instanceof Backbone.Collection ? results.models : results;
            var resultsCollection = new Backbone.Collection(models);
            var resultsView = new ResultsView({
                collection: resultsCollection,
                resultView: this.resultView
            });
            resultsView.on('all', function (evtName, view, item) {
                if (evtName.indexOf('result:') === 0 && item && item.model) {
                    this.trigger(evtName, item.model);
                }
            }, this);
            return resultsView;
        },
        _buildErrorView: function (err) {
            return new ErrorView({
                title: 'Error fetching search results',
                message: err.message || 'unknown error'
            });
        }
    });
    return SearchFormControl;
});