define('catalogentries-reln/sections/master/entities/relationship', [
    'require',
    'exports',
    'module',
    'backbone',
    'catalogentries-reln/sections/master/entities/author_refs'
], function (require) {
    var Backbone = require('backbone'), AuthorRefViewModelCollection = require('./author_refs');
    var RelationshipViewModel = Backbone.Model.extend({
        defaults: {
            type: '[unspecified]',
            title: '',
            authors: new AuthorRefViewModelCollection(),
            pubDate: null,
            edition: null
        },
        edit: function () {
            this.trigger('edit', this);
        }
    });
    return RelationshipViewModel;
});