define('catalogentries-reln/sections/info/relationship_info_section', [
    'require',
    'exports',
    'module',
    'promise',
    'underscore',
    'controls/summary/summary',
    'catalogentries-reln/sections/info/relationship_info_controller',
    'catalogentries-reln/sections/info/views/relationship_view',
    'catalogentries-reln/sections/info/views/edition_selector_view',
    'catalogentries-reln/sections/info/views/target_selector_view',
    'catalogentries-reln/sections/info/views/type_selector_view'
], function (require) {
    var Promise = require('promise'), _ = require('underscore'), SummaryControl = require('controls/summary/summary'), RelationshipInfoController = require('./relationship_info_controller'), RelationshipView = require('./views/relationship_view'), EditionSelectorView = require('./views/edition_selector_view'), TargetSelectorView = require('./views/target_selector_view'), TypeSelectorView = require('./views/type_selector_view');
    function constructSection(options) {
        var opts = _.defaults(options || {}, {});
        if (!opts.work) {
            throw new TypeError('no work provided to act as relationship source');
        }
        if (!opts.relationshipsRepo) {
            throw new TypeError('no relationships repository provided');
        }
        if (!opts.worksRepo) {
            throw new TypeError('no works repository provided');
        }
        if (!opts.channel) {
            throw new TypeError('no channel provided');
        }
        if (!opts.model) {
            throw new TypeError('no relationship domain model provided');
        }
        var controllerPromise = opts.relationshipsRepo.findTypes().then(function (types) {
            var controller = new RelationshipInfoController({
                worksRepo: opts.worksRepo,
                work: opts.work,
                types: types
            });
            return controller;
        });
        var workSearchProviderPromise = controllerPromise.then(function (controller) {
            function workSearchProvider(query, limit) {
                var promise = opts.worksRepo.search(query, { limit: limit }).then(function (results) {
                    return _.map(results, function (result) {
                        result.getViewModel = function () {
                            return result.getWork().then(_.bind(controller.convertWorkDomainModel, controller));
                        };
                        return {
                            label: result.label,
                            obj: result
                        };
                    });
                });
                return promise;
            }
            return _.throttle(workSearchProvider, 500);
        });
        var relationshipViewModelPromise = controllerPromise.then(function (controller) {
            controller.setDomainModel(opts.model);
            return controller.getViewModel();
        });
        var relationshipViewPromise = Promise.join(controllerPromise, workSearchProviderPromise, relationshipViewModelPromise, function (controller, workSearchProvider, relationshipViewModel) {
            var relationshipView = new RelationshipView({ model: relationshipViewModel });
            var editionSelectorView = new EditionSelectorView({
                model: relationshipViewModel.get('source'),
                channel: opts.channel
            });
            var typeSelectorView = new TypeSelectorView({
                model: relationshipViewModel,
                types: controller.getViewTypes(),
                channel: opts.channel
            });
            var targetSelectorView = new TargetSelectorView({
                model: relationshipViewModel.get('target'),
                channel: opts.channel,
                workSearchProvider: workSearchProvider
            });
            var summaryView = new SummaryControl.EditorView({
                model: relationshipViewModel.get('summary'),
                channel: opts.channel
            });
            function attachControls() {
                relationshipView.getRegion('source').show(editionSelectorView);
                relationshipView.getRegion('type').show(typeSelectorView);
                relationshipView.getRegion('target').show(targetSelectorView);
                relationshipView.getRegion('summary').show(summaryView);
            }
            relationshipView.on('show', attachControls);
            return relationshipView;
        });
        return {
            getView: function () {
                return relationshipViewPromise;
            }
        };
    }
    return { create: constructSection };
});