define('modules/work_editor/sections/work_master/views/work_master_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'modules/work_editor/sections/work_master/views/templates/work_master'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var WorkMasterView = Marionette.LayoutView.extend({
        template: require('./templates/work_master'),
        tagName: 'section',
        className: 'work',
        regions: {
            authors: '> .citation > .authors',
            summary: '> .summary',
            otherAuthors: '> .citation > .other-authors',
            editions: '> .editions',
            relationships: '> .relationships'
        },
        modelEvents: {
            'change:title': function (model, value) {
                if (_.isNull(value)) {
                    this.$('> .citation > .title').html('<span class="text-muted">[no canonical title]</span>');
                } else {
                    this.$('> .citation > .title').text(value);
                }
            }
        }
    });
    return WorkMasterView;
});