define('controls/select/select', [
    'require',
    'exports',
    'module',
    'backbone',
    'underscore',
    'controls/select/entities/select_item',
    'controls/select/entities/select_items',
    'controls/select/views/select_view'
], function (require) {
    var Backbone = require('backbone'), _ = require('underscore'), ItemModel = require('./entities/select_item'), ItemModelCollection = require('./entities/select_items'), SelectView = require('./views/select_view');
    function SelectControl(options) {
        var opts = _.defaults(options, {
            collection: new Backbone.Collection(),
            provider: function (obj) {
                return {
                    value: obj,
                    label: obj
                };
            },
            empty: false,
            value: null
        });
        if (!opts.collection) {
            throw new TypeError('no collection provided');
        }
        if (!(opts.collection instanceof Backbone.Collection)) {
            opts.collection = new Backbone.Collection(opts.collection);
        }
        _.extend(this, _.pick(opts, 'provider', 'collection'));
        if (opts.empty) {
            this.emptyItem = new ItemModel({ label: _.isString(opts.empty) ? opts.empty : '' });
        }
        var itemCollection = this._prepareCollection(this.collection);
        this.view = new SelectView({ collection: itemCollection });
        this.listenTo(this.view, 'select', function (itemModel) {
            this.trigger('select', itemModel.get('model'));
        });
        if (opts.value) {
            this.setValue(opts.value);
        }
    }
    _.extend(SelectControl.prototype, Backbone.Events, {
        getView: function () {
            return this.view;
        },
        setOptions: function (collection) {
            if (collection instanceof Backbone.Collection) {
                collection = collection.models;
            }
            this.collection.reset(collection);
        },
        setValue: function (obj) {
            this.defaultItem = obj ? this._prepareItem(obj) : this.emptyItem;
            return this.view.setValue(this.defaultItem);
        },
        getValue: function () {
            var value = this.view.getValue();
            return value ? value.get('model') : null;
        },
        _prepareItem: function (obj) {
            var item = this.provider(obj);
            _.extend(item, { model: obj });
            var itemModel = new ItemModel(item);
            if (_.isFunction(obj.on)) {
                obj.on('destroy', function () {
                    itemModel.destroy();
                });
            }
            return itemModel;
        },
        _prepareCollection: function (collection) {
            var itemModels = collection.map(this._prepareItem, this);
            var itemCollection = new ItemModelCollection(itemModels);
            collection.on('add', function (objModel) {
                var item = this._prepareItem(objModel);
                itemCollection.add(item);
                this.view.setValue(this.defaultItem);
            }, this);
            collection.on('reset', function () {
                var newModels = collection.map(this._prepareItem, this);
                itemCollection.reset(newModels);
                this._ensureEmptyElement(itemCollection);
                this.view.setValue(this.defaultItem);
            }, this);
            this._ensureEmptyElement(itemCollection);
            return itemCollection;
        },
        _ensureEmptyElement: function (itemCollection) {
            if (!this.emptyItem) {
                return;
            }
            if (!itemCollection.isEmpty() && itemCollection.first() === this.emptyItem) {
                return;
            }
            itemCollection.add(this.emptyItem, { at: 0 });
        }
    });
    return SelectControl;
});