define('controls/autocomplete/views/results_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var AutoCompleteResultItemView = Marionette.ItemView.extend({
        template: _.property('label'),
        tagName: 'li',
        triggers: { click: 'select' },
        modelEvents: {
            select: function () {
                this.$el.addClass('selected');
            },
            deselect: function () {
                this.$el.removeClass('selected');
            }
        }
    });
    var AutocompleteResultsView = Marionette.CollectionView.extend({
        tagName: 'ul',
        className: 'autocomplete',
        childView: AutoCompleteResultItemView,
        events: {
            mouseenter: function () {
                this.collection.deselect();
                this._focus = true;
            },
            mouseleave: function () {
                this._focus = false;
            }
        },
        initialize: function () {
            this._focus = false;
        },
        isFocused: function () {
            return this._focus;
        }
    });
    return AutocompleteResultsView;
});