define('modules/work_editor/controls/publication_info/entities/publication_info', [
    'require',
    'exports',
    'module',
    'backbone'
], function (require) {
    var Backbone = require('backbone');
    var PublicationInfo = Backbone.Model.extend({
        defaults: {
            publisher: '',
            place: '',
            dateValue: null,
            dateDescription: ''
        },
        flush: function () {
            this.trigger('flush', this);
        }
    });
    return PublicationInfo;
});