define('catalogentries-digitalcopies/references_repository', [
    'require',
    'exports',
    'module',
    'underscore',
    'catalogentries-digitalcopies/entities/copy_reference',
    'catalogentries-digitalcopies/entities/copy_references'
], function (require) {
    var _ = require('underscore'), CopyReferenceDomainModel = require('./entities/copy_reference'), CopyReferenceDomainModelCollection = require('./entities/copy_references');
    function ReferencesRepository(options) {
        var opts = _.defaults(options || {}, {});
        if (!opts.apiEndpoint) {
            throw new TypeError('no API endpoint provided');
        }
        if (!opts.restClient) {
            throw new TypeError('no REST client provided');
        }
        _.extend(this, _.pick(opts, 'apiEndpoint', 'restClient'));
    }
    _.extend(ReferencesRepository.prototype, {
        findAll: function (entityUri) {
            var repo = this;
            return this.restClient.get(this.apiEndpoint + '/' + entityUri + '/').then(function (data) {
                var models = new CopyReferenceDomainModelCollection(data, { parse: true });
                models.invoke('setRepo', repo);
                return models;
            });
        },
        find: function (id) {
            var repo = this;
            return this.restClient.get(this.apiEndpoint + '/' + id).then(function (data) {
                var copyRef = new CopyReferenceDomainModel(data, { parse: true });
                copyRef.setRepo(repo);
                return copyRef;
            });
        },
        create: function (sourceUri, targetBookId) {
            var copyRef = new CopyReferenceDomainModel({
                associatedEntry: sourceUri,
                copyId: targetBookId
            });
            copyRef.setRepo(this);
            return Promise.resolve(copyRef);
        },
        save: function (model) {
            return this.restClient.send({
                method: model.isNew() ? 'post' : 'put',
                url: this.apiEndpoint + '/' + (model.isNew() ? model.get('associatedEntry') : model.id),
                data: model.toJSON()
            }).then(_.property('id'));
        },
        delete: function (model) {
            if (model.isNew()) {
                return Promise.resolve(false);
            }
            return this.restClient.delete(this.apiEndpoint + '/' + model.id);
        }
    });
    return ReferencesRepository;
});