define('catalogentries-reln/entities/relationship', [
    'require',
    'exports',
    'module',
    'catalogentries-reln/entities/base',
    'underscore',
    'mixins/dirtyable_model_mixin',
    'catalogentries-reln/entities/anchors',
    'catalogentries-reln/entities/anchor',
    'catalogentries-reln/entities/provenance'
], function (require) {
    var BaseModel = require('./base'), _ = require('underscore'), DirtyableModelMixin = require('mixins/dirtyable_model_mixin'), AnchorDomainModelCollection = require('./anchors'), AnchorDomainModel = require('./anchor'), ProvenanceDomainModel = require('./provenance');
    var RelationshipDomainModel = BaseModel.extend({
        relations: {
            provenance: { class: ProvenanceDomainModel },
            relatedEntities: { class: AnchorDomainModelCollection },
            targetEntities: { class: AnchorDomainModelCollection }
        },
        defaults: function () {
            return {
                typeId: '',
                description: '',
                descriptionMimeType: 'text/html',
                provenance: new ProvenanceDomainModel(),
                relatedEntities: new AnchorDomainModelCollection(),
                targetEntities: new AnchorDomainModelCollection([new AnchorDomainModel()])
            };
        },
        initialize: function (attributes, options) {
            this.initializeDirtyable(options);
        },
        setRepo: function (repo) {
            this._repo = repo;
        },
        save: function () {
            if (!this._repo) {
                throw new Error('no repo');
            }
            var promise = this._repo.saveRelationship(this);
            var _this = this;
            return promise.then(function (id) {
                if (_this.isNew()) {
                    _this.set('id', id);
                } else if (id !== _this.get('id')) {
                    throw new Error('ID mismatch');
                }
                _this.trigger('sync');
                return _this;
            });
        },
        destroy: function () {
            BaseModel.prototype.destroy.apply(this, arguments);
            if (!this._repo) {
                throw new Error('no repo');
            }
            return this._repo.deleteRelationship(this);
        }
    });
    _.defaults(RelationshipDomainModel.prototype, DirtyableModelMixin);
    return RelationshipDomainModel;
});