define('modules/work_editor/sections/work_master/views/relationships_placeholder_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var RelationshipsPlaceholderView = Marionette.ItemView.extend({
        className: 'alert alert-info',
        template: _.constant('<strong>Note:</strong> Relationships cannot be edited on a work that has not yet been saved. Please save this work to add relationships.')
    });
    return RelationshipsPlaceholderView;
});