define('notifications/views/confirm_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'notifications/views/templates/confirm'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var ConfirmView = Marionette.LayoutView.extend({
        template: require('./templates/confirm'),
        className: 'modal-dialog modal-lg',
        regions: { content: '.modal-body' },
        events: {
            'click button.confirm': function () {
                this.dismiss(true);
            },
            'click button.cancel': function () {
                this.dismiss(false);
            }
        },
        templateHelpers: function () {
            return _.pick(this, 'cancelText', 'confirmText');
        },
        initialize: function (options) {
            var opts = _.defaults(options || {}, {
                confirmText: 'Confirm',
                cancelText: 'Cancel'
            });
            _.extend(this, _.pick(opts, 'confirmText', 'cancelText'));
        },
        dismiss: function (result) {
            var _this = this;
            this.trigger('modal:close', function () {
                _this.model.dismiss(result);
            });
        },
        show: function (view) {
            this.getRegion('content').show(view);
        }
    });
    return ConfirmView;
});