define('modules/person_editor/sections/master_editor/views/person_master_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'modules/person_editor/sections/master_editor/views/templates/person'
], function (require) {
    var Marionette = require('marionette');
    var PersonMasterView = Marionette.LayoutView.extend({
        template: require('./templates/person'),
        tagName: 'article',
        className: 'person',
        regions: {
            displayName: '.display-name',
            names: '.names',
            birth: '.birth',
            death: '.death',
            summary: '.summary'
        }
    });
    return PersonMasterView;
});