define('notifications/entities/message', [
    'require',
    'exports',
    'module',
    'backbone',
    'underscore'
], function (require) {
    var Backbone = require('backbone'), _ = require('underscore');
    var MessageModel = Backbone.Model.extend({
        defaults: {
            type: 'info',
            duration: 15,
            title: '',
            description: null,
            view: null,
            icon: null
        },
        initialize: function () {
            var icon = MessageModel.getIcon(this.get('icon') || this.get('type')) || this.get('icon');
            this.set('icon', icon);
            if (!this.isValid()) {
                throw new TypeError('invalid message data: ' + this.validationError);
            }
        },
        validate: function (attrs) {
            if (_.isNull(attrs.type) || attrs.type === '' || !_.contains(MessageModel.MESSAGE_TYPES, attrs.type)) {
                return 'type field is invalid';
            }
            if (_.isNull(attrs.title) || attrs.title === '') {
                return 'title field is required';
            }
        },
        dismiss: function (result) {
            this.trigger('dismiss', result);
        }
    });
    function glyphicon(name) {
        return 'glyphicon glyphicon-' + name;
    }
    _.extend(MessageModel, {
        MESSAGE_TYPES: [
            'alert',
            'confirm',
            'error',
            'warning',
            'info',
            'success'
        ],
        _icons: {
            'info': glyphicon('info-sign'),
            'warning': glyphicon('alert'),
            'error': glyphicon('exclamation-sign'),
            'success': glyphicon('ok-sign')
        },
        setIcon: function (name, cssClass) {
            MessageModel._icons[name] = cssClass;
        },
        getIcon: function (name) {
            return name && _.has(MessageModel._icons, name) ? MessageModel._icons[name] : null;
        }
    });
    return MessageModel;
});