define('catalogentries-digitalcopies/sections/info/views/hathitrust_book_reader_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    function getHathitrustBookReaderUrl(id) {
        return '//babel.hathitrust.org/cgi/pt?id=' + id + ';view=2up;seq=6;skin=mobile';
    }
    var HathitrustBookReaderView = Marionette.ItemView.extend({
        template: function (ctx) {
            return '<iframe class="full" src="' + ctx.src + '" height="600"></iframe>';
        },
        templateHelpers: function () {
            return { src: getHathitrustBookReaderUrl(this.htid) };
        },
        initialize: function (opts) {
            if (!opts.htid) {
                throw new TypeError('no htid given');
            }
            _.extend(this, _.pick(opts, 'htid'));
        }
    });
    return HathitrustBookReaderView;
});