define('catalogentries-digitalcopies/sections/info/views/search_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'notifications/index',
    'controls/search_form/search_form',
    'catalogentries-digitalcopies/sections/info/views/hathitrust_book_reader_view',
    'catalogentries-digitalcopies/sections/info/views/templates/result',
    'catalogentries-digitalcopies/sections/info/views/templates/search'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), Notifications = require('notifications/index').getInstance(), SearchForm = require('controls/search_form/search_form'), HathitrustBookReaderView = require('./hathitrust_book_reader_view');
    var ResultView = Marionette.ItemView.extend({
        template: require('./templates/result'),
        className: 'media media-selectable',
        triggers: {
            'click .accept': 'accept',
            'click .content': 'preview'
        }
    });
    var SearchView = Marionette.LayoutView.extend({
        template: require('./templates/search'),
        regions: { search: '> .search-form' },
        initialize: function (options) {
            var opts = options || {};
            if (!opts.searchProvider) {
                throw new TypeError('no digital copies search provider given');
            }
            _.extend(this, _.pick(opts, 'searchProvider'));
        },
        onShow: function () {
            this.searchForm = new SearchForm({
                placeholder: 'Search HathiTrust',
                searchProvider: this.searchProvider,
                resultView: ResultView
            });
            this.listenTo(this.searchForm, 'result:accept', this._acceptResult);
            this.listenTo(this.searchForm, 'result:preview', this._previewResult);
            this.getRegion('search').show(this.searchForm.getView());
        },
        _acceptResult: function (result) {
            this.trigger('select', result);
        },
        _previewResult: function (result) {
            var _this = this;
            var idParts = result.id.match(/^htid:(\d{9})#(.+)$/);
            if (!idParts) {
                throw new Error('expected HathiTrust resource identifier, received {' + result.id + '}');
            }
            var htid = idParts[2];
            var bookReaderView = new HathitrustBookReaderView({ htid: htid });
            Notifications.confirm({
                title: 'Is this digital copy correct?',
                view: bookReaderView
            }).then(function (accept) {
                if (accept) {
                    _this._acceptResult(result);
                }
            });
        }
    });
    return SearchView;
});