define('modules/person_editor/sections/info_editor/views/names_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'backbone.wreqr',
    'modules/person_editor/sections/info_editor/views/name_view',
    'modules/person_editor/sections/info_editor/views/templates/names'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), Wreqr = require('backbone.wreqr'), NameView = require('./name_view');
    var NoNamesView = Marionette.ItemView.extend({
        template: function () {
            return '[no names added yet]';
        },
        tagName: 'span',
        className: 'text-muted',
        triggers: { dblclick: 'add:name' }
    });
    var NamesView = Marionette.CompositeView.extend({
        template: require('./templates/names'),
        childView: NameView,
        emptyView: NoNamesView,
        childViewContainer: '.names',
        childViewOptions: function () {
            return { channel: this.channel };
        },
        events: {
            'click button.add-name': function () {
                this.collection.add({});
            }
        },
        initialize: function (options) {
            var opts = _.defaults(options, { channel: new Wreqr.EventAggregator() });
            _.extend(this, _.pick(opts, 'channel'));
            this.on('childview:add:name', function () {
                this.collection.add({});
            }, this);
            this.rendered = false;
        },
        onAddChild: function (childView) {
            if (this.rendered) {
                childView.startEditing();
            }
        },
        onShow: function () {
            this.rendered = true;
        }
    });
    return NamesView;
});