define('controls/summary/views/display_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var SummaryView = Marionette.ItemView.extend({
        template: _.property('summary'),
        tagName: 'section',
        className: 'summary',
        modelEvents: {
            'change:summary': function (model, value) {
                this.$el.html(value);
            }
        }
    });
    return SummaryView;
});