define('modules/work_editor/sections/work_info/entities/work', [
    'require',
    'exports',
    'module',
    'backbone',
    'modules/work_editor/controls/author_refs_editor/author_refs_editor',
    'modules/work_editor/controls/titles_editor/titles_editor'
], function (require) {
    var Backbone = require('backbone'), AuthorRefsEditorControl = require('modules/work_editor/controls/author_refs_editor/author_refs_editor'), TitlesEditorControl = require('modules/work_editor/controls/titles_editor/titles_editor');
    var WorkViewModel = Backbone.Model.extend({
        defaults: function () {
            return {
                authors: new AuthorRefsEditorControl.Collection(),
                titles: new TitlesEditorControl.Collection(),
                otherAuthors: new AuthorRefsEditorControl.Collection()
            };
        },
        flush: function () {
            this.trigger('flush', this);
        }
    });
    return WorkViewModel;
});