define('modules/work_editor/sections/edition_info/views/edition_name_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'mixins/editable_field_view_mixin',
    'modules/work_editor/sections/edition_info/views/templates/edition_name'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), EditableFieldViewMixin = require('mixins/editable_field_view_mixin');
    var KEY_ENTER = 13;
    var EditionNameView = Marionette.ItemView.extend({
        template: require('./templates/edition_name'),
        tagName: 'section',
        className: 'edition-name editable-field',
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing',
            'keyup input': function (e) {
                if (e.keyCode === KEY_ENTER) {
                    this.stopEditing();
                }
            }
        },
        modelEvents: {
            'change:editionName': function (model, value) {
                this.$('input[name=editionName]').val(value.trim());
                if (value) {
                    this.$('.display .edition-name').text(value);
                } else {
                    this.$('.display .edition-name').html('<span class="text-muted">[no edition name]</span>');
                }
            }
        },
        initialize: function (options) {
            var opts = _.defaults(options, {});
            if (opts.channel) {
                this.listenTo(opts.channel, 'commit', this.stopEditing);
            }
            this.initializeEditableField(options);
        },
        onFocus: function () {
            this.$('input[name=editionName]').focus();
        },
        onBlur: function () {
            this.updateViewModel();
        },
        updateViewModel: function () {
            this.model.set({ editionName: this.$('input[name=editionName]').val() });
            if (this.model.changedAttributes()) {
                this.model.flush();
            }
        }
    });
    _.defaults(EditionNameView.prototype, EditableFieldViewMixin);
    return EditionNameView;
});