define('modules/work_editor/sections/volume_info/views/volume_number_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'mixins/editable_field_view_mixin',
    'modules/work_editor/sections/volume_info/views/templates/volume_number'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), EditableFieldViewMixin = require('mixins/editable_field_view_mixin');
    var KEY_ENTER = 13;
    var VolumeNumberView = Marionette.ItemView.extend({
        template: require('./templates/volume_number'),
        tagName: 'section',
        className: 'volume-number editable-field',
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing',
            'keyup input': function (e) {
                if (e.keyCode === KEY_ENTER) {
                    this.stopEditing();
                }
            }
        },
        modelEvents: {
            'change:volumeNumber': function (model, value) {
                this.$('input[name=volumeNumber]').val(value.trim());
                if (value) {
                    this.$('.display .volume-number').text(value);
                } else {
                    this.$('.display .volume-number').html('<span class="text-muted">[no volume number]</span>');
                }
            }
        },
        initialize: function (options) {
            var opts = _.defaults(options, {});
            if (opts.channel) {
                this.listenTo(opts.channel, 'commit', this.stopEditing);
            }
            this.initializeEditableField(options);
        },
        onFocus: function () {
            this.$('input[name=volumeNumber]').focus();
        },
        onBlur: function () {
            this.updateViewModel();
        },
        updateViewModel: function () {
            this.model.set({ volumeNumber: this.$('input[name=volumeNumber]').val() });
            if (this.model.changedAttributes()) {
                this.model.flush();
            }
        }
    });
    _.defaults(VolumeNumberView.prototype, EditableFieldViewMixin);
    return VolumeNumberView;
});