define('modules/person_editor/sections/master_editor/views/names_list_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var NameView = Marionette.ItemView.extend({
        template: function (ctx) {
            var display = ctx.display;
            var nameParts = [
                ctx.prefix,
                ctx.first,
                ctx.middle,
                ctx.last,
                ctx.suffix
            ];
            var details = _.chain(nameParts).filter(_.identity).join(' ').value();
            if (details) {
                display += ' <small class="details">[' + details + ']</small>';
            }
            return display;
        },
        tagName: 'li',
        className: 'name',
        modelEvents: { change: 'render' }
    });
    var NoNamesView = Marionette.ItemView.extend({
        template: function () {
            return '[no names have been added to this person]';
        },
        tagName: 'li',
        className: 'text-muted'
    });
    var NamesView = Marionette.CollectionView.extend({
        tagName: 'ul',
        className: 'names',
        childView: NameView,
        emptyView: NoNamesView
    });
    return NamesView;
});