define('notifications/display_chain/message_display_chain_mixin', [
    'require',
    'exports',
    'module'
], function (require) {
    return {
        next: null,
        handlesType: function () {
            return false;
        },
        display: function () {
        },
        handle: function (model, options) {
            if (this.handlesType(model.get('type'))) {
                return this.display(model, options);
            } else if (this.next) {
                return this.next.handle(model, options);
            }
            throw new Error('unable to handle provided message model');
        }
    };
});