define('controls/autocomplete/entities/items', [
    'require',
    'exports',
    'module',
    'backbone',
    'underscore',
    'controls/autocomplete/entities/item'
], function (require) {
    var Backbone = require('backbone'), _ = require('underscore'), AutocompleteItem = require('./item');
    var AutocompleteItemCollection = Backbone.Collection.extend({
        model: AutocompleteItem,
        initialize: function () {
            this.cursor = null;
            this.on('reset', function () {
                this.cursor = null;
            }, this);
        },
        deselect: function () {
            if (_.isNull(this.cursor)) {
                return;
            }
            this.at(this.cursor).deselect();
            this.cursor = null;
        },
        selectNext: function () {
            if (this.length === 0 || this.cursor === this.length - 1) {
                return;
            }
            if (_.isNull(this.cursor)) {
                this.cursor = 0;
            } else {
                this.at(this.cursor).deselect();
                this.cursor += 1;
            }
            this.at(this.cursor).select();
        },
        selectPrevious: function () {
            if (this.length === 0) {
                return;
            }
            if (_.isNull(this.cursor)) {
                this.cursor = this.length - 1;
            } else {
                this.at(this.cursor).deselect();
                this.cursor = Math.max(this.cursor - 1, 0);
            }
            this.at(this.cursor).select();
        },
        getSelected: function () {
            if (_.isNull(this.cursor)) {
                return null;
            }
            return this.at(this.cursor);
        }
    });
    return AutocompleteItemCollection;
});