define('modules/work_editor/sections/work_master/sections/edition_master/views/volumes_domain_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'modules/work_editor/sections/work_master/sections/edition_master/views/templates/volumes_domain'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var VolumeDomainView = Marionette.LayoutView.extend({
        template: _.constant('<div class="volume"></div>'),
        className: 'panel-wrapper volume-domain',
        regions: { volume: '.volume' },
        initialize: function (options) {
            var opts = _.defaults(options, { parentId: null });
            if (!opts.sectionFactory) {
                throw new TypeError('no section factory provided');
            }
            _.extend(this, _.pick(opts, 'parentId', 'sectionFactory'));
            this.view = null;
        },
        onShow: function () {
            this.section = this.sectionFactory.create({
                volume: this.model,
                viewParentId: this.parentId
            });
            var view = this.section.getView();
            this.listenTo(view, 'edit', function () {
                this.trigger('edit');
            });
            this.listenTo(view, 'link-copies', function () {
                this.trigger('link-copies');
            });
            this.getRegion('volume').show(view);
        },
        expand: function () {
            if (this.section) {
                this.section.getView().expand();
            }
        },
        collapse: function () {
            if (this.section) {
                this.section.getView().collapse();
            }
        },
        toggle: function () {
            if (this.section) {
                this.section.getView().toggle();
            }
        }
    });
    var NoVolumesView = Marionette.ItemView.extend({
        template: _.constant('[no volumes]'),
        className: 'text-muted',
        triggers: { dblclick: 'add' }
    });
    var VolumesDomainView = Marionette.CompositeView.extend({
        template: require('./templates/volumes_domain'),
        className: 'volumes-domain',
        childView: VolumeDomainView,
        emptyView: NoVolumesView,
        childViewContainer: '.volumes',
        templateHelpers: function () {
            return { parentId: this.cid };
        },
        childViewOptions: function () {
            return {
                parentId: this.cid,
                sectionFactory: this.sectionFactory
            };
        },
        triggers: { 'click button.add-volume': 'add' },
        initialize: function (opts) {
            if (!opts.sectionFactory) {
                throw new TypeError('no section factory provided');
            }
            _.extend(this, _.pick(opts, 'sectionFactory'));
            this.on('childview:add', function () {
                this.trigger('add');
            }, this);
            this.on('childview:edit', function (view) {
                this.trigger('edit', view.model);
            });
            this.on('childview:link-copies', function (view) {
                this.trigger('link-copies', view.model);
            });
            this.rendered = false;
        },
        onAddChild: function (childView) {
            if (this.rendered) {
                childView.expand();
            }
        },
        onShow: function () {
            this.rendered = true;
        }
    });
    return VolumesDomainView;
});