define('services/repos/entities/edition', [
    'require',
    'exports',
    'module',
    'services/repos/entities/base',
    'underscore',
    'mixins/dirtyable_model_mixin',
    'services/repos/entities/publication_info',
    'services/repos/entities/volumes',
    'services/repos/entities/author_refs',
    'services/repos/entities/titles'
], function (require) {
    var BaseModel = require('./base'), _ = require('underscore'), DirtyableModelMixin = require('mixins/dirtyable_model_mixin'), PublicationInfo = require('./publication_info'), Volumes = require('./volumes'), AuthorRefs = require('./author_refs'), Titles = require('./titles');
    var Edition = BaseModel.extend({
        relations: {
            publicationInfo: { class: PublicationInfo },
            volumes: { class: Volumes },
            authors: { class: AuthorRefs },
            titles: { class: Titles },
            otherAuthors: { class: AuthorRefs }
        },
        defaults: function () {
            return {
                editionName: '',
                publicationInfo: new PublicationInfo(),
                volumes: new Volumes(),
                authors: new AuthorRefs(),
                titles: new Titles(),
                otherAuthors: new AuthorRefs(),
                summary: '',
                series: ''
            };
        },
        initialize: function (attributes, options) {
            this.initializeDirtyable(options);
            this.get('volumes').invoke('setEdition', this);
        },
        getCanonicalTitle: function () {
            var result = this.get('titles').find(function (title) {
                return title.isCanonical();
            });
            return _.isUndefined(result) ? null : result;
        },
        createVolume: function () {
            if (!this._repo) {
                throw new Error('no repo');
            }
            return this._repo.createVolume(this);
        },
        setRepo: function (repo) {
            this._repo = repo;
            this.get('volumes').invoke('setRepo', repo);
        },
        setWork: function (work) {
            this._work = work;
        },
        getWork: function () {
            return this._work;
        },
        getUri: function () {
            if (!this._repo) {
                throw new Error('no repo');
            }
            return this._repo.getEditionUri(this);
        }
    });
    _.defaults(Edition.prototype, DirtyableModelMixin);
    return Edition;
});