define('services/registry', [
    'require',
    'exports',
    'module',
    'backbone',
    'underscore'
], function (require) {
    var Backbone = require('backbone'), _ = require('underscore');
    function ServiceRegistration(options) {
        var config = _.defaults(options, { name: 'Unnamed Service' });
        _.extend(this, _.pick(config, 'id', 'name', 'service'));
    }
    _.extend(ServiceRegistration.prototype, Backbone.Events, {
        getId: function () {
            return this.id;
        },
        getName: function () {
            return this.name;
        },
        getInstance: function () {
            return this.service;
        }
    });
    function ServiceRegistry() {
        this._registrations = {};
    }
    _.extend(ServiceRegistry.prototype, Backbone.Events, {
        isRegistered: function (svcId) {
            return _.has(this._registrations, svcId) && !_.isNull(this._registrations[svcId]);
        },
        get: function (svcId) {
            if (!this.isRegistered(svcId)) {
                throw new Error('The requested service [' + svcId + '] is not available.');
            }
            return this._registrations[svcId];
        },
        register: function (cfg) {
            var svcId = cfg.id;
            if (_.isUndefined(svcId) || _.isNull(svcId) || _.isEmpty(svcId)) {
                throw new TypeError('No service ID specified');
            }
            if (this.isRegistered(svcId)) {
                throw new Error('The supplied service [' + svcId + '] has already been registered');
            }
            this._registrations[svcId] = new ServiceRegistration(cfg);
            return {
                unregister: _.bind(function () {
                    this._registrations[svcId] = null;
                }, this)
            };
        }
    });
    return ServiceRegistry;
});