define('modules/work_editor/work_editor', [
    'require',
    'exports',
    'module',
    'backbone',
    'promise',
    'backbone.wreqr',
    'underscore',
    'notifications/index',
    'catalogentries-digitalcopies/index',
    'catalogentries-reln/index',
    'modules/work_editor/views/editor_layout',
    'modules/work_editor/sections/work_master/work_master_section',
    'modules/work_editor/sections/work_info/work_info_section',
    'modules/work_editor/sections/edition_info/edition_info_section',
    'modules/work_editor/sections/volume_info/volume_info_section'
], function (require) {
    var Backbone = require('backbone'), Promise = require('promise'), Wreqr = require('backbone.wreqr'), _ = require('underscore'), Notifications = require('notifications/index').getInstance(), DigitalCopies = require('catalogentries-digitalcopies/index'), Relationship = require('catalogentries-reln/index'), WorkEditorLayout = require('./views/editor_layout'), WorkMasterEditorSection = require('./sections/work_master/work_master_section'), WorkInfoEditorSection = require('./sections/work_info/work_info_section'), EditionInfoEditorSection = require('./sections/edition_info/edition_info_section'), VolumeInfoEditorSection = require('./sections/volume_info/volume_info_section');
    function WorkEditor(options) {
        var opts = _.defaults(options, { channel: new Wreqr.EventAggregator() });
        if (!opts.region) {
            throw new TypeError('No region specified');
        }
        if (!opts.worksRepo) {
            throw new TypeError('No works repository specified');
        }
        if (!opts.peopleRepo) {
            throw new TypeError('No people repository specified');
        }
        if (!opts.relationshipsRepo) {
            throw new TypeError('No relationships repository specified');
        }
        if (!opts.booksRepo) {
            throw new TypeError('No digital copies book search repository specified');
        }
        if (!opts.copiesRepo) {
            throw new TypeError('No digital copy references repository specified');
        }
        _.extend(this, _.pick(opts, 'channel', 'region', 'worksRepo', 'peopleRepo', 'relationshipsRepo', 'booksRepo', 'copiesRepo'));
        this.listenTo(this.channel, 'new:work', this.createWork);
        this.listenTo(this.channel, 'edit:work', this._renderWorkEditor);
        this.listenTo(this.channel, 'edit:edition', this._renderEditionDetails);
        this.listenTo(this.channel, 'edit:volume', this._renderVolumeDetails);
        this.listenTo(this.channel, 'edit:relationship', this._renderRelationshipDetails);
        this.listenTo(this.channel, 'edit:copies', this._renderCopyDetails);
        this.layout = null;
    }
    _.extend(WorkEditor.prototype, Backbone.Events, {
        editWork: function () {
        },
        createWork: function () {
            var render = _.bind(this._renderWorkEditor, this);
            this.worksRepo.createWork().then(render);
        },
        removeWork: function () {
        },
        quickAdd: function () {
        },
        save: function () {
            if (this.work) {
                this.channel.trigger('commit');
                var saveWork = this.work.save();
                var saveRelationships = this.relationships ? Promise.all(this.relationships.invoke('save')) : null;
                Promise.join(saveWork, saveRelationships).then(function () {
                    Notifications.success({ title: 'Work saved successfully' });
                }).catch(function () {
                    Notifications.error({ title: 'Oops! Sorry, I was not able to save this work.' });
                });
            }
        },
        getLayout: function () {
            if (!_.isNull(this.layout)) {
                return this.layout;
            }
            this.layout = new WorkEditorLayout();
            this.listenTo(this.layout, 'save', this.save);
            this.listenTo(this.layout, 'edit', this._renderWorkDetails);
            this.listenTo(this.layout, 'link-copies', function () {
                if (this.work.isNew()) {
                    Notifications.info('Digital copies may only be linked with a work that has already been persisted.');
                    return;
                }
                this.channel.trigger('edit:copies', this.work.getUri());
            });
            this.listenTo(this.layout, 'delete', function () {
                var _this = this;
                Notifications.confirm({
                    title: 'Delete Work',
                    description: 'Are you sure you want to delete this work?'
                }).then(function (result) {
                    if (result) {
                        _this.work.destroy();
                    }
                });
            });
            this.listenTo(this.layout, 'before:destroy', this._onFocusLost);
            this.listenTo(this.layout, 'destroy', function () {
                this.layout = null;
            });
            return this.layout;
        },
        isDirty: function () {
            return this.work && this.work.isDirty() || this.relationships && this.relationships.any(function (r) {
                return r.isDirty();
            });
        },
        _renderWorkEditor: function (work) {
            var promise;
            var _this = this;
            if (work !== this.work && this.isDirty()) {
                promise = Notifications.confirm({
                    title: 'Unsaved Changes',
                    description: 'Do you want to save the changes to this work?'
                }).then(function (result) {
                    if (result) {
                        _this.save();
                    }
                    _this.stopListening(_this.work);
                });
            } else {
                promise = Promise.resolve(null);
            }
            promise.then(function () {
                _this.work = work;
                var layout = _this.getLayout();
                if (_this.region.currentView !== layout) {
                    _this.region.show(layout);
                }
                _this.listenTo(_this.work, 'dirty:changed', function (model, dirty) {
                    layout.setDirty(dirty);
                });
                layout.setDirty(_this.isDirty());
                _this.listenTo(_this.work, 'destroy', function () {
                    layout.destroy();
                });
                var relationshipsPromise = (work.isNew() ? Promise.resolve(null) : _this.relationshipsRepo.findAllByUri(work.getUri())).catch(function () {
                    Notifications.error('Unable to fetch relationships from server. Relationships editor will not be available.');
                    return null;
                });
                relationshipsPromise.then(function (relationships) {
                    _this.relationships = relationships;
                    if (relationships) {
                        _this.listenTo(relationships, 'change', function () {
                            layout.setDirty(_this.isDirty());
                        });
                    }
                    var masterEditorSection = WorkMasterEditorSection.create({
                        work: work,
                        worksRepo: _this.worksRepo,
                        channel: _this.channel,
                        relationshipsRepo: _this.relationshipsRepo,
                        relationships: relationships
                    });
                    layout.getRegion('master').show(masterEditorSection.getView());
                    _this._renderWorkDetails();
                });
            });
        },
        _renderWorkDetails: function () {
            var layout = this.getLayout();
            if (this.region.currentView !== layout) {
                this.region.show(layout);
            }
            var infoEditorSection = WorkInfoEditorSection.create({
                peopleRepo: this.peopleRepo,
                work: this.work,
                channel: this.channel
            });
            layout.setDetailsTitle('Work');
            layout.getRegion('details').show(infoEditorSection.getView());
        },
        _renderCopyDetails: function (entryUri) {
            var layout = this.getLayout();
            var chooserSection = DigitalCopies.InfoSection.create({
                uri: entryUri,
                booksRepo: this.booksRepo,
                copiesRepo: this.copiesRepo
            });
            chooserSection.getView().then(function (view) {
                layout.setDetailsTitle('Digital Copies');
                layout.getRegion('details').show(view);
            });
        },
        _renderEditionDetails: function (edition) {
            var layout = this.getLayout();
            this.region.show(layout);
            var infoEditorSection = EditionInfoEditorSection.create({
                peopleRepo: this.peopleRepo,
                edition: edition,
                channel: this.channel
            });
            edition.on('destroy', function () {
                if (layout.getRegion('details').currentView === infoEditorSection.getView()) {
                    layout.getRegion('details').empty();
                }
            });
            layout.setDetailsTitle('Edition');
            layout.getRegion('details').show(infoEditorSection.getView());
        },
        _renderVolumeDetails: function (volume) {
            var layout = this.getLayout();
            this.region.show(layout);
            var infoEditorSection = VolumeInfoEditorSection.create({
                peopleRepo: this.peopleRepo,
                volume: volume,
                channel: this.channel
            });
            volume.on('destroy', function () {
                if (layout.getRegion('details').currentView === infoEditorSection.getView()) {
                    layout.getRegion('details').empty();
                }
            });
            layout.setDetailsTitle('Volume');
            layout.getRegion('details').show(infoEditorSection.getView());
        },
        _renderRelationshipDetails: function (relationship) {
            if (!this.work) {
                throw new Error('relationship editor can only be shown when there is a work is in focus');
            }
            var layout = this.getLayout();
            if (this.region.currentView !== layout) {
                this.region.show(layout);
            }
            var infoEditorSection = Relationship.InfoSection.create({
                model: relationship,
                channel: this.channel,
                relationshipsRepo: this.relationshipsRepo,
                worksRepo: this.worksRepo,
                work: this.work
            });
            layout.setDetailsTitle('Loading...');
            infoEditorSection.getView().then(function (view) {
                relationship.on('destroy', function () {
                    if (layout.getRegion('details').currentView === view) {
                        layout.getRegion('details').empty();
                    }
                });
                layout.getRegion('details').show(view);
                layout.setDetailsTitle('Relationship');
            });
        },
        _onFocusLost: function () {
            var promise;
            var _this = this;
            if (this.isDirty()) {
                promise = Notifications.confirm({
                    title: 'Unsaved Changes',
                    description: 'Do you want to save the changes to this work?'
                }).then(function (result) {
                    if (result) {
                        _this.save();
                    }
                });
            } else {
                promise = Promise.resolve(null);
            }
            promise.then(function () {
                _this.stopListening(_this.work);
                _this.work = null;
            });
        }
    });
    return WorkEditor;
});