define('modules/person_editor/sections/info_editor/views/name_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'mixins/editable_field_view_mixin',
    'modules/person_editor/sections/info_editor/views/templates/name'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore'), EditableFieldMixin = require('mixins/editable_field_view_mixin');
    var KEY_ENTER = 13;
    var NameView = Marionette.ItemView.extend({
        template: require('./templates/name'),
        tagName: 'section',
        className: 'name editable-field',
        events: {
            'dblclick .display': 'startEditing',
            'click button.done': 'stopEditing',
            'click button.delete': function () {
                if (this.allowDelete) {
                    this.model.destroy();
                }
            },
            'keypress input': function (e) {
                if (e.keyCode === KEY_ENTER) {
                    e.preventDefault();
                    this.stopEditing();
                }
            }
        },
        modelEvents: {
            'change:prefix': function (model, value) {
                this.$('input[name=prefix]').val(value);
            },
            'change:first': function (model, value) {
                this.$('input[name=first]').val(value);
            },
            'change:middle': function (model, value) {
                this.$('input[name=middle]').val(value);
            },
            'change:last': function (model, value) {
                this.$('input[name=last]').val(value);
            },
            'change:suffix': function (model, value) {
                this.$('input[name=suffix]').val(value);
            },
            'change:display': function (model, value) {
                this.$('input[name=display]').val(value);
                var displayValue = value.trim();
                this.$('.display').html(displayValue || '<span class="text-muted">[name]</span>');
            }
        },
        templateHelpers: function () {
            return { allowDelete: this.allowDelete };
        },
        initialize: function (options) {
            var opts = _.defaults(options, { allowDelete: true });
            if (opts.channel) {
                this.listenTo(opts.channel, 'commit', this.stopEditing);
            }
            _.extend(this, _.pick(opts, 'allowDelete'));
            this.initializeEditableField(options);
        },
        onFocus: function () {
            this.$('input[name=prefix]').focus();
        },
        onBlur: function () {
            this.updateViewModel();
        },
        updateViewModel: function () {
            this.model.set({
                prefix: this.$('input[name=prefix]').val(),
                first: this.$('input[name=first]').val(),
                middle: this.$('input[name=middle]').val(),
                last: this.$('input[name=last]').val(),
                suffix: this.$('input[name=suffix]').val(),
                display: this.$('input[name=display]').val()
            });
            if (this.model.changedAttributes()) {
                this.model.flush();
            }
        }
    });
    _.defaults(NameView.prototype, EditableFieldMixin);
    return NameView;
});