define('modules/work_editor/sections/work_master/work_master_section', [
    'require',
    'exports',
    'module',
    'underscore',
    'notifications/index',
    'catalogentries-reln/index',
    'controls/summary/summary',
    'modules/work_editor/sections/work_master/work_master_controller',
    'modules/work_editor/sections/work_master/views/author_refs_view',
    'modules/work_editor/sections/work_master/views/work_master_view',
    'modules/work_editor/sections/work_master/views/relationships_placeholder_view',
    'modules/work_editor/sections/work_master/views/relationships_unavailable_view',
    'modules/work_editor/sections/work_master/views/editions_domain_view',
    'modules/work_editor/sections/work_master/sections/edition_master/edition_section'
], function (require) {
    var _ = require('underscore'), Notifications = require('notifications/index'), Relationships = require('catalogentries-reln/index'), SummaryControl = require('controls/summary/summary'), WorkMasterController = require('./work_master_controller'), AuthorRefsView = require('./views/author_refs_view'), WorkMasterView = require('./views/work_master_view'), RelationshipsPlaceholderView = require('./views/relationships_placeholder_view'), RelationshipsUnavailableView = require('./views/relationships_unavailable_view'), EditionsDomainView = require('./views/editions_domain_view'), EditionSection = require('./sections/edition_master/edition_section');
    function constructSection(options) {
        var opts = _.defaults(options, {});
        if (!opts.work) {
            throw new TypeError('no work provided to work master section');
        }
        if (!opts.worksRepo) {
            throw new TypeError('no works repository provided');
        }
        if (!opts.relationshipsRepo) {
            throw new TypeError('no relationships repository given');
        }
        if (!opts.channel) {
            throw new TypeError('no channel provided');
        }
        var controller = new WorkMasterController();
        controller.setDomainModel(opts.work);
        var viewModel = controller.getViewModel();
        var workView = new WorkMasterView({ model: viewModel });
        var authorsView = new AuthorRefsView({ collection: viewModel.get('authors') });
        var otherAuthorsView = new AuthorRefsView({
            collection: viewModel.get('otherAuthors'),
            hideEmptyView: true
        });
        var summaryView = new SummaryControl.DisplayView({ model: viewModel.get('summary') });
        var editionsView = new EditionsDomainView({
            collection: opts.work.get('editions'),
            channel: opts.channel,
            sectionFactory: EditionSection
        });
        function editEdition(edition) {
            opts.channel.trigger('edit:edition', edition);
        }
        editionsView.on('add', function () {
            opts.work.createEdition().then(editEdition);
        });
        editionsView.on('edit', editEdition);
        editionsView.on('link-copies', function (edition) {
            if (edition.isNew()) {
                Notifications.info('Digital copies may only be linked with an edition that has already been persisted.');
                return;
            }
            opts.channel.trigger('edit:copies', edition.getUri());
        });
        var relationshipsViewPromise;
        if (opts.work.isNew()) {
            relationshipsViewPromise = Promise.resolve(new RelationshipsPlaceholderView());
        } else if (!opts.relationships) {
            relationshipsViewPromise = Promise.resolve(new RelationshipsUnavailableView());
        } else {
            var relationshipsSection = Relationships.MasterSection.create({
                collection: opts.relationships,
                channel: opts.channel,
                relationshipsRepo: opts.relationshipsRepo,
                worksRepo: opts.worksRepo,
                work: opts.work
            });
            relationshipsViewPromise = relationshipsSection.getView();
        }
        function attachControls() {
            workView.getRegion('authors').show(authorsView);
            workView.getRegion('otherAuthors').show(otherAuthorsView);
            workView.getRegion('summary').show(summaryView);
            workView.getRegion('editions').show(editionsView);
            relationshipsViewPromise.then(function (view) {
                workView.getRegion('relationships').show(view);
            });
        }
        workView.once('attach', attachControls);
        return {
            getController: function () {
                return controller;
            },
            getView: function () {
                return workView;
            }
        };
    }
    return { create: constructSection };
});