define('services/repos/works', [
    'require',
    'exports',
    'module',
    'promise',
    'underscore',
    'services/repos/entities/work',
    'services/repos/entities/edition',
    'services/repos/entities/volume'
], function (require) {
    var Promise = require('promise'), _ = require('underscore'), Work = require('./entities/work'), Edition = require('./entities/edition'), Volume = require('./entities/volume');
    var WORK_URI = /^works\/([^\/]+)$/, EDITION_URI = /^works\/([^\/]+)\/editions\/([^\/]+)$/, VOLUME_URI = /^works\/([^\/]+)\/editions\/([^\/]+)\/volumes\/([^\/]+)$/;
    function workEndpoint(repo, work) {
        return repo.apiEndpoint + (work.isNew() ? '' : '/' + work.id);
    }
    function editionEndpoint(repo, edition) {
        var work = edition.getWork();
        if (work.isNew()) {
            throw new Error('tried to get edition endpoint on an unposted work');
        }
        return workEndpoint(repo, work) + '/editions' + (edition.isNew() ? '' : '/' + edition.id);
    }
    function volumeEndpoint(repo, volume) {
        var edition = volume.getEdition();
        if (edition.isNew()) {
            throw new Error('tried to get volume endpoint on an unposted edition');
        }
        return editionEndpoint(repo, edition) + '/volumes' + (volume.isNew() ? '' : '/' + volume.id);
    }
    function WorksRepository(options) {
        var opts = _.defaults(options, {});
        if (!opts.apiEndpoint) {
            throw new Error('Remote API is not defined.');
        }
        if (!opts.restClient) {
            throw new Error('REST client is not defined');
        }
        _.extend(this, _.pick(opts, 'apiEndpoint', 'restClient'));
    }
    _.extend(WorksRepository.prototype, {
        find: function (uri) {
            var match;
            if (WORK_URI.test(uri)) {
                match = WORK_URI.exec(uri);
                return this.findWork(match[1]).then(function (work) {
                    return {
                        type: 'work',
                        model: work
                    };
                });
            } else if (EDITION_URI.test(uri)) {
                match = EDITION_URI.exec(uri);
                return this.findWork(match[1]).then(function (work) {
                    var edition = work.get('editions').get(match[2]);
                    return {
                        type: 'edition',
                        model: edition
                    };
                });
            } else if (VOLUME_URI.test(uri)) {
                match = VOLUME_URI.exec(uri);
                return this.findWork(match[1]).then(function (work) {
                    var edition = work.get('editions').get(match[2]);
                    var volume = edition.get('volumes').get(match[3]);
                    return {
                        type: 'volume',
                        model: volume
                    };
                });
            } else {
                throw new TypeError('unrecognized URI: ' + uri);
            }
        },
        findWork: function (workId) {
            var _this = this;
            return this.restClient.get(this.apiEndpoint + '/' + workId).then(function (data) {
                var work = new Work(data, { parse: true });
                work.setRepo(_this);
                return work;
            });
        },
        search: function (title, options) {
            var opts = _.defaults(options, { limit: 10 });
            var queryParams = {
                q: title,
                max: opts.limit
            };
            var repo = this;
            return this.restClient.get(this.apiEndpoint, queryParams).then(function (results) {
                return _.map(results, function (result) {
                    return {
                        id: result.id,
                        uri: repo.getWorkUri(result),
                        title: result.title,
                        authors: result.authors,
                        label: result.label,
                        getWork: function () {
                            return repo.findWork(result.id);
                        }
                    };
                });
            }).catch(function (err) {
                console.error('unable to fetch results: ' + err.error);
                throw new Error('Cannot connect to server');
            });
        },
        createWork: function () {
            var work = new Work();
            work.setRepo(this);
            return Promise.resolve(work);
        },
        createEdition: function (work) {
            if (!work) {
                throw new TypeError('no work provided');
            }
            var edition = new Edition({
                authors: work.get('authors').toJSON(),
                titles: work.get('titles').toJSON(),
                otherAuthors: work.get('otherAuthors').toJSON()
            }, { parse: true });
            edition.setRepo(this);
            edition.setWork(work);
            work.get('editions').add(edition);
            return Promise.resolve(edition);
        },
        createVolume: function (edition) {
            if (!edition) {
                throw new TypeError('no edition provided');
            }
            var volume = new Volume({
                publicationInfo: edition.get('publicationInfo').toJSON(),
                authors: edition.get('authors').toJSON(),
                titles: edition.get('titles').toJSON(),
                otherAuthors: edition.get('otherAuthors').toJSON()
            }, { parse: true });
            volume.setRepo(this);
            volume.setEdition(edition);
            edition.get('volumes').add(volume);
            return Promise.resolve(volume);
        },
        getWorkUri: function (work) {
            if (!work.id) {
                throw new Error('unable to get URI of unsaved work');
            }
            return 'works/' + work.id;
        },
        getEditionUri: function (edition) {
            if (!edition.id) {
                throw new Error('unable to get URI of unsaved edition');
            }
            var work = edition.getWork();
            var workUri = this.getWorkUri(work);
            return workUri + '/editions/' + edition.id;
        },
        getVolumeUri: function (volume) {
            if (!volume.id) {
                throw new Error('unable to get URI of unsaved volume');
            }
            var edition = volume.getEdition();
            var editionUri = this.getEditionUri(edition);
            return editionUri + '/volumes/' + volume.id;
        },
        saveWork: function (work) {
            return this.restClient.send({
                method: work.isNew() ? 'post' : 'put',
                url: workEndpoint(this, work),
                data: work.toJSON()
            }).then(_.property('id'));
        },
        saveEdition: function (edition) {
            return this.restClient.send({
                method: edition.isNew() ? 'post' : 'put',
                url: editionEndpoint(this, edition),
                data: edition.toJSON()
            }).then(_.property('id'));
        },
        saveVolume: function (volume) {
            return this.restClient.send({
                method: volume.isNew() ? 'post' : 'put',
                url: volumeEndpoint(this, volume),
                data: volume.toJSON()
            }).then(_.property('id'));
        },
        deleteWork: function (work) {
            if (work.isNew()) {
                return Promise.resolve(false);
            }
            return this.restClient.delete(workEndpoint(this, work));
        },
        deleteEdition: function (edition) {
            if (edition.isNew()) {
                return Promise.resolve(false);
            }
            return this.restClient.delete(editionEndpoint(this, edition));
        },
        deleteVolume: function (volume) {
            if (volume.isNew()) {
                return Promise.resolve(false);
            }
            return this.restClient.delete(volumeEndpoint(this, volume));
        }
    });
    return WorksRepository;
});