define('catalogentries-reln/sections/info/views/relationship_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'catalogentries-reln/sections/info/views/templates/relationship'
], function (require) {
    var Marionette = require('marionette');
    var RelationshipView = Marionette.LayoutView.extend({
        template: require('./templates/relationship'),
        regions: {
            source: '> .source',
            type: '> .relationship-type',
            target: '> .target',
            summary: '> .summary'
        }
    });
    return RelationshipView;
});