define('controls/moment.infer', [
    'require',
    'exports',
    'module',
    'moment'
], function (require) {
    var moment = require('moment');
    var INFER_FORMATS = [
        'YYYY',
        'MMMM-YYYY',
        'MMM-YYYY',
        'MM-YYYY',
        'M-YYYY',
        'YYYY-MMMM',
        'YYYY-MMM',
        'YYYY-MM',
        'YYYY-M',
        'MMMM-DD-YYYY',
        'MMM-DD-YYYY',
        'MM-DD-YYYY',
        'M-DD-YYYY',
        'MMMM-D-YYYY',
        'MMM-D-YYYY',
        'MM-D-YYYY',
        'M-D-YYYY',
        'DD-MMMM-YYYY',
        'DD-MMM-YYYY',
        'DD-MM-YYYY',
        'DD-M-YYYY',
        'D-MMMM-YYYY',
        'D-MMM-YYYY',
        'D-MM-YYYY',
        'D-M-YYYY',
        'YYYY-MM-DD',
        'YYYY-M-D'
    ];
    return function (str) {
        return moment(str, INFER_FORMATS);
    };
});