define('modules/catalog_navigation/catalog_navigation', [
    'require',
    'exports',
    'module',
    'backbone',
    'backbone.wreqr',
    'underscore',
    'views/tab_panel_view',
    'modules/catalog_navigation/sections/people_nav_section/people_nav_section',
    'modules/catalog_navigation/sections/works_nav_section/works_nav_section',
    'modules/catalog_navigation/views/layout'
], function (require) {
    var Backbone = require('backbone'), Wreqr = require('backbone.wreqr'), _ = require('underscore'), TabPanelView = require('views/tab_panel_view'), PeopleNavigationSection = require('./sections/people_nav_section/people_nav_section'), WorksNavigationSection = require('./sections/works_nav_section/works_nav_section'), CatalogNavigationLayoutView = require('./views/layout');
    function CatalogNavigation(options) {
        var opts = _.defaults(options, { channel: new Wreqr.EventAggregator() });
        if (!opts.region) {
            throw new TypeError('No region specified');
        }
        if (!opts.worksRepo) {
            throw new TypeError('No works repository specified');
        }
        if (!opts.peopleRepo) {
            throw new TypeError('No people repository specified');
        }
        _.extend(this, _.pick(opts, 'channel', 'region', 'worksRepo', 'peopleRepo'));
        this.layout = null;
        this._initializeControls();
    }
    _.extend(CatalogNavigation.prototype, Backbone.Events, {
        getLayout: function () {
            if (!_.isNull(this.layout)) {
                return this.layout;
            }
            this.layout = new CatalogNavigationLayoutView();
            this.layout.on('destroy', function () {
                this.layout = null;
            });
            return this.layout;
        },
        _initializeControls: function () {
            var layout = this.getLayout();
            if (this.region.currentView !== layout) {
                this.region.show(layout);
            }
            var peopleSection = PeopleNavigationSection.create(_.pick(this, 'channel', 'peopleRepo'));
            var worksSection = WorksNavigationSection.create(_.pick(this, 'channel', 'worksRepo'));
            this.tabs = new TabPanelView();
            this.tabs.add('People', peopleSection.getView());
            this.tabs.add('Works', worksSection.getView());
            layout.getRegion('main').show(this.tabs);
        }
    });
    return CatalogNavigation;
});