define('modules/work_editor/sections/volume_info/volume_info_section', [
    'require',
    'exports',
    'module',
    'backbone.wreqr',
    'underscore',
    'controls/summary/summary',
    'modules/work_editor/controls/author_refs_editor/author_refs_editor',
    'modules/work_editor/controls/publication_info/publication_info',
    'modules/work_editor/controls/titles_editor/titles_editor',
    'modules/work_editor/sections/volume_info/volume_info_controller',
    'modules/work_editor/sections/volume_info/views/volume_number_view',
    'modules/work_editor/sections/volume_info/views/volume_info_view'
], function (require) {
    var Wreqr = require('backbone.wreqr'), _ = require('underscore'), SummaryControl = require('controls/summary/summary'), AuthorRefsEditorControl = require('modules/work_editor/controls/author_refs_editor/author_refs_editor'), PublicationInfoControl = require('modules/work_editor/controls/publication_info/publication_info'), TitlesEditorControl = require('modules/work_editor/controls/titles_editor/titles_editor'), VolumeInfoController = require('./volume_info_controller'), VolumeNumberView = require('./views/volume_number_view'), VolumeInfoView = require('./views/volume_info_view');
    function constructSection(options) {
        var opts = _.defaults(options, { channel: new Wreqr.EventAggregator() });
        if (!opts.volume) {
            throw new TypeError('no volume provided to volume info section');
        }
        if (!opts.peopleRepo) {
            throw new TypeError('no people repository provided to volume info section');
        }
        var controller = new VolumeInfoController({ peopleRepo: opts.peopleRepo });
        controller.setDomainModel(opts.volume);
        var viewModel = controller.getViewModel();
        var volumeView = new VolumeInfoView({
            model: viewModel,
            channel: opts.channel
        });
        var provider = _.throttle(function (q, limit) {
            return opts.peopleRepo.findAllByName(q, { limit: limit }).then(function (results) {
                return _.map(results, function (person) {
                    return {
                        label: person.formattedName,
                        obj: controller.createAuthorResultViewModel(person)
                    };
                });
            });
        }, 500);
        var volumeNumberView = new VolumeNumberView({
            model: viewModel,
            channel: opts.channel
        });
        var publicationInfoView = new PublicationInfoControl.EditorView({
            model: viewModel.get('publicationInfo'),
            channel: opts.channel
        });
        var authorsView = new AuthorRefsEditorControl.View({
            collection: viewModel.get('authors'),
            channel: opts.channel,
            authorSearchProvider: provider
        });
        var titlesView = new TitlesEditorControl.View({
            collection: viewModel.get('titles'),
            channel: opts.channel
        });
        var otherAuthorsView = new AuthorRefsEditorControl.View({
            collection: viewModel.get('otherAuthors'),
            channel: opts.channel,
            title: 'Other Authors',
            authorSearchProvider: provider
        });
        var summaryView = new SummaryControl.EditorView({
            model: viewModel.get('summary'),
            channel: opts.channel
        });
        function attachControls() {
            volumeView.getRegion('volumeNumber').show(volumeNumberView);
            volumeView.getRegion('publicationInfo').show(publicationInfoView);
            volumeView.getRegion('authors').show(authorsView);
            volumeView.getRegion('titles').show(titlesView);
            volumeView.getRegion('otherAuthors').show(otherAuthorsView);
            volumeView.getRegion('summary').show(summaryView);
        }
        volumeView.once('attach', attachControls);
        return {
            getController: function () {
                return controller;
            },
            getView: function () {
                return volumeView;
            }
        };
    }
    return { create: constructSection };
});