define('modules/person_editor/views/editor_layout', [
    'require',
    'exports',
    'module',
    'marionette',
    'modules/person_editor/views/templates/editor_layout'
], function (require) {
    var Marionette = require('marionette');
    var PersonEditorLayout = Marionette.LayoutView.extend({
        template: require('./templates/editor_layout'),
        regions: {
            master: '#master-pane',
            details: '#details-pane'
        },
        triggers: {
            'click .save': 'save',
            'click .delete': 'delete'
        },
        setDirty: function (dirty) {
            this.$('.save').prop('disabled', !dirty);
            if (dirty) {
                this.$('.save .btn-label').text('save');
                this.$('.save .glyphicon').addClass('glyphicon-save').removeClass('glyphicon-saved');
            } else {
                this.$('.save .btn-label').text('saved');
                this.$('.save .glyphicon').addClass('glyphicon-saved').removeClass('glyphicon-save');
            }
        }
    });
    return PersonEditorLayout;
});