define('modules/work_editor/sections/edition_info/edition_info_section', [
    'require',
    'exports',
    'module',
    'backbone.wreqr',
    'underscore',
    'controls/summary/summary',
    'modules/work_editor/controls/author_refs_editor/author_refs_editor',
    'modules/work_editor/controls/titles_editor/titles_editor',
    'modules/work_editor/controls/publication_info/publication_info',
    'modules/work_editor/sections/edition_info/edition_info_controller',
    'modules/work_editor/sections/edition_info/views/edition_name_view',
    'modules/work_editor/sections/edition_info/views/edition_info_view'
], function (require) {
    var Wreqr = require('backbone.wreqr'), _ = require('underscore'), SummaryControl = require('controls/summary/summary'), AuthorRefsEditorControl = require('modules/work_editor/controls/author_refs_editor/author_refs_editor'), TitlesEditorControl = require('modules/work_editor/controls/titles_editor/titles_editor'), PublicationInfoControl = require('modules/work_editor/controls/publication_info/publication_info'), EditionInfoController = require('./edition_info_controller'), EditionNameView = require('./views/edition_name_view'), EditionInfoView = require('./views/edition_info_view');
    function constructSection(options) {
        var opts = _.defaults(options, { channel: new Wreqr.EventAggregator() });
        if (!opts.edition) {
            throw new TypeError('no edition provided to edition info section');
        }
        if (!opts.peopleRepo) {
            throw new TypeError('no people repository provided to edition info section');
        }
        var controller = new EditionInfoController({ peopleRepo: opts.peopleRepo });
        controller.setDomainModel(opts.edition);
        var viewModel = controller.getViewModel();
        var editionView = new EditionInfoView({
            model: viewModel,
            channel: opts.channel
        });
        var provider = _.throttle(function (q, limit) {
            return opts.peopleRepo.findAllByName(q, { limit: limit }).then(function (results) {
                return _.map(results, function (person) {
                    return {
                        label: person.formattedName,
                        obj: controller.createAuthorResultViewModel(person)
                    };
                });
            });
        }, 500);
        var editionNameView = new EditionNameView({
            model: viewModel,
            channel: opts.channel
        });
        var publicationInfoView = new PublicationInfoControl.EditorView({
            model: viewModel.get('publicationInfo'),
            channel: opts.channel
        });
        var authorsView = new AuthorRefsEditorControl.View({
            collection: viewModel.get('authors'),
            channel: opts.channel,
            authorSearchProvider: provider
        });
        var titlesView = new TitlesEditorControl.View({
            collection: viewModel.get('titles'),
            channel: opts.channel
        });
        var otherAuthorsView = new AuthorRefsEditorControl.View({
            collection: viewModel.get('otherAuthors'),
            channel: opts.channel,
            title: 'Other Authors',
            authorSearchProvider: provider
        });
        var summaryView = new SummaryControl.EditorView({
            model: viewModel.get('summary'),
            channel: opts.channel
        });
        function attachControls() {
            editionView.getRegion('editionName').show(editionNameView);
            editionView.getRegion('publicationInfo').show(publicationInfoView);
            editionView.getRegion('authors').show(authorsView);
            editionView.getRegion('titles').show(titlesView);
            editionView.getRegion('otherAuthors').show(otherAuthorsView);
            editionView.getRegion('summary').show(summaryView);
        }
        editionView.once('attach', attachControls);
        return {
            getController: function () {
                return controller;
            },
            getView: function () {
                return editionView;
            }
        };
    }
    return { create: constructSection };
});