define('modules/work_editor/sections/work_master/views/editions_domain_view', [
    'require',
    'exports',
    'module',
    'marionette',
    'underscore',
    'modules/work_editor/sections/work_master/views/templates/editions_domain'
], function (require) {
    var Marionette = require('marionette'), _ = require('underscore');
    var EditionDomainView = Marionette.LayoutView.extend({
        template: _.constant('<div class="edition"></div>'),
        className: 'panel-wrapper edition-domain',
        regions: { edition: '.edition' },
        initialize: function (options) {
            var opts = _.defaults(options, { parentId: null });
            if (!opts.channel) {
                throw new TypeError('no channel provided');
            }
            if (!opts.sectionFactory) {
                throw new TypeError('no section factory provided');
            }
            _.extend(this, _.pick(opts, 'parentId', 'channel', 'sectionFactory'));
            this.view = null;
        },
        onShow: function () {
            this.section = this.sectionFactory.create({
                edition: this.model,
                channel: this.channel,
                viewParentId: this.parentId
            });
            var view = this.section.getView();
            this.listenTo(view, 'edit', function () {
                this.trigger('edit');
            });
            this.listenTo(view, 'link-copies', function () {
                this.trigger('link-copies');
            });
            this.getRegion('edition').show(view);
        },
        expand: function () {
            if (this.section) {
                this.section.getView().expand();
            }
        },
        collapse: function () {
            if (this.section) {
                this.section.getView().collapse();
            }
        },
        toggle: function () {
            if (this.section) {
                this.section.getView().toggle();
            }
        }
    });
    var NoEditionsView = Marionette.ItemView.extend({
        template: _.constant('[no editions]'),
        className: 'text-muted',
        triggers: { dblclick: 'add' }
    });
    var EditionsDomainView = Marionette.CompositeView.extend({
        template: require('./templates/editions_domain'),
        className: 'editions-domain',
        childView: EditionDomainView,
        emptyView: NoEditionsView,
        childViewContainer: '.editions',
        templateHelpers: function () {
            return { parentId: this.cid };
        },
        childViewOptions: function () {
            return {
                parentId: this.cid,
                channel: this.channel,
                sectionFactory: this.sectionFactory
            };
        },
        triggers: { 'click button.add-edition': 'add' },
        initialize: function (opts) {
            if (!opts.sectionFactory) {
                throw new TypeError('no section factory provided');
            }
            if (!opts.channel) {
                throw new TypeError('no channel provided');
            }
            _.extend(this, _.pick(opts, 'channel', 'sectionFactory'));
            this.on('childview:add', function () {
                this.trigger('add');
            }, this);
            this.on('childview:edit', function (view) {
                this.trigger('edit', view.model);
            });
            this.on('childview:link-copies', function (view) {
                this.trigger('link-copies', view.model);
            });
            this.rendered = false;
        },
        onAddChild: function (childView) {
            if (this.rendered) {
                childView.expand();
            }
        },
        onShow: function () {
            this.rendered = true;
        }
    });
    return EditionsDomainView;
});