define('modules/catalog_navigation/views/layout', [
    'require',
    'exports',
    'module',
    'marionette',
    'modules/catalog_navigation/views/templates/layout'
], function (require) {
    var Marionette = require('marionette');
    var LayoutView = Marionette.LayoutView.extend({
        template: require('./templates/layout'),
        className: 'panel panel-default',
        regions: { main: '> .panel-collapse > .panel-body' }
    });
    return LayoutView;
});