define('services/repos/entities/work', [
    'require',
    'exports',
    'module',
    'services/repos/entities/base',
    'underscore',
    'mixins/dirtyable_model_mixin',
    'services/repos/entities/author_refs',
    'services/repos/entities/titles',
    'services/repos/entities/editions'
], function (require) {
    var BaseModel = require('./base'), _ = require('underscore'), DirtyableModelMixin = require('mixins/dirtyable_model_mixin'), AuthorRefs = require('./author_refs'), Titles = require('./titles'), Editions = require('./editions');
    var Work = BaseModel.extend({
        relations: {
            authors: { class: AuthorRefs },
            titles: { class: Titles },
            otherAuthors: { class: AuthorRefs },
            editions: { class: Editions }
        },
        defaults: function () {
            return {
                authors: new AuthorRefs(),
                titles: new Titles(),
                otherAuthors: new AuthorRefs(),
                series: '',
                summary: '',
                editions: new Editions()
            };
        },
        initialize: function (attributes, options) {
            this.initializeDirtyable(options);
            this.get('editions').invoke('setWork', this);
        },
        getCanonicalTitle: function () {
            var result = this.get('titles').find(function (title) {
                return title.isCanonical();
            });
            return _.isUndefined(result) ? null : result;
        },
        createEdition: function () {
            if (!this._repo) {
                throw new Error('no repo');
            }
            return this._repo.createEdition(this);
        },
        setRepo: function (repo) {
            this._repo = repo;
            this.get('editions').invoke('setRepo', repo);
        },
        getUri: function () {
            if (!this._repo) {
                throw new Error('no repo');
            }
            return this._repo.getWorkUri(this);
        },
        save: function () {
            if (!this._repo) {
                throw new Error('no repo');
            }
            var promise = this._repo.saveWork(this);
            var _this = this;
            return promise.then(function (id) {
                if (_this.isNew()) {
                    _this.set('id', id);
                } else if (id !== _this.get('id')) {
                    throw new Error('ID mismatch');
                }
                _this.trigger('sync');
                return _this;
            });
        },
        destroy: function () {
            BaseModel.prototype.destroy.apply(this, arguments);
            if (!this._repo) {
                throw new Error('no repo');
            }
            return this._repo.deleteWork(this);
        }
    });
    _.defaults(Work.prototype, DirtyableModelMixin);
    return Work;
});