define('views/app_layout', [
    'require',
    'exports',
    'module',
    'marionette',
    'notifications/index',
    'views/templates/app_layout'
], function (require) {
    var Marionette = require('marionette'), Notifications = require('notifications/index');
    var ApplicationLayoutView = Marionette.LayoutView.extend({
        template: require('./templates/app_layout'),
        regions: {
            nav: '#navigation-pane',
            main: '#main-pane'
        },
        onRender: function () {
            Notifications.getInstance().setEl(this.$('#notifications'));
        }
    });
    return ApplicationLayoutView;
});