define('services/repos/people', [
    'require',
    'exports',
    'module',
    'promise',
    'underscore',
    'services/repos/entities/person'
], function (require) {
    var Promise = require('promise'), _ = require('underscore'), Person = require('./entities/person');
    function PeopleRepository(options) {
        var opts = _.defaults(options, {});
        if (!opts.apiEndpoint) {
            throw new Error('Remote API is not defined.');
        }
        if (!opts.restClient) {
            throw new Error('REST client is not defined');
        }
        _.extend(this, _.pick(opts, 'apiEndpoint', 'restClient'));
    }
    _.extend(PeopleRepository.prototype, {
        create: function () {
            var person = new Person();
            person.setRepo(this);
            return Promise.resolve(person);
        },
        savePerson: function (person) {
            return this.restClient.send({
                method: person.isNew() ? 'post' : 'put',
                url: this.apiEndpoint + (person.isNew() ? '' : '/' + person.id),
                data: person.toJSON()
            }).then(_.property('id'));
        },
        find: function (id) {
            var _this = this;
            return this.restClient.get(this.apiEndpoint + '/' + id).then(function (data) {
                var person = new Person(data, { parse: true });
                person.setRepo(_this);
                return person;
            });
        },
        findAllByName: function (name, options) {
            var opts = _.defaults(options, { limit: 10 });
            var queryParams = {
                syntheticName: name,
                numResults: opts.limit
            };
            return this.restClient.get(this.apiEndpoint, queryParams).then(function (results) {
                return _.map(results, function (result) {
                    return {
                        id: result.id,
                        name: result.displayName,
                        formattedName: result.formattedName
                    };
                });
            }).catch(function (err) {
                console.error('unable to fetch results: ' + err.error);
                throw new Error('Cannot connect to server');
            });
        },
        removePerson: function (id) {
            return this.restClient.delete(this.apiEndpoint + '/' + id);
        }
    });
    return PeopleRepository;
});