define('catalogentries-reln/sections/master/relationships_master_section', [
    'require',
    'exports',
    'module',
    'catalogentries-reln/sections/master/relationships_master_controller',
    'catalogentries-reln/sections/master/views/relationships_view'
], function (require) {
    var RelationshipsMasterController = require('./relationships_master_controller'), RelationshipsView = require('./views/relationships_view');
    function constructSection(opts) {
        if (!opts.relationshipsRepo) {
            throw new TypeError('no relationships repository provided');
        }
        if (!opts.collection) {
            throw new TypeError('no relationship domain collection given');
        }
        if (!opts.channel) {
            throw new TypeError('no channel provided');
        }
        if (!opts.work) {
            throw new TypeError('no work provided to serve as relationship source');
        }
        if (!opts.worksRepo) {
            throw new TypeError('no works repository provided');
        }
        var controllerPromise = opts.relationshipsRepo.findTypes().then(function (types) {
            var controller = new RelationshipsMasterController({
                domainCollection: opts.collection,
                channel: opts.channel,
                types: types,
                work: opts.work,
                worksRepo: opts.worksRepo
            });
            return controller;
        });
        var viewCollectionPromise = controllerPromise.then(function (controller) {
            return controller.getViewCollection();
        });
        var relationshipsViewPromise = viewCollectionPromise.then(function (viewCollection) {
            var relationshipsView = new RelationshipsView({ collection: viewCollection });
            relationshipsView.on('add', function () {
                opts.relationshipsRepo.create(opts.work.getUri()).then(function (relationship) {
                    opts.collection.add(relationship);
                    opts.channel.trigger('edit:relationship', relationship);
                });
            });
            return relationshipsView;
        });
        return {
            getView: function () {
                return relationshipsViewPromise;
            }
        };
    }
    return { create: constructSection };
});