define('catalogentries-digitalcopies/entities/copy_reference', [
    'require',
    'exports',
    'module',
    'catalogentries-digitalcopies/entities/base'
], function (require) {
    var BaseModel = require('./base');
    var CopyReferenceDomainModel = BaseModel.extend({
        defaults: {
            associatedEntry: '',
            copyId: '',
            title: '',
            summary: '',
            rights: ''
        },
        setRepo: function (repo) {
            this._repo = repo;
        },
        save: function () {
            if (!this._repo) {
                throw new Error('no repository set');
            }
            var _this = this;
            return this._repo.save(this).then(function (id) {
                if (_this.isNew()) {
                    _this.set('id', id);
                } else if (id !== _this.get('id')) {
                    throw new Error('ID mismatch');
                }
                _this.trigger('sync');
                return _this;
            });
        },
        destroy: function () {
            BaseModel.prototype.destroy.apply(this, arguments);
            if (!this._repo) {
                throw new Error('no repo');
            }
            return this._repo.delete(this);
        }
    });
    return CopyReferenceDomainModel;
});