define('services/repos/entities/person', [
    'require',
    'exports',
    'module',
    'promise',
    'underscore',
    'services/repos/entities/base',
    'mixins/dirtyable_model_mixin',
    'services/repos/entities/name',
    'services/repos/entities/names',
    'services/repos/entities/historical_event'
], function (require) {
    var Promise = require('promise'), _ = require('underscore'), BaseModel = require('./base'), DirtyableModelMixin = require('mixins/dirtyable_model_mixin'), Name = require('./name'), Names = require('./names'), HistoricalEvent = require('./historical_event');
    var Person = BaseModel.extend({
        relations: {
            displayName: { class: Name },
            names: { class: Names },
            birth: { class: HistoricalEvent },
            death: { class: HistoricalEvent }
        },
        defaults: function () {
            return {
                displayName: new Name(),
                names: new Names(),
                birth: new HistoricalEvent({ title: 'birth' }),
                death: new HistoricalEvent({ title: 'death' }),
                summary: ''
            };
        },
        initialize: function (attributes, options) {
            this.initializeDirtyable(options);
        },
        setRepo: function (repo) {
            this._repo = repo;
        },
        save: function () {
            if (!this._repo) {
                throw new Error('no repo');
            }
            var promise = this._repo.savePerson(this);
            var _this = this;
            return promise.then(function (id) {
                if (_this.isNew()) {
                    _this.set('id', id);
                } else if (id !== _this.get('id')) {
                    throw new Error('ID mismatch');
                }
                _this.trigger('sync');
                return _this;
            });
        },
        destroy: function (options) {
            if (!this._repo) {
                throw new Error('no repo');
            }
            var promise = this.isNew() ? Promise.resolve(false) : this._repo.removePerson(this.id);
            var _this = this;
            return promise.then(function (resp) {
                _this.trigger('destroy', _this, _this.collection, options);
                _this.trigger('sync', _this, resp, options);
                return _this;
            });
        },
        getFormattedName: function () {
            var displayName = this.get('displayName'), birth = this.get('birth').getMoment(), death = this.get('death').getMoment();
            return displayName.get('displayName') + ' (' + (birth && birth.isValid() ? birth.format('YYYY') : '') + '\u2013' + (death && death.isValid() ? death.format('YYYY') : '') + ')';
        }
    });
    _.defaults(Person.prototype, DirtyableModelMixin);
    return Person;
});