define('catalogentries-digitalcopies/sections/info/dc_info_section', [
    'require',
    'exports',
    'module',
    'promise',
    'underscore',
    'notifications/index',
    'controls/summary/summary',
    'catalogentries-digitalcopies/sections/info/dc_info_controller',
    'catalogentries-digitalcopies/sections/info/views/layout_view',
    'catalogentries-digitalcopies/sections/info/views/copies_view',
    'catalogentries-digitalcopies/sections/info/views/search_view',
    'catalogentries-digitalcopies/sections/info/views/reference_editor_view'
], function (require) {
    var Promise = require('promise'), _ = require('underscore'), Notifications = require('notifications/index').getInstance(), SummaryControl = require('controls/summary/summary'), DigitalCopyInfoController = require('./dc_info_controller'), LayoutView = require('./views/layout_view'), CopiesView = require('./views/copies_view'), SearchView = require('./views/search_view'), ReferenceEditorView = require('./views/reference_editor_view');
    function constructSection(options) {
        var opts = _.defaults(options || {}, {});
        if (!opts.uri) {
            throw new TypeError('no source URI given');
        }
        if (!opts.booksRepo) {
            throw new TypeError('no books search repository given');
        }
        if (!opts.copiesRepo) {
            throw new TypeError('no digital copy references repository given');
        }
        var controller = new DigitalCopyInfoController();
        var layoutView = new LayoutView();
        var domainCollectionPromise = opts.copiesRepo.findAll(opts.uri);
        var viewCollectionPromise = domainCollectionPromise.then(controller.createViewCollection);
        function showCopyRefEditor(domainModel) {
            var viewModelPromise = controller.createViewModel(domainModel);
            var summaryViewPromise = viewModelPromise.then(function (viewModel) {
                return new SummaryControl.EditorView({ model: viewModel.get('summary') });
            });
            var editorViewPromise = Promise.join(viewModelPromise, summaryViewPromise, function (viewModel, summaryView) {
                var editorView = new ReferenceEditorView({ model: viewModel });
                editorView.on('show', function () {
                    editorView.getRegion('summary').show(summaryView);
                });
                return editorView;
            });
            var savePromise = editorViewPromise.then(function (editorView) {
                return Notifications.confirm({
                    title: 'Digital Copy Reference Details',
                    view: editorView
                }, { confirmText: 'Save' });
            });
            return Promise.join(savePromise, summaryViewPromise, domainCollectionPromise, function (shouldSave, summaryView, domainCollection) {
                if (shouldSave) {
                    summaryView.stopEditing();
                    return domainModel.save().then(function () {
                        domainCollection.add(domainModel);
                    });
                }
            });
        }
        layoutView.on('show', function () {
            viewCollectionPromise.then(function (viewCollection) {
                var copiesView = new CopiesView({ collection: viewCollection });
                layoutView.getRegion('copies').show(copiesView);
            });
            var searchView = new SearchView({ searchProvider: _.bind(opts.booksRepo.search, opts.booksRepo) });
            searchView.on('select', function (book) {
                opts.copiesRepo.create(opts.uri, book.id).then(showCopyRefEditor);
            });
            layoutView.getRegion('search').show(searchView);
        });
        var viewPromise = Promise.resolve(layoutView);
        return {
            getView: function () {
                return viewPromise;
            }
        };
    }
    return { create: constructSection };
});